import { createSlice } from '@reduxjs/toolkit';

export interface GlobalRef {
  appContainerRef: HTMLDivElement | null;
  hasNotch: boolean;
  paddingTopWhenHasNotch: any;
}

export const initialStateRef: GlobalRef = {
  appContainerRef: null,
  hasNotch: window.localStorage.getItem('hasNotch') === 'true',
  paddingTopWhenHasNotch: {
    paddingTop: window.localStorage.getItem('hasNotch') === 'true' ? 44 : 0,
  },
};

const RefSlice = createSlice({
  name: 'Ref',
  initialState: initialStateRef,
  reducers: {
    setGlobalRef(state, action) {
      return { ...state, appContainerRef: action.payload };
    },
    setHasNotch(state, action) {
      return { ...state, hasNotch: action.payload };
    },
    setPaddingTopWhenHasNotch(state, action) {
      return { ...state, paddingTop: action.payload };
    },
    removeGlobalRef(state, action) {
      return action.payload;
    },
  },
});

export const {
  setGlobalRef,
  removeGlobalRef,
  setHasNotch,
  setPaddingTopWhenHasNotch,
} = RefSlice.actions;
export default RefSlice.reducer;
