import React, { useMemo } from 'react';
import DetailTransaction from '@components/DetailTransaction';
import ModalBase from '@components/Modal/ModalBase';
import Thumb from '@components/Thumb';
import { transactions } from '@page/ManageWallet';

function ModalTransactionDetail({ visible, onClose }) {
  const dataCustom = useMemo(
    () => [
      {
        name: 'Transaction',
        customRenderValue: () => (
          <span style={{ color: 'var( --color-accent-blue-1)' }}>
            {transactions[0].transaction}
          </span>
        ),
      },
      { name: 'Status', status: 'time-out' },
      {
        name: 'From Wallet',
        value: '0x32-characters Kaa,aaaaaaa,smska0291mMaks',
      },
      { name: 'Amount of coin', value: '2,000$' },
      { name: 'To Wallet', value: 'x2478127987192312893' },
      { name: 'Payment Methods', value: 'USDT(TRC20)' },
      { name: 'Payment time', value: '01 Jan 2023, 11:00' },
    ],
    [],
  );
  return (
    <ModalBase visible={visible} onClose={onClose}>
      <Thumb />
      <DetailTransaction note={'Payment Timeout'} data={dataCustom} />
    </ModalBase>
  );
}

export default ModalTransactionDetail;
