import React from 'react';
import { Animated } from 'react-animated-css';
import { timeTr3 } from '@utils/variable';
import styled from 'styled-components';
import { Title } from './ListStep/Step1';

function BlockTitleList({
  list,
  row,
  delayTime = 0,
  fullWidth = false,
  style = {},
}) {
  return (
    <Wrap className={`${row ? 'row' : ''}`} style={style}>
      {list.map((item, index) => (
        <Animated
          key={item.title}
          animationIn="fadeInRightCustom"
          animationInDelay={delayTime + timeTr3 * index}
          style={{ width: fullWidth ? '100%' : '' }}>
          <WrapItem>
            <Title className="title">{item.title}</Title>
            <div className="text">{item.value}</div>
          </WrapItem>
        </Animated>
      ))}
    </Wrap>
  );
}

export default BlockTitleList;

const Wrap = styled.div`
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
  &.row {
    flex-wrap: wrap;
    .text {
    }
  }
`;
const WrapItem = styled.div`
  flex: 1;
  .title {
    letter-spacing: -0.8px;
  }
  .text {
    width: 100%;
    /* max-width: 160px; */
    min-width: 160px;
    height: 48px;
    background: #eff8ff;
    border-radius: 10px;
    font-size: 15px;
    color: #004dff;
    font-weight: 500;
    line-height: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 4px;
  }
`;
