import React from 'react';
import iconRepaymentOnTime from '@assets/icon/iconRepaymentOnTime.svg';
import iconBalance from '@assets/icon/iconBalance.svg';
import iconRepaymentLate from '@assets/icon/iconRepaymentLate.svg';
import styled from 'styled-components';
import iconSmartContract from '@assets/icon/iconContract1.svg';
import iconContract from '@assets/icon/iconContact.svg';
import iconRefund from '@assets/icon/iconRefund.svg';
import iconDeposit from '@assets/icon/iconDeposit.svg';
import iconSmc1 from '@assets/icon/iconSmc1.svg';

import { numberWithCommas } from '@utils/currencyFormat';

const TransactionItem = ({
  onClick = undefined,
  time = undefined,
  type = undefined,
  releaseMonth = undefined,
  repayment = undefined,
  status = '',
  balance = '',
  isCreate = false,
}) => {
  const mappingStatusColor = (status) => {
    switch (status) {
      case 'refundProcessing':
      case 'pending':
        return '#FC9D00';
      case 'success':
        return '#04B800';
      case 'refundReject':
      case 'expired':
      case 'denied':
        return '#FF0366';
      case 'refund':
        return '#554CFF';
      default:
        return '';
    }
  };

  const mappingStatusText = (status) => {
    switch (status) {
      case 'refundProcessing':
        return 'refund processing';
      case 'refundReject':
        return 'refund reject';
      default:
        return status;
    }
  };

  const mappingIcon = (type) => {
    console.log('type ->', type);
    switch (type) {
      case 'repaymentOnTime':
        return iconRepaymentOnTime;
      case 'repaymentLate':
        return iconRepaymentLate;
      case 'balance':
        return iconBalance;
      case 'deposit':
        return iconDeposit;
      case 'contract':
        return iconContract;
      case 'smartContract':
        return iconSmartContract;
      case 'refund':
        return iconRefund;
      case 'smc1':
        return iconSmc1;
      default:
        return iconRepaymentOnTime;
    }
  };

  const mappingPrice = (price) => {
    if (price.includes('+')) {
      return (
        <span>
          <span className="plus">+</span>
          {price.replace('+', '')}
        </span>
      );
    }
    if (price.includes('-')) {
      return (
        <>
          <span>
            <span className="minus">-</span>
            {price.replace('-', '')}
          </span>
        </>
      );
    }
    return (
      <>
        <span>
          {numberWithCommas(price, ',')}{' '}
          {type === 'smc1' || type === 'smartContract' ? 'MTD' : 'USDT'}
        </span>
      </>
    );
  };

  return (
    <Root>
      <Wrapper onClick={() => onClick && onClick()}>
        {!isCreate ? (
          <>
            <RightContent>
              <Icon src={mappingIcon(type)}></Icon>
              <div className="content">
                <p className="date">
                  {releaseMonth && (
                    <>
                      <span>{releaseMonth} </span>
                      <span className="dot"></span>
                    </>
                  )}

                  <span> {time} </span>
                </p>
                <p className="status"> {repayment} </p>
              </div>
            </RightContent>
            <LeftContent>
              {status && (
                <p className="status" style={{ color: mappingStatusColor(status) }}>
                  {mappingStatusText(status)}
                </p>
              )}
              <p className="price">{balance && mappingPrice(balance.toString())}</p>
            </LeftContent>
          </>
        ) : (
          <WrapperCreate>
            <Icon src={mappingIcon(type)}></Icon>
            <WrapperContent>
              <TopContent>
                <div className="content">
                  <p className="date">
                    {releaseMonth && (
                      <>
                        <span>{releaseMonth} </span>
                        <span className="dot"></span>
                      </>
                    )}
                    <span> {time} </span>
                  </p>
                </div>
                <p className="status"> {status} </p>
              </TopContent>
              <BottomContent>
                <p className="repayment"> {repayment} </p>
              </BottomContent>
            </WrapperContent>
          </WrapperCreate>
        )}
      </Wrapper>
    </Root>
  );
};

export default TransactionItem;

