import React from 'react';
import styled from 'styled-components';

const Tab = ({ active = false, onClick, tab }) => {
  return (
    <Root onClick={() => onClick && onClick(tab.ID)} className={active && 'active'}>
      {tab.description}
    </Root>
  );
};

export default Tab;

export const Root = styled.div`
  display: flex;
  position: relative;
  text-transform: capitalize;
  font-size: 17px;
  line-height: 23px;
  font-weight: 400;
  margin-right: var(--space-1);
  margin-left: var(--space-1);
  padding-bottom: 5px;

  white-space: nowrap;

  color: var(--color-text-inactive);

  :nth-last-child() {
    margin-right: 0;
  }

  &.active {
    font-weight: 500;
    color: var(--color-title);

    &::after {
      content: '';
      position: absolute;
      width: 32px;
      height: 3px;
      background-color: var(--color-underline);
      left: 50%;
      top: 100%;
      transform: translate(-50%, -100%);
    }
  }
`;
