import Button from '@components/atoms/Button';
import GroupTextMapping from '@components/atoms/GroupText/GroupTextMapping';
import ContractDetail from '@components/ContractDetail';
import Header from '@components/Header';
import { numberWithCommas } from '@utils/currencyFormat';
import { useMemo } from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import React from 'react';

function Step3({ dataAllStep, handleBack }) {
  const navigate = useNavigate();

  const dataMapping = useMemo(() => {
    const d = dataAllStep.dataStep1;

    const data = [
      {
        data: {
          toSmartContract: dataAllStep.dataStep2.toSmc,
          deposit: `${numberWithCommas(d?.amount, ',')}$`,
          debtBalance: `-${numberWithCommas(d?.amount, ',')}$`,
          balance: `+${numberWithCommas(d?.amount, ',')}$`,
          paymentMethods: 'USDT (TRC20)',
        },
        title: 'Transaction detail',
      },
    ];
    return data;
  }, []);

  return (
    <Wrapper>
      <Header
        children={''}
        onBack={() => {
          handleBack && handleBack();
        }}
      />
      <ContractDetail
        transactionData={dataAllStep.dataStep2}
        childrenTransactions={
          <>
            <GroupTextMapping data={dataMapping} />
          </>
        }
        buttonBottom={
          <Button
            variant="container"
            title={'HOME'}
            noGradient
            onClick={() => navigate('/')}
          />
        }
      />
    </Wrapper>
  );
}

export default Step3;

const Wrapper = styled.div`
  padding: 0 15px;
  padding-bottom: 120px;
  margin-top: 16px;
`;
