import React, { useEffect, useState } from 'react';
import styled, { css } from 'styled-components';
import { transactionStatus } from '../../../../assets/icon';
import { useSelector } from 'react-redux';
import { numberWithCommas } from '@utils/currencyFormat';
import DefineKey from '@page/Transaction/components/definedKey';
import { isObject } from 'lodash';
const defaultDisabled = {
  ranking: true,
};

function TransactionDetailLayout({
  detail,
  note,
  disabledDetail = defaultDisabled,
  transactionDetailInputData,
  type = 'buy-token',
}) {
  const mappingStatusLabel = React.useCallback((status) => {
    switch (status) {
      case 'pending':
        return 'Pending';
      case 'success':
        return 'Success';
      case 'sold-out':
        return 'Sold Out';
      case 'time-out':
        return 'Time Out';
      case 'refund':
        return 'Refund';
      default:
        return '';
    }
  }, []);

  if (!detail) return null;

  const walletAddress = useSelector((state) => state.global.walletAddress);
  const [dataTransactionContact, setDataTransactionContact] = useState([]);
  const keyNumber = React.useMemo(() => {
    return ['balance', 'Amount', 'ReceiveMTD', 'Payment', 'Receive', 'Balance'];
  }, []);

  const cloneDeepObj = (obj) => {
    let arr = [];
    for (const key in obj) {
      if (Object.hasOwnProperty.call(obj, key)) {
        const element = obj[key];
        let item = {
          key: key,
          value: element,
        };
        arr.push(item);
      }
    }
    return arr;
  };

  useEffect(() => {
    if (transactionDetailInputData.type === 'smartcontact') {
      let obj = transactionDetailInputData;
      let arr = [];
      for (const key in obj) {
        if (Object.hasOwnProperty.call(obj, key)) {
          const element = obj[key];

          if (element && isObject(element)) {
            let item = cloneDeepObj(element);
            arr.push(item);
          }
        }
      }

      setDataTransactionContact(arr);
    }
  }, [transactionDetailInputData]);

  const renderDistribution = () => {};

  const renderContent = React.useCallback(
    (key, data) => {
      let keyOfNumber = keyNumber[key] ? true : false;

      if (key === 'Package') {
        return (
          <>
            <Row>
              <Left>{DefineKey[key]}</Left>
              <Right>
                <div style={{ fontSize: 14 }}>
                  {data.map((item, index) => (
                    <p key={index}>{item}</p>
                  ))}
                </div>
              </Right>
            </Row>
          </>
        );
      } else if (keyOfNumber) {
        return (
          <>
            <Row>
              <Left>{DefineKey[key]}</Left>
              <Right>{`${numberWithCommas(data, ',')} MTD`}</Right>
            </Row>
          </>
        );
      } else if (key === 'Distribution') {
        return <>Distribution</>;
      } else {
        return (
          <>
            <Row>
              <Left>{DefineKey[key]}</Left>
              <Right>{data}</Right>
            </Row>
          </>
        );
      }
    },
    [dataTransactionContact],
  );

  const renderRow = React.useMemo(() => {
    switch (type) {
      case 'payment':
        if (transactionDetailInputData.type === 'smartcontact') {
          return (
            <>
              <Content>
                <Row>
                  <Left>Round</Left>
                  <Right>{transactionDetailInputData.Detail.Round || '----'}</Right>
                </Row>
                <Row>
                  <Left>Create time</Left>
                  <Right>{transactionDetailInputData.Detail.CreateTime}</Right>
                </Row>
                <Row>
                  <Left>Package</Left>
                  <Right>
                    <div style={{ fontSize: 14 }}>
                      {transactionDetailInputData.Detail.Package.map(
                        (item, index) => (
                          <p key={index}>{item}</p>
                        ),
                      )}
                    </div>
                  </Right>
                </Row>
                <Row>
                  <Left>Receive MTD Wallet</Left>
                  <Right>{transactionDetailInputData.Detail.ReceiveMTDWallet}</Right>
                </Row>
                <Row>
                  <Left>Receive MTD</Left>
                  <Right>{`${numberWithCommas(
                    transactionDetailInputData.Detail.ReceiveMTD,
                    ',',
                  )} MTD`}</Right>
                </Row>
                <Row>
                  <Left>Payment (USDT)</Left>
                  <Right>{`${numberWithCommas(
                    transactionDetailInputData.Detail.ReceiveUSDT,
                    ',',
                  )}$`}</Right>
                </Row>
              </Content>
              <Content>
                <Row>
                  <Left>Email</Left>
                  <Right>{transactionDetailInputData.Detail.Email || '----'}</Right>
                </Row>
                <Row>
                  <Left>Refund Wallet</Left>
                  <Right>{transactionDetailInputData.Detail.RefundWallet}</Right>
                </Row>

                <Row>
                  <Left>Payment Method</Left>
                  <Right>{`${numberWithCommas(
                    transactionDetailInputData.Detail.PaymentMethod,
                    ',',
                  )}$`}</Right>
                </Row>
              </Content>
            </>
          );
        } else {
          return (
            <Content>
              {!disabledDetail?.From && (
                <Row>
                  <Left>From</Left>
                  <Right>{transactionDetailInputData.From || '----'}</Right>
                </Row>
              )}
              {!disabledDetail?.ToSmartContact && (
                <Row>
                  <Left>To smart contact</Left>
                  <Right>{transactionDetailInputData.ToSmartContact}</Right>
                </Row>
              )}
              {!disabledDetail?.Amount && (
                <Row>
                  <Left>Amount</Left>
                  <Right>{transactionDetailInputData.Amount}</Right>
                </Row>
              )}
              {!disabledDetail?.Balance && (
                <Row>
                  <Left>Balance</Left>
                  <Right>{transactionDetailInputData.Balance}</Right>
                </Row>
              )}
              {!disabledDetail?.Total && (
                <Row>
                  <Left>Total</Left>
                  <Right>{transactionDetailInputData.Total}</Right>
                </Row>
              )}
              {!disabledDetail?.PaymentMethods && (
                <Row>
                  <Left>Payment Methods</Left>
                  <Right>{transactionDetailInputData.PaymentMethods}</Right>
                </Row>
              )}
            </Content>
          );
        }

      case 'buy-token':
        return (
          <Content>
            {!disabledDetail?.transaction && (
              <Row transaction>
                <Left>Transaction</Left>
                <Right>{detail.transaction}</Right>
              </Row>
            )}
            {!disabledDetail?.status && (
              <Row noPad>
                <Left>Status</Left>
                <Right>
                  <Status status={detail.status}>
                    <Image src={transactionStatus[detail.status]} />
                    {mappingStatusLabel(detail.status)}
                  </Status>
                </Right>
              </Row>
            )}
            {!disabledDetail?.metanodeWallet && (
              <Row>
                <Left>Metanode Wallet</Left>
                <Right>{walletAddress.address}</Right>
              </Row>
            )}
            {!disabledDetail?.round && (
              <Row>
                <Left>Round</Left>
                <Right>{transactionDetailInputData.round}</Right>
              </Row>
            )}
            {!disabledDetail?.price && (
              <Row>
                <Left>Price</Left>
                <Right>{transactionDetailInputData.price}</Right>
              </Row>
            )}
            {!disabledDetail?.package && (
              <Row>
                <Left>Package</Left>
                <Right>{transactionDetailInputData.package}</Right>
              </Row>
            )}
            {!disabledDetail?.numOfPack && (
              <Row>
                <Left>Number of Package</Left>
                <Right>{transactionDetailInputData.numberOfPakage}</Right>
              </Row>
            )}
            {!disabledDetail?.totalMTD && detail?.totalMTD && (
              <Row>
                <Left>Total MTD</Left>
                <Right>{transactionDetailInputData.totalMTD}</Right>
              </Row>
            )}
            {!disabledDetail?.totalUSDT && (
              <Row>
                <Left>Total USDT</Left>
                <Right>{transactionDetailInputData.totalUsdt}</Right>
              </Row>
            )}
            {!disabledDetail?.ranking && detail?.ranking && (
              <Row>
                <Left>Ranking</Left>
                <Right>{detail.ranking}</Right>
              </Row>
            )}
            {!disabledDetail?.email && (
              <Row>
                <Left>Email</Left>
                <Right>{transactionDetailInputData.email}</Right>
              </Row>
            )}
            {!disabledDetail?.refundWallet && (
              <Row>
                <Left>Refund Wallet</Left>
                <Right>{transactionDetailInputData.refundWallet}</Right>
              </Row>
            )}
            {!disabledDetail?.paymentMed && (
              <Row>
                <Left>Payment Method</Left>
                <Right>{transactionDetailInputData.paymentMethod}</Right>
              </Row>
            )}
          </Content>
        );
      default:
        break;
    }
  });

  const renderContentDetail = (arr) => {
    return (
      <Content>{arr.map((item) => renderContent(item.key, item.value))}</Content>
    );
  };

  return (
    <Root>
      {note && <Note>{note}</Note>}
      <Title>Transaction detail</Title>
      <WrapTransactionDetail>
        {dataTransactionContact.map((item) => renderContentDetail(item))}
      </WrapTransactionDetail>
    </Root>
  );
}

export default TransactionDetailLayout;

const Root = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`;

const Title = styled.div``;

const Note = styled.div`
  color: var(--color-accent-blue-1);
  font-weight: 500;
  font-size: 17px;
  line-height: 24px;

  & + ${Title} {
    margin-top: var(--space-3);
  }
`;

const WrapTransactionDetail = styled.div``;

const Content = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  border: 1px solid #edeef0;
  border-radius: 16px;
  padding: var(--space-2) var(--space-3);
  margin-top: var(--space-2);
`;

const Row = styled.div`
  ${({ transaction, noPad }) =>
    css`
      padding: ${noPad ? '0' : 'var(--space-2) 0'};

      ${Right} {
        color: ${transaction ? 'var( --color-accent-blue-1)' : ''};
      } ;
    `}
  /* margin-top: var(--space-1); */
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #edeef0;
  :last-child {
    border-bottom: none;
  }
`;

const Left = styled.p`
  height: 100%;
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 15px;
  line-height: 21px;
  white-space: nowrap;
  color: var(--color-des);
  margin-right: var(--space-0);
`;

const Right = styled.div`
  height: 100%;
  max-width: 192px;
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 15px;
  line-height: 21px;
  display: flex;
  justify-content: flex-end;
  color: var(--color-title);
`;

const Image = styled.img`
  margin-right: 6px;
`;

const Status = styled.div`
  width: 100px;
  height: 100%;
  min-height: 40px;
  display: flex;
  font-weight: 500;
  ${({ status }) => {
    switch (status) {
      case 'pending':
        return css`
          background-color: var(--color-semantic-orange-3);
          color: var(--color-semantic-orange-1);
        `;
      case 'success':
        return css`
          background-color: var(--color-semantic-green-3);
          color: var(--color-semantic-green-1);
        `;
      case 'sold-out':
      case 'time-out':
        return css`
          background-color: var(--color-semantic-pink-3);
          color: var(--color-semantic-pink-1);
        `;
      case 'refund':
        return css`
          background-color: var(--color-semantic-blue-3);
          color: var(--color-semantic-blue-1);
        `;
      default:
        return '';
    }
  }}
  justify-content: center;
  align-items: center;
`;
