import React, { useEffect, useState } from 'react';
import GroupText from '.';

const GroupTextMapping = ({ data }) => {
  return (
    <>
      {data &&
        data.map(
          (item, index) =>
            item.data && (
              <GroupText
                key={index}
                data={item.data}
                title={item?.title}
                titlePosition={item?.titlePosition}
                type={
                  Array.isArray(item.data)
                    ? 'distribution'
                    : item.type
                    ? item.type
                    : ''
                }></GroupText>
            ),
        )}
    </>
  );
};

export default GroupTextMapping;
