import React from 'react';
import styled from 'styled-components';
import { formatMTDToken } from '../../../../../utils/format';

const ProcessBar = ({ min = 0, max = 100, value = 50, currency = 'MTD' }) => {
  const process = React.useMemo(() => {
    return (value * 100) / max;
  }, [min, max, value]);

  return (
    <Root>
      <ValueTop>
        <div style={{ width: `${process}%` }}> {formatMTDToken(value, 3)} </div>
      </ValueTop>
      <WrapBar>
        <BarWidth />
        <BarProcess process={process} />
      </WrapBar>
      <Value>
        {formatMTDToken(max, 3)}
        {currency}
      </Value>
    </Root>
  );
};

export default ProcessBar;

const Value = styled.div`
  font-weight: 700;
  font-size: 13px;
  line-height: 13px;
`;

const ValueTop = styled.div`
  font-weight: 700;
  font-size: 13px;
  line-height: 12px;
  height: 12px;
  width: 100%;
  position: relative;
  div {
    position: absolute;
    top: 0;
    left: 0;
    text-align: right;
  }
`;

const Root = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  color: white;
  justify-content: center;
  align-items: flex-end;

  ${Value}:last-of-type {
    margin-top: 5px;
  }
  ${Value}:first-of-type {
    align-self: flex-start;
  }
`;

const WrapBar = styled.div`
  position: relative;
  width: 100%;
  height: 5px;

  margin-top: 5px;
`;
const BarWidth = styled.div`
  background-color: var(--color-background-text);
  height: 100%;
  border-radius: 3px;
`;
const BarProcess = styled.div`
  border-radius: 3px;
  position: absolute;
  top: 0;
  left: 0;

  background-color: white;

  width: ${(props) => `${props.process}%`};
  height: 100%;
`;
