import React, { useCallback, useEffect, useState } from 'react';
import { Animated } from 'react-animated-css';
import { useHistory, useNavigate, useParams } from 'react-router-dom';
import Button from '@components/atoms/Button';
import ContentPage from '@components/ContentPage';
import FormGroup from '@components/FormGroup';
import Header from '@components/Header';
import InputText from '@components/InputText';
import { WrapButton } from '@page/SendingCoin/styled';
import { timeDuration, timeTr3 } from '@utils/variable';
import styled from 'styled-components';
import Wallet from '@assets/icon/wallet.svg';
import Setting from '@assets/icon/setting.svg';
import ListItem from '@page/BuyToken/components/ListItem';
import CountInputCoin from '@components/CountInputCoin';
import Container from '@components/Container';
import { getBalanceByUserPackId, getPendingDeposit } from '@api/Deposit';
import { numberWithCommas } from '@utils/currencyFormat';
import Input from '@components/Input';
import { validateNumber } from '@utils/validate';
import { toast } from 'react-toastify';
import { getPaddingWhenHasNotch } from '@redux/selector';
import { useAppSelector } from '@redux/configureStore';
import { formatDepositBalance, formatMTDToken } from '@utils/format';
import { getTransactionPending } from '@api/Transaction';
import { useSelector } from 'react-redux';
import HasPendingTransaction from '@page/BuyToken/components/Modal/HasPendingTransaction';

function Step1({ onNext, onViewDepositPending }) {
  // hook
  const navigate = useNavigate();
  const param = useParams();

  // redux
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);

  // states
  const [payment, setPayment] = useState('USDT (TRC20)');
  const [notice, setNotice] = useState('');
  const [error, setError] = useState('');
  const [amount, setAmount] = useState('');
  const [balance, setBalance] = useState();
  const [transactionPending, setTransactionPending] = useState();
  const [hasPendingTransaction, setHasPendingTransaction] = useState();

  // react hooks
  useEffect(() => {
    // get balance by userPackID
    getBalanceByUserPackId(param.id)
      .then((res) => {
        setBalance(res.data);
      })
      .catch((err) => {
        setError(`Something wrong !`);
        console.log('err --->', err);
      });
  }, []);

  const hanndleChangeInput = useCallback((v) => {
    let lastChar = v[v.length - 1];

    if (!lastChar) {
      setAmount('');
      return;
    }
    let regex = /^[0-9]$/g;
    let checkChar = regex.test(lastChar);
    if (checkChar) {
      let removeAllComma = v.replaceAll(',', '');
      let reformat = numberWithCommas(removeAllComma, ',');
      setAmount(reformat);
    }
  }, []);

  const onSubmit = () => {
    let numAmount = amount.split(',').join('');

    let check = validateNumber(Number(numAmount));
    if (check) {
      onNext &&
        onNext({
          amount: numAmount,
          paymentMethod: payment,
          fromStep: 1,
          notice: notice,
        });
    } else {
      toast.error('Amount is not in the correct format', {
        position: toast.POSITION.TOP_RIGHT,
      });
    }
  };

  console.log('balance --->', balance);

  const hanldeConfirm = async () => {
    let numAmount = amount.split(',').join('');
    let validatePassed = validateNumber(Number(numAmount));

    if (!validatePassed)
      return toast.error('Amount is not in the correct format', {
        position: toast.POSITION.TOP_RIGHT,
      });
    try {
      const hasPendingTransaction = await getPendingDeposit(param.id);

      if (hasPendingTransaction.data !== null) {
        setTransactionPending(hasPendingTransaction.data);
        setHasPendingTransaction(true);
      } else {
        onNext?.({
          amount: numAmount,
          paymentMethod: payment,
          fromStep: 1,
          notice: notice,
        });
      }
    } catch (error) {
      console.log('error --->', error);
    }
  };

  const handleViewTransaction = useCallback(() => {
    console.log('transactionPending --->', transactionPending);

    const { txnTron, userPack } = transactionPending;
    const amount = txnTron.transferVolume.toString();
    const tmpStep1 = {
      amount: amount.slice(0, -6),
      paymentMethod: payment,
      notice: userPack.note,
    };
    const tmpStep2 = {
      prePayment: amount.slice(0, -6) + '.' + amount.slice(-6),
      address: userPack.MTDAddress,
      userPackId: userPack.ID,
      dueTime: txnTron.dueTime,
      toSmc: `xsmart${userPack.ID}`,
    };
    onViewDepositPending && onViewDepositPending({ tmpStep1, tmpStep2 });
  }, [transactionPending]);

  return (
    <Container style={{ background: '#554CFF', ...paddingWhenHasNotch }}>
      <ContentPage
        childrenButton={
          <WrapButton>
            <Button
              title="Continue"
              onClick={hanldeConfirm}
              disable={error || !amount}
            />
          </WrapButton>
        }>
        <Header
          children={'Deposit'}
          onBack={() => navigate(-1)}
          color={'#fff'}
          backgound={'rgb(85, 76, 255)'}
        />
        <Animated
          animationIn="fadeInRightCustom"
          animationInDuration={timeDuration}
          animationInDelay={timeTr3 * 2}>
          <WrapCard>
            <div className="top">
              <img src={icon1} alt="icon" />
              <div>Balance</div>
            </div>
            <div className="bottom">
              <div className="left">
                <div className="unit">USDT</div>
                <div style={{ fontWeight: 400 }}>TRC20</div>
              </div>
              <div className="right">
                {balance ? <div>{formatDepositBalance(balance.balance)}</div> : null}
              </div>
            </div>
          </WrapCard>
        </Animated>
        <WrapLoan>
          <Animated
            animationIn="fadeInRightCustom"
            animationInDuration={timeDuration}
            animationInDelay={timeTr3 * 2}>
            <WrapCard>
              <div className="top">Debt Balance</div>
              <div className="bottom">
                <div className="left">
                  <div className="unit">USDT</div>
                  <div style={{ fontWeight: 400 }}>TRC20</div>
                </div>
                <div className="right">
                  {balance ? (
                    <div>{formatDepositBalance(balance.remainLoan)}</div>
                  ) : null}
                </div>
              </div>
            </WrapCard>
          </Animated>
        </WrapLoan>
        <WrapContent SpaceTop={paddingWhenHasNotch.paddingTop}>
          <WrapInputAmount>
            <TitleInputAmount>Amount</TitleInputAmount>
            <Input
              value={amount}
              onChange={hanndleChangeInput}
              showIcon={false}
              placeholder="0"
              pattern="\d*"
            />
          </WrapInputAmount>

          <WrapPaymentMethod>
            <Animated
              animationIn="fadeInRightCustom"
              animationInDuration={timeDuration}
              animationInDelay={timeTr3 * 3}>
              <FormGroup
                label={'Payment Method'}
                id="payMed"
                controllProps={{
                  copyAble: false,
                  isIcon: false,
                  boxShadow: false,
                  placeholder: 'Type ...',
                  value: payment,
                  onChange: (e) => setPayment(e),
                  disabled: true,
                  borderRadius: 10,
                  style: { fontWeight: 400 },
                }}
                controll={InputText}
              />
            </Animated>
          </WrapPaymentMethod>

          <WrapNotice>
            <Animated
              animationIn="fadeInLeftCustom"
              animationInDuration={timeDuration}
              animationInDelay={timeTr3 * 4}>
              <FormGroup
                label={'Notice'}
                id="notice"
                controllProps={{
                  copyAble: false,
                  placeholder: 'Type ...',
                  boxShadow: false,
                  value: notice,
                  borderRadius: 10,
                  onChange: (e) => setNotice(e),
                }}
                controll={InputText}
              />
            </Animated>
          </WrapNotice>

          {/* {dataPayment.map((item, index) => (
            <ListItem
              key={item.title}
              item={item}
              delayTime={timeTr3 * 6}
              index={index}
            />
          ))} */}
        </WrapContent>
      </ContentPage>
      <HasPendingTransaction
        buttonTitle="my transaction"
        title="Please complete your current transaction or wait for it to expire."
        visible={hasPendingTransaction}
        onClose={() => setHasPendingTransaction(false)}
        onViewTransaction={handleViewTransaction}
      />
    </Container>
  );
}

export default Step1;
