import React from 'react';
import styled from 'styled-components';
import Copy from '@assets/icon/copy.svg';
import { SystemCore } from '@core';
import { useSelector } from 'react-redux';

function WrapInputIcon({
  children,
  value,
  icon = true,
  onClickPaste,
  childrenLeftInput,
}) {
  const isOffline = false;

  const HandleClick = async () => {
    SystemCore.send({
      command: 'get-from-clipboard',
    })
      .then((res) => {
        onClickPaste(res.data);
      })
      .catch((err) => {});
  };
  return (
    <WrapInput>
      {/* <WrapSelectInput>{children}</WrapSelectInput> */}
      {childrenLeftInput
        ? childrenLeftInput
        : icon && (
            <img
              src={Copy}
              style={{
                fill: value ? (isOffline ? '#04004D' : '#D3D7DB') : '#004DFF',
                marginLeft: 25,
              }}
              onClick={HandleClick}
            />
          )}
    </WrapInput>
  );
}

export default WrapInputIcon;

const WrapInput = styled.div`
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 2px 20px rgb(0 0 0 / 1%);
  background: #ffffff;
  padding: 14px 3px;
  padding-right: 10px;
  border: 1px solid var(--color-neutral-5);
  border-radius: 10px;
`;

const WrapSelectInput = styled.div`
  display: flex;
  width: 100%;
`;
