import React from 'react';
import styled from 'styled-components';
import Metanode from '@assets/icon/metanode.svg';
import { numberWithCommas } from '@utils/currencyFormat';
import iconFiat from '@assets/icon/iconFiat.svg';
import iconMeta500 from '@assets/icon/iconMeta500.svg';
import iconMeta100 from '@assets/icon/iconMeta100.svg';
import iconMeta50 from '@assets/icon/iconMeta50.svg';
import iconMeta20 from '@assets/icon/iconMeta20.svg';
import iconMeta10 from '@assets/icon/iconMeta10.svg';
import iconMeta1 from '@assets/icon/iconMeta1.svg';

function DetailListPackage({ dataSteps }) {
  console.log('DetailListPackage --->', dataSteps);
  const renderIcon = (price) => {
    switch (price) {
      case 500000:
        return iconMeta500;
      case 100000:
        return iconMeta100;
      case 50000:
        return iconMeta50;
      case 20000:
        return iconMeta20;
      case 1000:
        return iconMeta1;
      default:
        return iconMeta10;
    }
  };
  return (
    <Wrapper className="mt-2">
      <Head>
        <span className="font-500">Package</span>
        <span className="font-500">Amount</span>
        <span className="font-500">USDT</span>
      </Head>
      <Item>
        <div className="package">
          <div className="avatar">
            <img
              src={
                dataSteps.packPaymentInfo?.fiat
                  ? iconFiat
                  : renderIcon(dataSteps.packPaymentInfo.discountPrice)
              }
              alt=""
            />
          </div>
          <div className="content">
            <div className="font-500">
              {`${numberWithCommas(dataSteps.packPaymentInfo.discountPrice, ',')}$`}
            </div>
            <div className="text-color-2" style={{ fontSize: 11 }}>
              {`1MTD = ${dataSteps.packPaymentInfo.price}$`}
            </div>
          </div>
        </div>
        <div className="amount text">{`x${dataSteps.packPaymentInfo.count}`}</div>
        <div className="amount">{`${numberWithCommas(
          dataSteps.packPaymentInfo.discountPrice * dataSteps.packPaymentInfo.count,
          ',',
        )}$`}</div>
      </Item>
    </Wrapper>
  );
}

export default DetailListPackage;

const Wrapper = styled.div``;
const Head = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid #edeef0;
`;

const Item = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #edeef0;

  .package {
    display: flex;
    gap: 10px;
    .avatar {
      width: 32px;
      height: 32px;
      background: #fbbcd5;
      border-radius: 10px;
      display: grid;
      place-items: center;
    }
  }

  .text {
    font-weight: 400;
    font-size: 15px;
    color: #004dff;
  }
`;
