import { useAppSelector } from '@redux/configureStore';
import { getPaddingWhenHasNotch } from '@redux/selector';
import React, { useEffect, useState } from 'react';
import { useLocation, useParams } from 'react-router-dom';

import ContractDetailResult from '@page/ContractDetailResult';
import Payment from './Payment';
import { dataDetailTransaction } from '@utils/datajsonFake';
import {
  getMtdTransactionDetail,
  getTronTransactionDetail,
  getUserpackTransactionDetail,
} from '@api/Transaction';
import { toast } from 'react-toastify';
import {
  renderDepositItem,
  renderItemTabContract,
  renderItemTabOrder,
  renderItemTabSmartcontract,
  renderRepaymentItem,
  renderRepaymentItemReview,
} from './renderTransactionItem';

const TransactionDetail = () => {
  // hooks
  const params = useParams();
  const location = useLocation();

  // redux
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);

  // states
  const [reviewContract, setReviewContract] = useState(false);
  const [transactionDatas, setTransactionDatas] = useState([]);

  // react hooks
  useEffect(() => {
    if (location) {
      const { apiTab, id } = location.state;
      switch (apiTab) {
        case 'order':
          getTronDetail(id);
          break;
        case 'smc':
          getSmcDetail(id);
          break;
        case 'contract':
          getContractDetail(id);
          break;
        default:
          break;
      }
    }
  }, [location]);

  // functions
  const getTronDetail = async (id) => {
    try {
      const res = await getTronTransactionDetail(id);
      const tranItem = renderItemTabOrder(res.data.txnDetail);
      setTransactionDatas(tranItem);

      if (tranItem.status === 'pending' && tranItem.isReview) {
        setReviewContract(true);
      }
    } catch (error) {
      toast.error(error);
    }
  };

  const getSmcDetail = async (id) => {
    try {
      const res = await getMtdTransactionDetail(id);
      const tranItem = renderItemTabSmartcontract(res.data.txnDetail);
      setTransactionDatas(tranItem);
    } catch (error) {
      toast.error(error);
    }
  };

  const getContractDetail = async (id) => {
    try {
      const res = await getUserpackTransactionDetail(id);
      const tranItem = renderItemTabContract(res.data);
      setTransactionDatas(tranItem);
      if (tranItem.status === 'pending') {
        setReviewContract(true);
      }
    } catch (error) {
      toast.error(error);
    }
  };

  const handlePayment = async () => {
    const res = await getTronTransactionDetail(location.state.id);
    const tranItem = renderRepaymentItem(res.data.txnDetail, true);
    setTransactionDatas(tranItem);
    setReviewContract(true);
  };

  const renderPayment = React.useMemo(() => {
    return (
      <Payment
        transactionData={transactionDatas?.transactionData}
        data={transactionDatas}
      />
    );
  }, [reviewContract, transactionDatas]);

  const renderDetail = React.useMemo(() => {
    return (
      <ContractDetailResult data={transactionDatas} onPayment={handlePayment} />
    );
  }, [reviewContract, transactionDatas]);

  return (
    <div style={paddingWhenHasNotch}>
      {reviewContract ? renderPayment : renderDetail}
    </div>
  );
};

export default TransactionDetail;
