import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPen, faPaste } from '@fortawesome/free-solid-svg-icons';
import { Input, Root, WrapIcon } from './styled';
import { SystemCore } from '@core';
import { Animated } from 'react-animated-css';
import { timeTr3 } from '@utils/variable';
import { toast } from 'react-toastify';

function InputTextarea({
  placeholder,
  value,
  onChange,
  onBlur,
  copyAble = false,
  isIcon = true,
  disabled = false,
  row = 1,
  style = {},
}) {
  const [valueText, setValueText] = useState('');
  const [copied, setCopied] = useState(false);
  const textareaRef = React.useRef(null);
  useEffect(() => {
    setValueText(value || '');
  }, [value]);

  React.useLayoutEffect(() => {
    textareaRef.current.style.height = `${textareaRef.current.scrollHeight}px`;
  }, [valueText]);

  const handleCopy = () => {
    console.log('starting copy -->');

    SystemCore.send({
      command: 'copy-clipboard',
      value: valueText,
    })
      .then((res) => {
        if (res) {
          setCopied(true);
          setTimeout(() => {
            setCopied(false);
          }, 2000);
        }
      })
      .catch((err) => {
        toast.error('Copy fail !');
      });
  };

  const handleOnChange = (e) => {
    setValueText(e);
    onChange?.(e);
  };

  const handelKeyDown = (e) => {
    e.target.style.height = 'inherit';
    e.target.style.height = `${e.target.scrollHeight}px`;
  };

  return (
    <Root>
      <Input
        ref={textareaRef}
        disabled={disabled}
        placeholder={placeholder}
        value={valueText}
        copyAble={copyAble}
        isIcon={isIcon}
        rows={row}
        style={{ ...style, fontSize: 12 }}
        onChange={(e) => {
          handleOnChange(e?.target?.value);
        }}
        onBlur={(e) => {
          onBlur?.(e?.target?.value);
        }}
        onKeyDown={handelKeyDown}
      />
      {isIcon && (
        <WrapIcon>
          <FontAwesomeIcon icon={faPen} />
        </WrapIcon>
      )}
      {copyAble && (
        <WrapIcon pos="end" onClick={handleCopy}>
          <FontAwesomeIcon icon={faPaste} color={''} />
          {copied && (
            <Animated
              animationIn="fadeInRightCustom"
              animationInDelay={timeTr3 * 15}>
              <div className="copyted">Copied</div>
            </Animated>
          )}
        </WrapIcon>
      )}
    </Root>
  );
}

export default InputTextarea;
