import React from 'react';
import { useHistory, useNavigate } from 'react-router-dom';
import { motion, AnimateSharedLayout } from 'framer-motion';
import styled, { css } from 'styled-components';
import Swip from '../Swip';
import { Animated } from 'react-animated-css';
import { timeTr3 } from '@utils/variable';

const MainTab = ({ tabs, currentTab, onChangeTab }) => {
  const defaultActiveTab = React.useMemo(() => {
    if (!tabs?.length) return '';
    if (currentTab) return currentTab;
    return tabs[0].value;
  }, [tabs, currentTab]);

  const [avtiveTab, setAvtiveTab] = React.useState(defaultActiveTab);
  const navigate = useNavigate();

  React.useEffect(() => {
    setAvtiveTab(defaultActiveTab);
  }, [defaultActiveTab]);

  const onClick = React.useCallback((v) => {
    setAvtiveTab(v);
    onChangeTab && onChangeTab(v);
    navigate('', {
      state: { currentTab: v },
      replace: true,
    });
  }, []);

  return (
    <Root>
      <AnimateSharedLayout>
        <Swip>
          {tabs.map((tab, index) => (
            <Animated
              key={index.toString()}
              animationIn="fadeInRightCustom"
              animationInDelay={200 + timeTr3 * index}>
              <Tab
                key={tab.value}
                tab={tab}
                active={tab.value === avtiveTab}
                onClick={() => onClick && onClick(tab.value)}>
                {tab.label}
                {tab.value === avtiveTab ? (
                  <motion.div className="underline" layoutId="underline" />
                ) : null}
              </Tab>
            </Animated>
          ))}
        </Swip>
      </AnimateSharedLayout>
    </Root>
  );
};

export default MainTab;

const Root = styled.div`
  margin-top: calc(var(--space-3) + var(--space-2));
  margin-bottom: 16px;
  display: flex;
  flex-wrap: nowrap;
  overflow: auto;
  margin-left: -20px;
  margin-right: -20px;

  & .Swip {
    /* padding-left: 12px;
    padding-right: 12px; */
    overflow-x: auto;
    overflow-y: hidden;
    padding: 0 8px;
  }

  ::-webkit-scrollbar {
    display: none;
  }

  & {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
`;
export const Tab = styled.div`
  display: flex;
  position: relative;

  font-size: 17px;
  line-height: 23px;
  font-weight: 400;
  margin-right: var(--space-1);
  margin-left: var(--space-1);
  padding-bottom: 5px;

  white-space: nowrap;

  color: var(--color-text-inactive);

  :nth-last-child() {
    margin-right: 0;
  }

  .underline {
    position: absolute;
    bottom: 0px;
    left: 0;
    right: 0;
    width: 35px;
    margin: 0 auto;
    border-radius: 15px;
    height: 3px;
    background: var(--color-background-btn);
  }

  ${({ active }) =>
    active &&
    css`
      font-weight: 500;
      color: #04004d;
    `}
`;
