import React, { memo } from 'react';
import ModalBase from '@components/atoms/ModalBase';
import Thumb from '@components/Thumb';
import styled from 'styled-components';
import Check from '@assets/icon/check.svg';
import { useNavigate } from 'react-router-dom';

function ModalList({
  visible,
  onClose,
  data,
  url,
  itemActive = '',
  onClick,
  title,
  onClickReturnItem = false,
}) {
  const navigate = useNavigate();

  return (
    <ModalBase visible={visible} onClose={() => onClose?.()}>
      <Thumb onClick={() => onClose?.()} />
      {title}
      <WrapScrollView>
        <List>
          {data.map((item) => (
            <div
              onClick={() => {
                url
                  ? navigate(`/${url}/${item?.name}`)
                  : onClick?.(onClickReturnItem ? item : item.id);
                onClose();
              }}
              className="item"
              key={item.name}>
              <div className="text-left">{item?.name}</div>
              <div className="text-right">
                {!itemActive
                  ? item?.value
                  : item.id === itemActive && <img src={Check} alt="" />}
              </div>
            </div>
          ))}
        </List>
      </WrapScrollView>
    </ModalBase>
  );
}

export default memo(ModalList);

const List = styled.div`
  .item {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    border-bottom: 1px solid var(--color-neutral-5);

    .text-left {
      font-family: 'ClearSans';
      font-style: normal;
      font-weight: 500;
      font-size: 15px;
      line-height: 21px;
      color: var(--color-title);
    }
    .text-right {
      font-family: 'ClearSans';
      font-style: normal;
      font-weight: 400;
      font-size: 15px;
      line-height: 21px;
      color: var(--color-title);
    }
  }
`;
const WrapScrollView = styled.div`
  max-height: 310px;
  overflow-x: scroll;
  ::-webkit-scrollbar {
    display: none;
  }
`;
