import React from 'react';
import Button from '@components/atoms/Button';
import ModalBase from '@components/atoms/ModalBase';
import Thumb from '@components/Thumb';
import styled from 'styled-components';
import imgInvoice from '@assets/img/invoice.png';

function Invoice({ visible, setVisible, setShowTransition }) {
  return (
    <ModalBase visible={visible} onClose={() => setVisible(false)}>
      <WrapModal>
        <Thumb onClose={() => setVisible(false)} />
        <Img src={imgInvoice} alt="Invoice" />
        <div className="text">You have a pending invoice!</div>
        <WrapButtonModal>
          <Button
            variant="outlined"
            title={'Close'}
            onClick={() => setVisible(false)}
            noGradient
          />
          <Button
            variant="container"
            title={'View'}
            onClick={() => {
              setShowTransition(true);
              setVisible(false);
            }}
            noGradient
          />
        </WrapButtonModal>
      </WrapModal>
    </ModalBase>
  );
}

export default Invoice;

const WrapModal = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 10px;

  > .text {
    font-weight: 500;
    font-size: 17px;
    line-height: 23px;
    color: #04004d;
  }
`;
const Img = styled.img`
  width: 90px;
  height: 84px;
`;
const WrapButtonModal = styled.div`
  display: flex;
  width: 100%;
  margin-top: 10px;
  gap: 20px;
`;
