import { faSearch } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';
import styled from 'styled-components';

function Input({
  placeholder,
  value,
  onChange,
  showIcon = true,
  type = 'text',
  ...props
}) {
  return (
    <Wrapper>
      {showIcon && <FontAwesomeIcon className="icon-search" icon={faSearch} />}
      <input
        value={value}
        onChange={(e) => onChange(e.target.value)}
        className="input"
        type={type}
        placeholder={placeholder}
        {...props}
      />
    </Wrapper>
  );
}

export default Input;

const Wrapper = styled.div`
  width: 100%;
  height: 48px;
  border: 1px solid #edeef0;
  box-shadow: -6px 4px 36px rgba(0, 0, 0, 0.01), 12px 4px 35px rgba(0, 0, 0, 0.03),
    0px 16px 38px -12px rgba(0, 38, 82, 0.08);
  border-radius: 10px;
  padding: 0px 15px;
  display: flex;
  align-items: center;

  .icon-search {
    color: #d3d7db;
    font-size: 20px;
    margin-right: 10px;
  }
  .input {
    height: 100%;
    width: 100%;
    border: none;
    &::placeholder {
      color: #d3d7db;
    }
  }
`;
