import React, { useCallback, useState } from 'react';
import styled from 'styled-components';
import InputText from '@components/InputText';
import FormGroup from '@components/FormGroup';
import FlexBox from '@components/FlexBox';
import Wallet from '@assets/icon/wallet.svg';
import Setting from '@assets/icon/setting.svg';
import ContentPage from '@components/ContentPage';
import ListItem from '../ListItem';
import { WrapButton } from './Step1';
import Button from '@components/atoms/Button';
import { Animated } from 'react-animated-css';
import { timeDuration, timeTr3 } from '@utils/variable';
import { useSelector } from 'react-redux';
import CheckBox from '@components/CheckBox';
import { checkIsEmail } from '@utils/validate';
import { toast } from 'react-toastify';
import InputTextarea from '@components/InputTextarea';
import useScrollToTop from '@hooks/useScrollToTop';
const Step3 = ({ onNext }) => {
  useScrollToTop();
  const [email, setEmail] = useState('');
  const [refund, setRefund] = useState('');
  const [payment, setPayment] = useState('USDT (TRC20)');
  const [notice, setNotice] = useState('');
  const walletAddress = useSelector((state) => state.global.walletAddress);
  const [check, setCheck] = useState(false);
  const [errEmail, setErrEmail] = useState(false);
  const onSubmit = () => {
    let isEmail = checkIsEmail(email);
    if (isEmail) {
      onNext && onNext({ email, refund, payment, notice, step: 3 });
      setErrEmail(false);
    } else {
      setErrEmail(true);
    }
  };

  return (
    <ContentPage
      childrenButton={
        <div>
          <WrapApproveInfo>
            <WrapIcon>
              <CheckBox
                type="circle"
                value={check}
                onChange={(v) => {
                  v ? setCheck(check + 1) : setCheck(check - 1);
                }}
              />
            </WrapIcon>
            <Text>
              I confirm that the wallet has been backed up to receive tokens; email
              is correct; the USDT wallet (TRC20) is correct to receive the refund
            </Text>
          </WrapApproveInfo>
          <WrapButton>
            <Button
              title="confirm"
              onClick={() => onSubmit()}
              disable={email == '' || refund == '' || !check}
            />
          </WrapButton>
        </div>
      }>
      <Root>
        <FlexBox flexDirection={'column'} gap={'var(--space-2)'}>
          <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 1}>
            <FormGroup
              label={'Email'}
              id="email"
              controllProps={{
                pasteAble: true,
                placeholder: 'Type ...',
                boxShadow: false,
                value: email,
                onChange: (e) => {
                  setErrEmail(false);
                  setEmail(e);
                },
                type: 'email',
                error: errEmail,
              }}
              controll={InputText}
            />
          </Animated>
          <Animated
            animationIn="fadeInLeftCustom"
            animationInDuration={timeDuration}
            animationInDelay={timeTr3 * 2}>
            <FormGroup
              label={'Refund Wallet - USDT (TRC20)'}
              id="refund"
              controllProps={{
                pasteAble: true,
                placeholder: 'Type ...',
                boxShadow: false,
                value: refund,
                onChange: (e) => setRefund(e),
              }}
              controll={InputText}
            />
          </Animated>
          <Animated
            animationIn="fadeInRightCustom"
            animationInDuration={timeDuration}
            animationInDelay={timeTr3 * 3}>
            <FormGroup
              label={'Payment Method'}
              id="payMed"
              controllProps={{
                copyAble: false,
                isIcon: false,
                boxShadow: false,
                placeholder: 'Type ...',
                value: payment,
                disabled: true,
                onChange: (e) => setPayment(e),
                style: { fontWeight: 400 },
              }}
              controll={InputText}
            />
          </Animated>
          <Animated
            animationIn="fadeInLeftCustom"
            animationInDuration={timeDuration}
            animationInDelay={timeTr3 * 4}>
            <FormGroup
              label={'Notice'}
              id="notice"
              controllProps={{
                copyAble: false,
                placeholder: 'Type ...',
                boxShadow: false,
                value: notice,
                onChange: (e) => setNotice(e),
              }}
              controll={InputText}
            />
          </Animated>
          <Animated
            animationIn="fadeInRightCustom"
            animationInDelay={timeTr3 * 16}></Animated>
        </FlexBox>
      </Root>
    </ContentPage>
  );
};
export default Step3;

