import React, { useRef, useEffect } from 'react';
import styled, { css } from 'styled-components';
import Tab from './Tab';
import { Root as TabRoot } from './Tab';

const TabButton = ({ active = false, onClick, tab }) => {
  const tabRef = useRef();
  useEffect(() => {
    let scrollView = document.querySelector('.subtabActive');
    if (scrollView && tabRef.current.className === 'subtabActive') {
      let parent = scrollView.parentElement.parentElement;
      let offsetLeft = tabRef.current.offsetLeft;
      let width = tabRef.current.offsetWidth;
      parent.scrollLeft = offsetLeft - width;
    }
  }, [active]);
  return (
    <Root active={active}>
      <div ref={tabRef} className={active && 'subtabActive'}>
        <Tab onClick={onClick} tab={tab} />
      </div>
    </Root>
  );
};

export default TabButton;

const Root = styled.div`
  ${TabRoot} {
    font-weight: 500;
    font-size: 15px;
    line-height: 15px;
    border: 1px solid #edeef0;
    border-radius: 20px;
    margin-right: var(--space-0);
    margin-left: var(--space-0);

    padding: 10px 16px;
  }

  ${(props) => {
    switch (props.active) {
      case true:
        return css`
          ${TabRoot} {
            background: var(--color-gradient-blue-1);
            color: white;
          }
        `;
      default:
        return '';
    }
  }}
`;
