import React, { useState } from 'react';
import styled from 'styled-components';
import Confirm from '../../components/Confirm';
import { transactions } from '../../../ManageWallet';
import { buyPack } from '@api/BuyToken';
import { useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import { numberWithCommas } from '@utils/currencyFormat';
import useScrollToTop from '@hooks/useScrollToTop';

function Step4({ onSubmit, dataSteps }) {
  useScrollToTop();
  const walletAddress = useSelector((state) => state.global.walletAddress);

  const handleSubmitByToken = (data) => {
    const param = {
      userAddress: walletAddress,
      email: dataSteps.dataStep3.email,
      tronAddress: dataSteps.dataStep3.refund,
      note: dataSteps.dataStep3.notice || '',
      packVariantId: dataSteps.dataStep2.packvariantMapingId,
      numberPackVariant: dataSteps.dataStep1.packPaymentInfo.count,
    };

    buyPack(param)
      .then((res) => {
        let tmpDepositMoney = res.data.prePayment.toString();
        const buytokenResResult = {
          prePayment:
            numberWithCommas(tmpDepositMoney.slice(0, -6), ',') +
            '.' +
            tmpDepositMoney.slice(-6),
          address: res.data.smAddress,
          userPackId: res.data.userPackId,
        };
        onSubmit?.({
          buytokenRes: buytokenResResult,
          dataUser: data.dataUser,
          dataTransaction: data.dataTransaction,
          step: 4,
        });
      })
      .catch((err) => {
        console.log('err -->', err);
        toast.error('Buy token fail', {
          position: toast.POSITION.TOP_CENTER,
        });
      });
  };
  return (
    <>
      <WrapConfirm>
        <Confirm dataSteps={dataSteps} onSubmit={handleSubmitByToken} />
      </WrapConfirm>
    </>
  );
}

export default Step4;
const WrapConfirm = styled.div`
  padding-bottom: 20px;
`;
