import Container from '@components/Container';
import DetailTransaction from '@components/DetailTransaction';
import Header from '@components/Header';
import { transactions } from '@page/ManageWallet';
import React, { useMemo } from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';

const MyOrderDetail = () => {
  const navigate = useNavigate();
  const dataCustom = useMemo(
    () => [
      {
        name: 'Transaction',
        customRenderValue: () => (
          <span style={{ color: 'var( --color-accent-blue-1)' }}>
            {transactions[0].transaction}
          </span>
        ),
      },
      { name: 'Status', status: 'success' },
      {
        name: 'From Wallet',
        value: '0x32-characters Kaa,aaaaaaa,smska0291mMaks',
      },
      { name: 'Amount of coin', value: '2,000$' },
      { name: 'To Wallet', value: 'x2478127987192312893' },
      { name: 'Payment Methods', value: 'USDT(TRC20)' },
      { name: 'Payment time', value: '01 Jan 2023, 11:00' },
    ],
    [],
  );
  return (
    <>
      <Container>
        <Header
          children={'Detail My Order'}
          onBack={() => navigate('/manage-tokenomic')}
        />
        <Wrapper>
          <DetailTransaction data={dataCustom} showTitle={false}></DetailTransaction>
        </Wrapper>
      </Container>
    </>
  );
};

export default MyOrderDetail;

const Wrapper = styled.div`
  overflow: hidden;
  .swiper-3d .swiper-slide-shadow {
    background-color: unset;
  }
`;
