import { createSlice } from '@reduxjs/toolkit';
export interface DepositState {
  onDeponsit: boolean;
}

const initialState: DepositState = {
  onDeponsit: false,
};

const deponsitSlice = createSlice({
  name: 'deponsit',
  initialState,
  reducers: {
    setDeponsit(state, action) {
      return {
        ...state,
        onDeponsit: action.payload,
      };
    },
  },
});

export const { setDeponsit } = deponsitSlice.actions;
export default deponsitSlice.reducer;
