import React from 'react';
import styled from 'styled-components';
import { membershipIcon } from '../../../../assets/icon';
import Membership from './components/Membership';

const Benefit = () => {
  return (
    <Root>
      <Membership
        memberships={memberships}
        membershipConditions={apiConditions}
      />
    </Root>
  );
};

export default Benefit;

const Root = styled.div``;

const apiConditions = [
  {
    label: 'Lorem ipsum 1',
    base: true,
    silver: true,
    gold: true,
    platinum: true,
  },
  {
    label: 'Lorem ipsum 2',
    base: true,
    silver: true,
    gold: true,
    platinum: true,
  },
  {
    label: 'Lorem ipsum 3',
    base: false,
    silver: true,
    gold: true,
    platinum: true,
  },
  {
    label: 'Lorem ipsum 4',
    base: false,
    silver: false,
    gold: true,
    platinum: true,
  },
];

const membershipType = Object.keys(apiConditions[0]).filter(
  (v) => v !== 'label',
);

const memberships = membershipType.map((v) => ({
  label: v,
  icon: membershipIcon[v],
  conditions: apiConditions
    .filter((condition) => condition[v])
    .map((c) => c.label),
}));
