import React from 'react';
import styled from 'styled-components';
import { formatMoney } from '../../../../utils/format';

const Transaction = ({ transaction, onClick }) => {
  const mappingStatusLabel = React.useCallback((status) => {
    switch (status) {
      case 'pending':
        return 'Pending';
      case 'success':
        return 'Success';
      case 'sold-out':
        return 'Sold Out';
      case 'time-out':
        return 'Time Out';
      case 'refund':
        return 'Refund';
      default:
        return '';
    }
  }, []);

  return (
    <Root onClick={() => onClick(transaction)}>
      <WrapLeft>
        <Left>
          <Title>{transaction.title}</Title>
          <DateTime>{transaction.dateTime}</DateTime>
        </Left>
      </WrapLeft>
      <WrapRight>
        <Right>
          <Value>{formatMoney(transaction.value)} MTD</Value>
          <Status>{mappingStatusLabel(transaction.status)}</Status>
        </Right>
      </WrapRight>
    </Root>
  );
};

export default Transaction;

const Root = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;

  & + & {
    margin-top: 14px;
  }
`;

const Title = styled.div``;
const DateTime = styled.div``;

const Left = styled.div`
  display: flex;
  flex-direction: column;
`;
const WrapLeft = styled.div``;

const Value = styled.div``;
const Status = styled.div``;

const Right = styled.div`
  display: flex;
  flex-direction: column;
  text-align: right;
`;
const WrapRight = styled.div``;
