import Button from '@components/atoms/Button';
import GroupText from '@components/atoms/GroupText';
import CheckBox from '@components/CheckBox';
import ContentPage from '@components/ContentPage';
import WaitingModal from '@components/Modal/WaitingModal';
import { numberFormatShowMTD, numberWithCommas } from '@utils/currencyFormat';
import { capitalizeFirstLetter } from '@utils/format';
import { timeTr3 } from '@utils/variable';
import { format } from 'date-fns';
import React, { useEffect, useMemo, useState } from 'react';
import { Animated } from 'react-animated-css';
import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import styled from 'styled-components';
import DistributionComponent from './DistributionTable';
import { WrapButton } from './ListStep/Step1';
import ModalBuyToken from './Modal';
import AntiSpam from './Modal/AntiSpam';

function Confirm({ onSubmit, dataSteps }) {
  const params = useParams();

  const [visible, setvisible] = useState(0);
  const [checked, setChecked] = useState(false);
  const walletAddress = useSelector((state) => state.global.walletAddress);
  const [round, setRound] = useState(4);

  useEffect(() => {
    if (params.id) {
      setRound(params.id);
    }
  }, [params]);

  console.log('Confirm --->', dataSteps);

  const dataUser = useMemo(() => {
    const { dataStep3 } = dataSteps;
    const data = {
      email: `${dataStep3.email}`,
      refundWallet: `${dataStep3.refund}`,
      paymentMethod: 'USDT (TRC20)',
    };

    return data;
  }, [dataSteps]);

  const dataTransaction = useMemo(() => {
    const { dataStep1, dataStep2 } = dataSteps;
    const data = {
      round: `${capitalizeFirstLetter(dataStep1.roundInfo.description)}`,
      createTime: format(
        new Date(dataStep1.roundInfo.CreatedAt),
        'dd MMMM yyyy, HH:mm',
      ),
      package: [
        `${numberWithCommas(dataStep1.packPaymentInfo.discountPrice, ',')} (${
          dataStep1.packPaymentInfo.price
        }$/MTD) x${dataStep1.packPaymentInfo.count}`,
      ],
      receiveMTDWallet: `0x${walletAddress}`,
      paymentUSDT: `${numberWithCommas(dataStep2.totalUsdt, ',')}$`,
      receiveMTD: `${numberFormatShowMTD(dataStep2.totalMtd, 0)} MTD`,
    };

    return data;
  }, [dataSteps]);

  const hanldeBuyMtd = () => {
    onSubmit && onSubmit({ dataUser, dataTransaction });
    setvisible(1);
    setTimeout(() => {
      setvisible(0);
    }, 1800);
  };

  return (
    <ContentPage
      childrenButton={
        <div>
          <WrapApproveInfo>
            <WrapIcon>
              <CheckBox
                type="circle"
                value={checked}
                onChange={(v) => {
                  v ? setChecked(checked + 1) : setChecked(checked - 1);
                }}
              />
            </WrapIcon>
            <Text>
              I confirm that the wallet has been backed up to receive tokens; email
              is correct; the USDT wallet (TRC20) is correct to receive the refund
            </Text>
          </WrapApproveInfo>
          <WrapButton>
            <Button
              variant="container"
              title={'Buy MTD'}
              noGradient
              onClick={() => setvisible(2)}
              disable={!checked}
            />
          </WrapButton>
        </div>
      }>
      <Root>
        <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 2}>
          <Title> You have a transaction pending for your confirmation</Title>
          <GroupText title="Transaction detail" data={dataTransaction} />
          <GroupText data={dataUser} />

          {round > 3 ? (
            <>
              {dataSteps.dataStep2.dataPrepayment && (
                <GroupText
                  data={dataSteps.dataStep2.dataPrepayment}
                  title="PREPAYMENT"
                  titlePosition="center"
                />
              )}
              {dataSteps.dataStep2.dataRepayment && (
                <GroupText
                  data={dataSteps.dataStep2.dataRepayment}
                  title="REPAYMENT"
                  titlePosition="center"
                />
              )}
              {dataSteps.dataStep2.getPayments && (
                <GroupText
                  data={dataSteps.dataStep2.getPayments}
                  titlePosition="center"
                  type="distribution"
                />
              )}
            </>
          ) : (
            <DistributionComponent
              round={round}
              totalMtd={dataSteps.dataStep2.totalMtd}
            />
          )}
        </Animated>
        <AntiSpam
          visible={visible === 2}
          onClose={() => setvisible(0)}
          onClick={hanldeBuyMtd}
        />
        <ModalBuyToken
          visible={visible === 1}
          content={<WaitingModal title="BUY MTD" />}
        />
      </Root>
    </ContentPage>
  );
}

export default Confirm;

const Root = styled.div``;
const Title = styled.div`
  font-weight: 500;
  font-size: 17px;
  line-height: 23px;
  display: flex;
  align-items: flex-end;

  color: #004dff;
`;

const WrapApproveInfo = styled.div`
  display: flex;
  margin: 12px 0;
  margin-top: 0;
  justify-content: center;
  align-items: flex-start;
`;

const WrapIcon = styled.div`
  display: flex;
  align-items: center;
  width: 24px;
  margin-right: var(--space-1);
  position: relative;
  margin-top: 4px;
`;

const Text = styled.span`
  font-weight: 400;
  font-size: 13px;
  line-height: 18px;
  color: #667386;
`;
