import { createTime } from '@utils/date';
import React, { useEffect, useState } from 'react';

const minute = 0;

export default function CountDownPending({
  onDone = undefined,
  isReload = false,
  time = 5,
}) {
  const [timePending, setTimePending] = useState(createTime(time, minute));
  let timeDown = null;

  const handleCountDown = (time, func) => {
    let t = time;
    timeDown = setInterval(() => {
      if (t === 0) {
        if (typeof func === 'function') {
          func();
        }
        clearInterval(timeDown);
      }
      let str = createTime(t, minute);
      setTimePending(str);
      t--;
    }, 1000);
  };

  useEffect(() => {
    if (isReload) {
      handleCountDown(time, onDone);
    }
  }, [isReload]);

  useEffect(() => {
    handleCountDown(time, onDone);
    return () => {
      clearInterval(timeDown);
    };
  }, []);

  return <>{timePending}</>;
}
