import { transactionStatus } from '@assets/icon';
import ArrowRedo from '@assets/icon/arrowRedo.svg';
import Button from '@components/atoms/Button';
import FlexBox, { FlexItem } from '@components/FlexBox';
import FormGroup from '@components/FormGroup';
import InputText from '@components/InputText';
import InputTextarea from '@components/InputTextarea';
import { useAppDispatch } from '@redux/configureStore';
import { setDeponsit } from '@redux/Deposit/reducer';
import { createTime } from '@utils/date';
import { timeTr3 } from '@utils/variable';
import React, { useEffect, useLayoutEffect, useMemo, useRef, useState } from 'react';
import { Animated } from 'react-animated-css';
import styled, { css } from 'styled-components';
import CountDownPending from './CountDownPending';
import Note from './Note';
import WrapQr from './WrapQr';

const ContractDetail = ({ transactionData, childrenTransactions, buttonBottom }) => {
  const dispath = useAppDispatch();
  const [status, setStatus] = useState('pending');
  const [timeOut, setTimeOut] = useState(false);
  const [isReload, setIsReload] = useState(false);
  useLayoutEffect(() => {
    document.querySelector('body').scrollTo(0, 0);
  }, []);

  const mappingStatusLabel = React.useCallback(
    (status) => {
      switch (status) {
        case 'pending':
          return 'PENDING';
        case 'success':
          return 'SUCCESS';
        case 'sold-out':
          return 'SOLDOUT';
        case 'time-out':
          return 'TIMEOUT';
        case 'expired':
          return 'EXPIRED';
        case 'refund':
          return 'REFUND';
        default:
          return '';
      }
    },
    [status],
  );

  const timeCountDown = useMemo(() => {
    if (transactionData.dueTime) {
      const sub = transactionData.dueTime - Math.floor(Date.now() / 1000);

      if (sub > 0) {
        return sub;
      }
      return 15 * 60;
    }
    return 15 * 60;
  }, []);

  const onDeponsitDone = () => {
    dispath(setDeponsit(true));
    setStatus('expired');
    setTimeOut(true);
    setIsReload(false);
  };

  const handleRedo = () => {
    dispath(setDeponsit(false));
    setStatus('pending');
    setTimeOut(false);
    setIsReload(true);
  };

  return (
    <Root>
      <FlexBox flexDirection={'column'} gap={'var(--space-2)'}>
        <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 1}>
          <FlexBox alignItem={'center'}>
            <FlexItem flex={'0 1 30%'}>
              <TextLeft>Transaction</TextLeft>
            </FlexItem>
            <FlexItem flex={'0 1 70%'}>
              <TextRight transaction>0xb082dbdb7645082dbdb7645</TextRight>
            </FlexItem>
          </FlexBox>
        </Animated>
        <Animated animationIn="fadeInLeftCustom" animationInDelay={timeTr3 * 2}>
          <FlexBox alignItem={'center'}>
            <FlexItem flex={'0 1 30%'}>
              <TextLeft>Status</TextLeft>
            </FlexItem>
            <FlexItem flex={'0 1 70%'}>
              <TextRight className="">
                <Status status={status}>
                  <Image src={transactionStatus[status]} />
                  {mappingStatusLabel(status)}
                </Status>
              </TextRight>
            </FlexItem>
          </FlexBox>
          {!timeOut && (
            <FlexboxContant>
              <FlexBox alignItem={'center'}>
                <FlexItem flex={'0 1 30%'}>
                  <TextLeft>Time</TextLeft>
                </FlexItem>
                <FlexItem flex={'0 1 70%'}>
                  <FlexItemChild>
                    <TextRight className="status">
                      <CountDownPending
                        onDone={onDeponsitDone}
                        isReload={isReload}
                        time={timeCountDown}
                      />
                    </TextRight>
                    {status === 'expired' && (
                      <ReFundIcon onClick={handleRedo}>
                        <img src={ArrowRedo}></img>
                      </ReFundIcon>
                    )}
                  </FlexItemChild>
                </FlexItem>
              </FlexBox>
            </FlexboxContant>
          )}
        </Animated>
      </FlexBox>
      {!timeOut && <WrapQr />}
      {!timeOut && (
        <WrapFormGroup>
          <FlexBox gap={'var(--space-3)'} flexDirection={'column'}>
            <FormGroup
              styleLabel={{
                fontWeight: 'bold',
              }}
              label={'Or deposit the exact amount'}
              controll={InputText}
              controllProps={{
                copyAble: true,
                isIcon: false,
                value: `${transactionData?.prePayment}`,
                disabled: true,
              }}
            />

            <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 5}>
              <FormGroup
                styleLabel={{
                  fontWeight: 'bold',
                }}
                label={'To USDT (TRC20) wallet address'}
                controll={InputTextarea}
                controllProps={{
                  copyAble: true,
                  isIcon: false,
                  value: `${transactionData?.address}`,
                  disabled: true,
                  style: { color: 'rgb(0, 77, 255)', height: 'unset !important' },
                }}
              />
            </Animated>
          </FlexBox>
        </WrapFormGroup>
      )}
      {!timeOut && (
        <Animated animationIn="fadeInLeftCustom" animationInDelay={timeTr3 * 6}>
          <WrapTransactionDetail>
            <Note />
          </WrapTransactionDetail>
        </Animated>
      )}

      <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 7}>
        <WrapTransactionDetail>{childrenTransactions}</WrapTransactionDetail>
      </Animated>
      <WrapButton>{buttonBottom}</WrapButton>
    </Root>
  );
};
export default ContractDetail;

const Root = styled.div`
  flex: 1;
  height: 100%;
  position: relative;
`;

export const WrapButton = styled.div`
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  min-height: 78px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  z-index: 20;
  padding: 20px 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.07);
`;

const TextLeft = styled.div`
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;
  color: var(--color-neutral-1);
`;

const TextRight = styled.div`
  ${({ transaction }) => {
    return css`
      font-weight: 500;
      font-size: 15px;
      line-height: 17px;
      color: ${transaction
        ? 'var(--color-accent-blue-1)'
        : 'var(--color-neutral-1)'};
      &.status {
        background: #ffeaf2;
        color: #ff0366;
        border-radius: 4px;
        padding: 10px 12px;
        line-height: 15px;
        display: flex;
        justify-content: center;
        align-items: center;
        &.error {
          background: rgba(251, 188, 213, 0.5);
          color: #ff0366;
        }
      }
    `;
  }}
`;

const WrapFormGroup = styled.div`
  margin-top: var(--space-3);
  input {
    color: var(--color-accent-blue-1);
  }
`;

const WrapTransactionDetail = styled.div`
  margin-top: var(--space-5);
`;

const FlexboxContant = styled.div`
  margin-top: 10px;
`;

const FlexItemChild = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  div {
    margin-right: 10px;
  }
`;
const Status = styled.div`
  width: 110px;
  height: 100%;
  min-height: 30px;
  display: flex;
  font-weight: 500;
  border-radius: 4px;
  ${({ status }) => {
    switch (status) {
      case 'pending':
        return css`
          background-color: var(--color-semantic-orange-3);
          color: var(--color-semantic-orange-1);
        `;
      case 'success':
        return css`
          background-color: var(--color-semantic-green-3);
          color: var(--color-semantic-green-1);
        `;
      case 'expired':
      case 'sold-out':
      case 'time-out':
        return css`
          background-color: var(--color-semantic-pink-3);
          color: var(--color-semantic-pink-1);
        `;
      case 'refund':
        return css`
          background-color: var(--color-semantic-blue-3);
          color: var(--color-semantic-blue-1);
        `;
      default:
        return '';
    }
  }}
  justify-content: center;
  align-items: center;
`;

const ReFundIcon = styled.div`
  width: 32px;
  height: 32px;
  background: #ff0366;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Image = styled.img`
  margin-right: 8px;
`;
