import React, { useCallback } from 'react';
import styled from 'styled-components';
import WalletConect from '@assets/icon/walletConect.svg';
import WalletError from '@assets/icon/walletError.svg';
import Expand from '@assets/icon/expand.svg';
import Scan from '@assets/icon/scan.svg';
import iconExitApp from '@assets/icon/iconExitApp.svg';
import { Animated } from 'react-animated-css';
import { timeTr3 } from '@utils/variable';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBell } from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';
// import avarta from '@assets/images/avatertoken.png';
import avarta from '@assets/icon/iconHomePage.svg';
import logoToken from '@assets/images/iconLogoToken.png';

import { useDispatch, useSelector } from 'react-redux';
import {
  setDefaultNoticeTab,
  setIsConnect,
  setManageTokenomicDefaultTab,
} from '@redux/Global/reducer';
import { SystemCore } from '@core';
const Header = ({ onConnect }) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const isWalletActive = useSelector((state) => state.global.walletAddress);

  const onClickBell = useCallback(() => {
    if (isWalletActive) {
      dispatch(setDefaultNoticeTab(1));
      navigate('notication');
    } else {
      dispatch(setIsConnect(true));
    }
  }, [isWalletActive]);

  const handleExitApp = useCallback(() => {
    console.log('on disconnect start');
    SystemCore.send({
      command: 'exit-app',
    })
      .then((res) => {
        console.log('disconnect success', res);
      })
      .catch((err) => {
        console.log('disconnect  error', err);
      });
  }, []);

  return (
    <Root>
      <WrapContent>
        <NoneImg></NoneImg>
        <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3}>
          <WrapTitle>
            <Title>Metanode Funding</Title>
          </WrapTitle>
        </Animated>
      </WrapContent>
      <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 2}>
        {/* {isConnected ? ( */}
        <WrapConnectWallet>
          {isWalletActive ? (
            <WrapIcon
              onClick={() => {
                dispatch(setManageTokenomicDefaultTab(1));
                navigate('/manage-tokenomic');
              }}
              style={{ marginRight: 10 }}>
              <img src={WalletConect} alt="" />
            </WrapIcon>
          ) : (
            <WrapIcon onClick={onConnect} style={{ marginRight: 12 }}>
              <img src={WalletError} alt="" />
            </WrapIcon>
          )}

          <WrapIcon
            className="bell"
            onClick={() => onClickBell()}
            style={{ marginRight: 12 }}>
            <FontAwesomeIcon icon={faBell} />
            <Circle className="circle" isconnect={isWalletActive}></Circle>
          </WrapIcon>
          <WrapIcon onClick={handleExitApp}>
            <img src={iconExitApp} alt="" />
          </WrapIcon>
        </WrapConnectWallet>
      </Animated>
    </Root>
  );
};

export default Header;

