import user, { UserState } from './User/reducer';
import global, { GobalState } from './Global/reducer';
import { combineReducers } from 'redux';
import notificationControllerReducer from './NotificationController/reducer';
import settingReducer from './Setting/reducer';
import tokenReducer from './Token/reducer';
import deponsit from './Deposit/reducer';
import globalRef from './globalRef/reducer';
import globalSlice from './Global/reducer';

export interface RootReducer {
  user?: UserState;
  global?: GobalState;
  languageRedux?: any;
}

const rootReducer = combineReducers({
  user,
  global,
  settingReducer,
  notificationControllerReducer,
  tokenReducer,
  deponsit,
  globalRef,
  globalSlice,
});

export default rootReducer;
