import React, { useMemo } from 'react';
import { Animated } from 'react-animated-css';
import { timeTr3 } from '@utils/variable';
import styled from 'styled-components';

const bgTotal = 'linear-gradient(135.09deg, #FC9D00 -10.89%, #FF0366 100%)';
const bgReserve =
  'linear-gradient(90.87deg, #004DFF -41.78%, #3584FB 32.56%, #66B5F8 100%)';
const bgRelease = 'linear-gradient(135.09deg, #FFDF00 -10.89%, #FC9D00 100%)';
const bgAvailability = 'linear-gradient(97.38deg, #FFDF00 -9.41%, #04B800 100%)';
function BlockCard({ type = 'total', position = 'wrap1', content = '' }) {
  const renderbg = useMemo(() => {
    switch (type) {
      case 'total':
      default:
        return {
          background: bgTotal,
          otherBG: [bgReserve, bgRelease, bgAvailability],
          icon: iconTotal,
        };
      case 'reserve':
        return {
          background: bgReserve,
          otherBG: [bgRelease, bgAvailability, bgTotal],
          icon: iconReserve,
        };
      case 'release':
        return {
          background: bgRelease,
          otherBG: [bgReserve, bgTotal, bgAvailability],
          icon: iconRelease,
        };
      case 'availability':
        return {
          background: bgAvailability,
          otherBG: [bgRelease, bgTotal, bgReserve],
          icon: iconAvailability,
        };
    }
  }, [type]);
  return (
    <Wrapper>
      <div className={position}>
        <Animated
          animationIn="fadeInRightCustom"
          animationInDelay={timeTr3 * (4 * 4)}>
          <Wrap className={position} bg={renderbg.background}>
            <Title>
              <img src={renderbg.icon} alt={'icon'} />
              <div className="text">{type}</div>
            </Title>
            <Monney>
              <div className="type">MTD</div>
              <div className="content">{content}</div>
            </Monney>
          </Wrap>
        </Animated>
      </div>
    </Wrapper>
  );
}

export default BlockCard;

