import React, { useState } from 'react';
import { Animated } from 'react-animated-css';
import Radio from '@components/Radio';
import { timeDuration, timeTr3 } from '@utils/variable';
import styled from 'styled-components';

function ListItem({ item, delayTime, index }) {
  const [checked, setChecked] = useState(false);
  return (
    <WrapItem>
      <Animated
        animationIn="roll-in-blurred-right"
        animationInDuration={timeDuration}
        animationInDelay={delayTime + timeTr3 * index}>
        {item.icon}
      </Animated>
      <Animated
        style={{ flex: 1 }}
        animationIn="fadeInRightBig"
        animationInDuration={timeDuration}
        animationInDelay={delayTime + timeTr3 * index + 30}>
        <ContentItem>
          {item.title}
          <span className="des">{item?.des}</span>
        </ContentItem>
      </Animated>
      <Animated
        animationIn="fadeInRightBig"
        animationInDuration={timeDuration}
        animationInDelay={delayTime + timeTr3 * index + 60}>
        <Radio checked={checked} onChange={() => setChecked(!checked)} />
      </Animated>
    </WrapItem>
  );
}

export default ListItem;

const WrapItem = styled.div`
  display: flex;
  padding: 10px 0;
  align-items: center;
  padding-bottom: 10px;
  border-bottom: 1px solid #edeef0;
`;

const ContentItem = styled.div`
  flex: 1;
  padding: 0 15px;
  font-weight: 500;
  color: #04004d;
  display: flex;
  flex-direction: column;
  .des {
    font-weight: 400;
    font-size: 13px;
    line-height: 18px;
    color: #667386;
  }
`;
