import Header from '@components/Header';
import React, { useEffect, useMemo, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styled, { css } from 'styled-components';
import { useAppSelector } from '@redux/configureStore';
import Button from '@components/atoms/Button';
import ContentPage from '@components/ContentPage';
import Checkout from '@page/BuyToken/components/Checkout';
import ContractDetail from '@components/ContractDetail';
import GroupTextMapping from '@components/atoms/GroupText/GroupTextMapping';
import { getContractDetailHeaderTitle } from '@redux/selector';
import DistributionComponent from '@page/BuyToken/components/DistributionTable';

const Payment = ({ transactionData, data = null }) => {
  // hooks
  const navigate = useNavigate();

  // redux
  const contractDetailHeaderTitle = useAppSelector(getContractDetailHeaderTitle);

  // state
  const [mappingData, setMappingData] = useState([]);

  // react hooks
  useEffect(() => {
    if (data.data) {
      const datatmp = JSON.parse(JSON.stringify(data.data));
      const result = datatmp.map((item) => {
        const tmpObj = {};

        if (item.title) {
          tmpObj.title = item.title;
        }
        if (item.titlePosition) {
          tmpObj.titlePosition = item.titlePosition;
        }
        tmpObj.data = item;

        if (tmpObj.data.title) {
          delete tmpObj.data.title;
        }
        if (tmpObj.data.titlePosition) {
          delete tmpObj.data.titlePosition;
        }
        return tmpObj;
      });
      setMappingData(result);
    }
  }, [data]);
  console.log(`trans data --->`, data);

  return (
    data && (
      <Wrapper>
        <Header children={contractDetailHeaderTitle} onBack={() => navigate(-1)} />
        <ContractDetail
          transactionData={transactionData}
          buttonBottom={
            <Button
              variant="container"
              title={'Home'}
              noGradient
              onClick={() => navigate('/')}
            />
          }
          childrenTransactions={
            <>
              <GroupTextMapping data={mappingData} />
              {data?.distributionTableData?.round &&
                data?.distributionTableData?.round <= 3 && (
                  <DistributionComponent
                    round={data?.distributionTableData.round}
                    totalMtd={data?.distributionTableData.totalMtd}
                  />
                )}
            </>
          }></ContractDetail>
      </Wrapper>
    )
  );
};

export default Payment;

const Wrapper = styled.div`
  overflow: hidden;
  padding: 10px 20px 120px;
  .swiper-3d .swiper-slide-shadow {
    background-color: unset;
  }
`;
