import { useAppSelector } from '@redux/configureStore';
import { getPaddingWhenHasNotch } from '@redux/selector';
import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';

import {
  getAllUserTransaction,
  getMtdTransactionDetail,
  getTronTransactionDetail,
} from '@api/Transaction';
import ContractDetailResult from '@page/ContractDetailResult';
import { useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import {
  renderItemNextPayment,
  renderItemPaymentDueAndLate,
  renderItemPaymentOverDue,
  renderItemPrepaymentMTD,
} from './renderPaymentCardTransaction';

const PaymentCardTransaction = () => {
  // hooks
  const location = useLocation();

  // redux
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);
  const walletAddress = useSelector((state) => state.global.walletAddress);

  // states
  const [transactionDatas, setTransactionDatas] = useState([]);

  // react hooks
  useEffect(() => {
    getAllUserTransaction(walletAddress)
      .then((res) => {
        const { data } = res;
        const result = {
          userPack: data.userPack || [],
          txnTron: data.txnTron || [],
          txnMTD: data.txnMTD || [],
        };
        const { userPack, txnTron, txnMTD } = result;
        const { cardData, id } = location.state;

        switch (cardData.type) {
          case 'prepaymentmtd':
          case 'overDue':
            const smcItem = txnMTD.find((i) => i.userPackId == id);
            if (!smcItem) return;
            getSmartContractItem(smcItem.ID, cardData);
            break;
          case 'repaymentdue':
          case 'nextrepayment':
          case 'repaymentlate':
            const tronItem = txnTron.find((i) => i.userPackId == id);
            if (!tronItem) return;
            getTronItem(tronItem.ID, cardData);

          default:
            break;
        }
      })
      .catch((err) => toast.error('Get transaction fail !'));
  }, [location]);

  // functions
  const getSmartContractItem = async (smcItemId, cardData) => {
    try {
      const res = await getMtdTransactionDetail(smcItemId);
      let tranItem;
      if (cardData.type === 'prepaymentmtd') {
        tranItem = renderItemPrepaymentMTD(res.data.txnDetail, cardData);
      }
      if (cardData.type === 'overDue') {
        tranItem = renderItemPaymentOverDue(res.data.txnDetail, cardData);
      }
      console.log('res ---->', res);
      setTransactionDatas(tranItem);
    } catch (error) {
      toast.error(error);
    }
  };

  const getTronItem = async (smcItemId, cardData) => {
    try {
      const res = await getTronTransactionDetail(smcItemId);
      let tranItem;
      if (cardData.type === 'repaymentdue' || cardData.type === 'repaymentlate') {
        tranItem = renderItemPaymentDueAndLate(res.data.txnDetail, cardData);
      }
      if (cardData.type === 'nextrepayment') {
        tranItem = renderItemNextPayment(res.data.txnDetail, cardData);
      }
      setTransactionDatas(tranItem);
    } catch (error) {
      toast.error(error);
    }
  };

  return (
    <div style={paddingWhenHasNotch}>
      <ContractDetailResult data={transactionDatas} />
    </div>
  );
};

export default PaymentCardTransaction;
