import React, { useEffect, useMemo, useState } from 'react';
import { useHistory, useNavigate, useParams } from 'react-router-dom';
import Header from '@components/Header';
import styled from 'styled-components';
import Copy from '@assets/icon/copy.svg';
import BlockCard from './components/BlockCard';
import Category from './components/Category';
import { Animated } from 'react-animated-css';
import { timeTr3 } from '@utils/variable';
import BlockTransactionStatus from './components/BlockTransactionStatus';
import {
  getTransactionMTD,
  getTransactionTron,
  getUserpackDetail,
} from '@api/BuyToken';
import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/css';
import 'swiper/css/effect-cards';
import 'swiper/css/pagination';
import { EffectCards, Mousewheel } from 'swiper';

const contentWrap = {
  total: '1,000,000,000.0000000000000000001',
  reserve: '0.0000000000000000001',
  release: '1,000,000,000',
  availability: '1,000,000,000.0000000000000000001',
};
import { useAppSelector } from '@redux/configureStore';
import { getCardsState, getPaddingWhenHasNotch } from '@redux/selector';
import ContentPage from '@components/ContentPage';
import Button from '@components/atoms/Button';
import BlockTransactionMapping from './components/BlockTransactionMapping';
import { dataDetailTransactionBlock } from '@utils/dataBlockFake';
import { formatMTDToken, formatReleaseToken, prependZero } from '@utils/format';
import { calculatePostPayment, calculatePreRelease } from '@utils/userpackStatus';
import { numberWithCommas } from '@utils/currencyFormat';
import { getUserpackStates } from '@api/Deposit';
import { formatInputCallDataSwiper, sendService } from '@utils/smartContact';
import { useDispatch, useSelector } from 'react-redux';
import { SystemCore } from '@core';
import { systemCore } from '@utils/core';
import { formatReturnValue } from '@utils/handleChainValue';
import { setCardsState } from '@redux/Global/reducer';
import hashCode, { makeid, stringToHash } from '@utils/hashCode';

function DetailSmartContact() {
  // hooks
  const navigate = useNavigate();
  const param = useParams();
  const dispatch = useDispatch();

  // constants
  const name = '0x32-characters-Kaa,a,smska0291m';

  // redux
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);
  const walletAddress = useSelector((state) => state.global.walletAddress);
  const cardsState = useAppSelector(getCardsState);

  // states
  const [listTransaction, setListTransaction] = useState();
  const [mtdAmountCard, setMtdAmountCard] = useState();

  // react hooks
  useEffect(() => {
    console.log('param -->', param);
    const userPackId = param.id;
    getUserpackDetail(userPackId)
      .then((res) => {
        const { data } = res;

        // constant
        const dayPerMonth = 1680;
        const day = 60;
        const startTime = data.userPack.timestamp;
        const currentTime = Math.floor(Date.now() / 1000);
        const timeDiff = currentTime - startTime;
        const userPackMonth = Math.floor(timeDiff / dayPerMonth) + 1;
        console.log('data --->', data);

        console.log('userPackMonth --->', userPackMonth);

        // prepayment card
        const prePaymentDuration = data.userPack.prePaymentDuration;
        const prePaymentCountDown = Math.ceil(
          (userPackMonth * dayPerMonth - timeDiff) / day,
        );
        const caculatePrePaymentRelease = calculatePreRelease(
          userPackMonth + 1,
          data.userPack.PackVariant.totalVolume,
          data.userPack.PackVariant.percentPrePayment,
        );
        console.log('caculatePrePaymentRelease --->', caculatePrePaymentRelease);

        const prePaymentRelease = formatReleaseToken(
          caculatePrePaymentRelease.monthlyRelease,
        );
        console.log('prePaymentRelease --->', prePaymentRelease);

        // payment due card
        const postClaimMonth =
          data.PostMonthList.postClaimMonth !== null
            ? data.PostMonthList.postClaimMonth.monthList.at(-1)
            : 0;
        console.log('postClaimMonth --->', postClaimMonth);

        // const paymentDueCurrentMonth =
        //   userPackMonth > postClaimMonth ? userPackMonth : postClaimMonth;
        const paymentDueDuration = data.userPack.PackVariant.paymentDuration;
        const caculatePaymenDueRelease = calculatePostPayment(
          userPackMonth,
          data.userPack.PackVariant.interest,
          data.userPack.PackVariant.paymentDuration,
          data.pack.discountPrice * data.userPack.numberPackVariant,
          data.userPack.PackVariant.percentPrePayment,
          data.pack.price,
        );
        console.log('caculatePaymenDueRelease ---->', caculatePaymenDueRelease);

        const paymentDueRelease = caculatePaymenDueRelease.mtdRelease.toFixed(0);
        const paymentDuePaymentUSDT = caculatePaymenDueRelease.usdtMonthlyPayment;
        console.log('paymentDuePaymentUSDT ---->', paymentDuePaymentUSDT);

        // nextpayment card
        const nextPaymentCountDown = Math.ceil(
          (userPackMonth * dayPerMonth - timeDiff) / day,
        );

        const nextDueCurrentMonth =
          userPackMonth > postClaimMonth ? userPackMonth + 1 : postClaimMonth + 1;
        const nextPaymentDueDuration = data.userPack.PackVariant.paymentDuration;
        const caculateNextPaymentRelease = calculatePostPayment(
          userPackMonth + 1,
          data.userPack.PackVariant.interest,
          data.userPack.PackVariant.paymentDuration,
          data.pack.discountPrice * data.userPack.numberPackVariant,
          data.userPack.PackVariant.percentPrePayment,
          data.pack.price,
        );
        console.log('caculateNextPaymentRelease ---->', caculateNextPaymentRelease);

        const nextPaymentDueRelease =
          caculateNextPaymentRelease.mtdRelease.toFixed(0);
        const nextPaymentDuePaymentUSDT =
          caculateNextPaymentRelease.usdtMonthlyPayment.toFixed(0);

        // payment late
        const repaymentLateDueDuration = data.userPack.PackVariant.paymentDuration;
        const repaymentLateCountDown = Math.ceil(
          (userPackMonth * dayPerMonth - timeDiff) / day - 14,
        );

        console.log('repaymentLateCountDown ---->', repaymentLateCountDown);

        // temp userpack month for payment late
        let userPackMonthPaymentLate = userPackMonth > 1 ? userPackMonth : 2;

        const caculateRepaymenLateRelease = calculatePostPayment(
          userPackMonthPaymentLate - 1,
          data.userPack.PackVariant.interest,
          data.userPack.PackVariant.paymentDuration,
          data.pack.discountPrice * data.userPack.numberPackVariant,
          data.userPack.PackVariant.percentPrePayment,
          data.pack.price,
        );
        console.log(
          'caculateRepaymenLateRelease ---->',
          caculateRepaymenLateRelease,
        );

        const repaymentLateRelease =
          caculateRepaymenLateRelease.mtdReleaseLate.toFixed(0);
        const repaymentLatePaymentUSDT =
          caculateRepaymenLateRelease.usdtMonthlyPayment.toFixed(0);
        const repaymentLateRevoked =
          caculateRepaymenLateRelease.mtdReleasePenalty.toFixed(0);

        // overdue
        const overDueDuration = data.userPack.PackVariant.paymentDuration;
        const overDueCountDown = 1;
        console.log('overDueDuration ---->', overDueDuration);
        // temp userpack month for overdue
        // const userPackMonthOverdue = 2;
        let userPackMonthOverdue = userPackMonth > 1 ? userPackMonth : 2;
        console.log('userPackMonthOverdue ---->', userPackMonthOverdue);

        const caculateOverdueRelease = calculatePostPayment(
          userPackMonthOverdue - 1,
          data.userPack.PackVariant.interest,
          data.userPack.PackVariant.paymentDuration,
          data.pack.discountPrice * data.userPack.numberPackVariant,
          data.userPack.PackVariant.percentPrePayment,
          data.pack.price,
        );
        const overdueRevoked = caculateOverdueRelease.mtdRelease.toFixed(0);
        console.log('caculateOverdueRelease ---->', caculateOverdueRelease);

        // lItem list
        let lItems = [
          {
            id: 1,
            type: `prepaymentmtd`,
            title: `Prepayment MTD (${prependZero(
              userPackMonth + 1,
            )}/${prePaymentDuration}M)`,
            timeout: `${
              prePaymentCountDown > 0 ? prePaymentCountDown : 1
            } days left`,
            release: numberWithCommas(prePaymentRelease, ','),
            revoked: null,
            currentMonth: prependZero(userPackMonth + 1),
            totalMonth: prePaymentDuration,
            payment: null,
          },
          {
            id: 2,
            type: 'repaymentdue',
            title: `Payment Due (${prependZero(
              userPackMonth,
            )}/${paymentDueDuration}M)`,
            timeout: `${
              prePaymentCountDown > 0 ? prePaymentCountDown : 1
            } days left`,
            release: paymentDueRelease,
            revoked: null,
            currentMonth: prependZero(userPackMonth),
            totalMonth: paymentDueDuration,
            payment: paymentDuePaymentUSDT,
          },
          {
            id: 3,
            type: 'nextrepayment',
            title: `Next Payment (${prependZero(
              userPackMonth + 1,
            )}/${nextPaymentDueDuration}M)`,
            timeout: `${
              nextPaymentCountDown > 0 ? nextPaymentCountDown : 1
            } days left`,
            release: nextPaymentDueRelease,
            revoked: null,
            currentMonth: prependZero(userPackMonth + 1),
            totalMonth: nextPaymentDueDuration,
            payment: nextPaymentDuePaymentUSDT,
          },
          {
            id: 4,
            type: 'repaymentlate',
            title: `Repayment Late (${prependZero(
              userPackMonthPaymentLate - 1,
            )}/${repaymentLateDueDuration}M)`,
            timeout: `${
              repaymentLateCountDown > 0 ? repaymentLateCountDown : 1
            } days left`,
            release: repaymentLateRelease,
            revoked: repaymentLateRevoked,
            currentMonth: prependZero(userPackMonthPaymentLate - 1),
            totalMonth: repaymentLateDueDuration,
            payment: repaymentLatePaymentUSDT,
          },
          {
            id: 5,
            type: 'overDue',
            title: `Repayment Overdue (${prependZero(
              userPackMonthOverdue - 1,
            )}/${overDueDuration}M)`,
            timeout: `${overDueCountDown > 0 ? overDueCountDown : 1} days left`,
            release: null,
            revoked: overdueRevoked,
            currentMonth: prependZero(userPackMonthOverdue - 1),
            totalMonth: overDueDuration,
            payment: null,
          },
        ];

        if (userPackMonth !== postClaimMonth) {
          const idxNext = lItems.findIndex((i) => i.type === 'nextrepayment');
          console.log('id --->', idxNext);
          lItems.splice(idxNext, 1);
        } else {
          const idxReDue = lItems.findIndex((i) => i.type === 'repaymentdue');
          lItems.splice(idxReDue, 1);
        }
        if (
          data.LateMonh.lateMonthList === 'None' ||
          userPackMonth === 1 ||
          repaymentLateCountDown < 1
        ) {
          const idxReLate = lItems.findIndex((i) => i.type === 'repaymentlate');
          lItems.splice(idxReLate, 1);
        }

        if (data.OverDueMonth.overDueMonth === null || userPackMonth === 1) {
          const idxOvderDue = lItems.findIndex((i) => i.type === 'overDue');
          lItems.splice(idxOvderDue, 1);
        }
        if (data.OverDueMonth.overDueMonth !== null) {
          if (
            userPackMonth - 1 !==
            data.OverDueMonth.overDueMonth.monthList.at(-1)
          ) {
            const idxOvderDue = lItems.findIndex((i) => i.type === 'overDue');
            lItems.splice(idxOvderDue, 1);
          } else {
            if (repaymentLateCountDown !== 0) {
              const idxOvderDue = lItems.findIndex((i) => i.type === 'overDue');
              lItems.splice(idxOvderDue, 1);
            }
          }
        }
        if (data.pack.Round.ID < 4 || data.Fiat) {
          lItems = [lItems[0]];
        }

        setListTransaction(lItems);
      })
      .catch((err) => {
        console.log('err -->', err);
      });
  }, []);

  useEffect(() => {
    if (!_.isEmpty(cardsState)) {
      setMtdAmountCard(cardsState);
    } else {
      const userPackId = param.id;
      const input = formatInputCallDataSwiper(userPackId);

      sendService(
        input,
        walletAddress,
        (res) => {
          console.log('sendService res -->', res);
        },
        (err) => {
          console.log('sendService err -->', err);
        },
        {
          message: 'call',
        },
        [],
      );
    }
  }, []);

  const excuteSmartContractResponse = (res) => {
    console.log('res ==>', res);
    const returnVal = formatReturnValue(res.data['return-value'], 'getState');
    const contentWrap = {
      total: formatMTDToken(returnVal[3], 3),
      reserve: formatMTDToken(returnVal[1], 3),
      release: formatMTDToken(returnVal[2], 3),
      availability: formatMTDToken(returnVal[0], 3),
    };
    dispatch(setCardsState(contentWrap));
    setMtdAmountCard(contentWrap);
    console.log('returnVal ==>', returnVal);
  };

  useEffect(() => {
    systemCore.on('excute-smart-contract', excuteSmartContractResponse);
    return () => {
      systemCore.removeEventListener(
        'excute-smart-contract',
        excuteSmartContractResponse,
      );
    };
  }, [excuteSmartContractResponse]);

  return (
    <Wrapper style={paddingWhenHasNotch}>
      <ContentPage
        childrenButton={
          <Button
            title="view transaction"
            onClick={() => navigate('/transaction')}
            style={{ marginBottom: 10 }}
          />
        }>
        <>
          <Header
            children={`xsmart${stringToHash(param.id.toString())}`}
            onBack={() => navigate('/manage-tokenomic')}
            rightChildren={<img src={Copy} style={{ fill: '#667386' }} />}
          />

          {/* call smartcontract thay vi call api nhu hien tai */}
          <SwiperBlock>
            <Swiper
              loop
              effect={'cards'}
              grabCursor={true}
              modules={[EffectCards, Mousewheel]}
              direction="vertical"
              cardsEffect={{
                rotate: false,
                transformEl: '',
                perSlideRotate: 4,
                perSlideOffset: 12,
              }}
              mousewheel={{
                invert: true,
                forceToAxis: true,
              }}
              className="mySwiper">
              <SwiperSlide>
                <BlockCard
                  type="total"
                  position="wrap1"
                  content={mtdAmountCard?.total}
                />
              </SwiperSlide>
              <SwiperSlide>
                <BlockCard
                  type="availability"
                  position="wrap4"
                  content={mtdAmountCard?.availability}
                />
              </SwiperSlide>
              <SwiperSlide>
                <BlockCard
                  type="reserve"
                  position="wrap2"
                  content={mtdAmountCard?.reserve}
                />
              </SwiperSlide>
              <SwiperSlide>
                <BlockCard
                  type="release"
                  position="wrap3"
                  content={mtdAmountCard?.release}
                />
              </SwiperSlide>
            </Swiper>
          </SwiperBlock>

          <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 2}>
            <Category userPackId={param.id} />
          </Animated>
          <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 4}>
            <BlockTransactionMapping listTransaction={listTransaction} />
          </Animated>
        </>
      </ContentPage>
    </Wrapper>
  );
}

export default DetailSmartContact;
const Wrapper = styled.div`
  padding: 0 15px;
  overflow: hidden;
  padding-bottom: 110px;
  .swiper-3d .swiper-slide-shadow {
    background-color: unset;
  }
`;

const SwiperBlock = styled.div`
  height: 220px;
  max-height: 200px;
  margin-top: 40px;
  .mySwiper {
    height: 100%;
    width: 100%;
  }
`;
