import React from 'react';
import { Animated } from 'react-animated-css';
import { timeTr3 } from '@utils/variable';
import styled from 'styled-components';
import Transaction from './Transcation';

const Transactions = ({ transactions = [], onClick, delayTime }) => {
  return (
    <Root>
      <Animated animationIn="fadeInLeftCustom" animationInDelay={delayTime}>
        <Title>Transactions</Title>
      </Animated>
      <WrapTransaction>
        {transactions.map((transaction, index) => (
          <Animated animationIn="fadeInRightCustom" animationInDelay={delayTime + timeTr3 * index}>
            <Transaction
              key={index.toString()}
              transaction={transaction}
              onClick={onClick}
            />
          </Animated>
        ))}
      </WrapTransaction>
    </Root>
  );
};

export default Transactions;

const Root = styled.div``;

const Title = styled.div`
  font-weight: 500;
  font-size: 17px;
  line-height: 23px;
  color: var(--color-neutral-1);
`;

const WrapTransaction = styled.div`
  margin-top: 14px;
`;
