import { typeBlock } from '@utils/dataBlockFake';
import React, { useMemo } from 'react';
import { useNavigate } from 'react-router';
import WrapperPaymentBlock from './WrapperPaymentBlock';

function BlockTransactionStatus({
  type = typeBlock.repaymentdue,
  data,
  handleOnlick,
}) {
  const navigate = useNavigate();

  const renderBlock = React.useMemo(() => {
    let color = {
      colorText: '',
      backgoundColor: '',
    };
    switch (type) {
      case typeBlock.repaymentdue:
        color.colorText = '#554CFF';
        color.backgoundColor = 'rgba(85, 76, 255, 0.05)';
        break;
      case typeBlock.prepaymentmtd:
        color.colorText = '#04B800';
        color.backgoundColor = 'rgba(4, 184, 0, 0.1)';
        break;
      case typeBlock.nextrepayment:
        color.colorText = '#0084F4';
        color.backgoundColor = 'rgba(0, 132, 244, 0.05)';
        break;
      case typeBlock.repaymentlate:
        color.colorText = '#FC9D00';
        color.backgoundColor = 'rgba(252, 157, 0, 0.05)';
        break;
      default:
        color.colorText = '#FF0366';
        color.backgoundColor = 'rgba(255, 3, 102, 0.05)';
        break;
    }

    return (
      <WrapperPaymentBlock color={color} handleOnlick={handleOnlick} data={data} />
    );
  }, [handleOnlick]);

  return <>{renderBlock}</>;
}

export default BlockTransactionStatus;
