import React from 'react';
import styled from 'styled-components';
import ManageWalletHeader from './components/ManageWalletHeader';
import ManageWalletAddress from './components/ManageWalletAddress';
import { LayoutPage } from '../../components/LayoutPage';
import ManageWalletStatus from './components/ManageWalletStatus';
import { walletStatus } from '../../assets/icon';
import NextRelease from './components/NextRelease';
import Transactions from './components/Transactions';
import ModalTransactionDetail from './components/Modal';
import ModalDisconnect from './components/Modal/ModalDisconnect';
import { timeTr3 } from '@utils/variable';
import { Animated } from 'react-animated-css';
import { useAppSelector } from '@redux/configureStore';
import { getPaddingWhenHasNotch } from '@redux/selector';

function ManageWallet() {
  const [visible, setVisible] = React.useState(false);
  const [visibleDisconnect, setVisibleDisconnect] = React.useState(false);
  const [transactionDetail, setTransactionDetail] = React.useState({});
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);

  return (
    <div style={paddingWhenHasNotch}>
      <LayoutPage
        header={<ManageWalletHeader onClick={() => setVisibleDisconnect(true)} />}>
        <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 2}>
          <WrapManageWalletAddress>
            <ManageWalletAddress />
          </WrapManageWalletAddress>
        </Animated>
        <Animated animationIn="fadeInLeftCustom" animationInDelay={timeTr3 * 3}>
          <WrapManageWalletStatus>
            <ManageWalletStatus statuses={statuses} />
          </WrapManageWalletStatus>
        </Animated>
        <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 4}>
          <WrapNextRelease>
            <NextRelease date={'01/03/2023'} value={1_000_000_000} />
          </WrapNextRelease>
        </Animated>
        <WrapTransactions>
          <Transactions
            transactions={transactions}
            onClick={(trans) => {
              setVisible(true);
              setTransactionDetail(trans);
            }}
            delayTime={timeTr3 * 5}
          />
        </WrapTransactions>

        <ModalTransactionDetail
          visible={visible}
          onClose={() => setVisible(false)}
          detail={transactionDetail}
        />

        <ModalDisconnect
          visible={visibleDisconnect}
          onClose={() => setVisibleDisconnect(false)}
        />
      </LayoutPage>
    </div>
  );
}

export default ManageWallet;

