import React, { memo, useCallback, useEffect, useMemo, useState } from 'react';
import styled from 'styled-components';

function TabList({
  list,
  scroll,
  widthMultipleRow,
  padding,
  autoActive = '',
  onClick,
  type,
  defaultSelect,
  isResevers = false,
}) {
  const renderItem = (item) => {
    switch (type) {
      case 'prepayment':
        return `${item}%`;
      case 'year':
        return `${item} year`;
      case 'repayment':
        return `${100 - item}%`;
      default:
        return `${item}%`;
    }
  };

  const [listItem, setListItem] = useState([]);

  const handleClick = (item) => {
    onClick && onClick({ item, type });
  };

  const setDefaultActive = useCallback(() => {
    if (defaultSelect) {
      handleClick(defaultSelect);
    }
  }, [defaultSelect]);

  useEffect(() => {
    setDefaultActive();
  }, []);

  useEffect(() => {
    if (list) {
      if (isResevers) {
        setListItem([...list].reverse());
      } else {
        setListItem([...list]);
      }
    }
  }, [list]);

  return (
    <Wrap
      padding={padding}
      className={`${scroll ? 'scroll' : ''} ${widthMultipleRow ? 'row' : ''} ${
        padding ? 'padding' : ''
      }`}>
      {listItem.map((item) => (
        <div
          key={item}
          onClick={() => handleClick(item)}
          className={`text ${
            item === autoActive || item === 100 - autoActive ? 'active' : ''
          }`}>
          {item && renderItem(item)}
        </div>
      ))}
    </Wrap>
  );
}

export default memo(TabList);

const Wrap = styled.div`
  display: flex;
  justify-content: space-between;
  /* margin-top: var(--space-3); */
  &.scroll {
    flex-wrap: nowrap;
    overflow: auto;
    ::-webkit-scrollbar {
      display: none;
    }
    .text {
      min-width: 48px;
      height: 48px;

      margin-right: 16px;
    }
  }
  &.row {
    flex-wrap: wrap;
    .text {
      width: 48%;
      height: 48px;
      margin-bottom: 16px;
    }
  }
  &.padding {
    white-space: nowrap;

    .text {
      width: unset;
      min-width: unset;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: ${({ padding }) => padding};
      height: auto;
      margin-right: 16px;
      border-radius: 6px;
      font-size: 15px;
      height: 28px;
      color: #667386;
    }
  }
  .text {
    width: 72px;
    height: 40px;
    background: rgba(237, 238, 240, 0.7);
    border-radius: 10px;
    font-size: 15px;
    line-height: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    &.active {
      background: var(--color-text-blur);
      color: white;
    }
  }
`;
