import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCloudArrowUp } from '@fortawesome/free-solid-svg-icons';
import React from 'react';
import { useHistory, useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import FlexBox, { FlexItem } from '../../../components/FlexBox';
import RankTag from '../../../components/RankTag';
import InputTextarea from '../../../components/InputTextarea';
import Button from '@components/atoms/Button';
import { useSelector } from 'react-redux';

const ManageWalletAddress = ({
  label = 'Disconnect',
  onClick,
  type = 'sun',
  showRight = true,
}) => {
  const navigate = useNavigate();
  const walletAddress = useSelector((state) => state.global.walletAddress);

  return (
    <Root onClick={onClick}>
      <FlexBox flexDirection={'column'} gap={'var(--space-2)'}>
        <Row>
          <ItemLeft>
            <div style={{ fontWeight: 500 }}>Metanode Wallet</div>
          </ItemLeft>
          <ItemRight>
            <RankTag
              type={type}
              fullWidth
              onClick={() => {
                navigate('/', { state: { currentTab: 'be' } });
              }}
            />
          </ItemRight>
        </Row>
        <Row>
          <ItemLeft>
            <WrapInput>
              <InputTextarea
                disabled
                copyAble
                isIcon={false}
                height={'14px'}
                value={walletAddress}
                rows={1}
              />
            </WrapInput>
          </ItemLeft>
          {showRight && (
            <ItemRight>
              <Button
                variant="outlined"
                noGradient
                boderWidth={'1px'}
                height={'64px'}
                borderColor={'var(--color-accent-blue-3)'}
                title={
                  <FlexBox flexDirection={'column'} alignItem={'center'}>
                    <FontAwesomeIcon
                      icon={faCloudArrowUp}
                      color={'var(--color-accent-blue-1)'}
                    />
                    <Text>Backup</Text>
                  </FlexBox>
                }
                onClick={() => {
                  navigate({
                    pathname: '/backup-and-restore',
                  });
                }}
              />
            </ItemRight>
          )}
        </Row>
      </FlexBox>
    </Root>
  );
};

const Row = ({ ...props }) => (
  <FlexBox alignItem={'center'} gap={'var(--space-3)'} {...props} />
);
const ItemLeft = ({ ...props }) => (
  <FlexItem flex={'1'} height={'100%'} {...props} />
);
const ItemRight = ({ ...props }) => (
  <FlexItem flex={'0 1 25%'} height={'100%'} {...props} />
);

export default ManageWalletAddress;

const Root = styled.div``;

const Text = styled.div`
  color: var(--color-accent-blue-1);
  background: unset;
  font-weight: 400;
  font-size: 15px;
  line-height: 15px;
  text-transform: capitalize;
  margin-top: 6px;
`;

const WrapInput = styled.div`
  & textarea {
    font-weight: 400;
    font-size: 13px;
    line-height: 18px;
    color: var(--color-neutral-3);
    min-height: auto;
  }
`;
