import React from 'react';
import styled, { css } from 'styled-components';

const StatusHead = ({ statuses = [] }) => {
  const getRoundStatus = (status) => {
    let roundStatus = 'PENDING';
    switch (status) {
      case 'pre_order':
        roundStatus = 'PREORDER';
        break;
      case 'open':
        roundStatus = 'OPEN';
        break;
      case 'close':
        roundStatus = 'CLOSE';
        break;
      case 'sold_out':
        roundStatus = 'SOLD OUT';
        break;
      default:
        break;
    }
    return roundStatus;
  };
  return (
    <Root>
      <Label>Status: </Label>
      {statuses.map((status, index) => (
        <StatusItem key={index.toString()} type={status}>
          {getRoundStatus(status)}
        </StatusItem>
      ))}
    </Root>
  );
};

export default StatusHead;

const Root = styled.div`
  display: flex;
  align-items: center;
  margin-top: var(--space-4);
`;

const Label = styled.div`
  margin-right: 10px;
  font-weight: 500;
  font-size: 15px;
  line-height: 15px;

  color: var(--color-neutral-1);
`;

const StatusItem = styled.div`
  text-transform: uppercase;
  font-weight: 500;
  font-size: 11px;
  line-height: 13px;

  padding: 5px 10px;
  border-radius: 16px;

  & + & {
    margin-left: var(--space-1);
  }

  ${(props) => {
    switch (props.type) {
      case 'open':
        return css`
          color: var(--color-semantic-green-1);
          background-color: var(--color-semantic-green-3);
        `;
      case 'pre-order':
        return css`
          color: var(--color-semantic-blue-1);
          background-color: var(--color-semantic-blue-3);
        `;
      case 'close':
        return css`
          color: var(--color-semantic-pink-1);
          background-color: var(--color-semantic-pink-3);
        `;
      default:
        return css`
          color: var(--color-des);
          background-color: var(--color-text-inactive);
        `;
    }
  }}
`;
