import React from 'react';
import styled from 'styled-components';
import Container, { Wrapper } from './Container';
import ScrollViewer from './ScrollViewer';
import { getPaddingWhenHasNotch } from '@redux/selector';
import { useAppSelector } from '@redux/configureStore';

export const LayoutPage = ({ header, children, bottom }) => {
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);

  return (
    <Container fluid>
      {header && (
        <div>
          <Sticky style={paddingWhenHasNotch}>
            <Container>{header}</Container>
          </Sticky>
        </div>
      )}
      <WrapContainer bottom={bottom}>
        <ScrollViewer>
          <Container>{children}</Container>
        </ScrollViewer>
      </WrapContainer>
    </Container>
  );
};

const Root = styled.div``;

const WrapContainer = styled.div`
  flex: 1;
  display: flex;
  min-height: unset;
  margin-bottom: ${({ bottom }) => bottom};
  ${Wrapper} {
    min-height: unset;
  }
`;

const Sticky = styled.div`
  display: flex;
  ${Wrapper} {
    min-height: unset;
    padding: 6px;
  }
`;
