import React, { useCallback, useEffect, useMemo, useState } from 'react';
import styled from 'styled-components';

import ModalBase from '@components/Modal/ModalBase';
import Thumb from '@components/Thumb';

import arrowCheck from '@assets/icon/arrowCheck.svg';
import TabContent from '@components/TabContent';
import { timeTr3 } from '@utils/variable';

const ModalChooseTransaction = ({
  visible,
  onClose,
  onChangeSmcValue,
  contractList,
  smcList,
}) => {
  // states
  const [selectTab, setSelectTab] = useState(0);
  const [listSmc, setListSmc] = useState();
  const [itemChecked, setItemChecked] = useState({ tab: 0, index: 0 });

  // react hooks
  useEffect(() => {
    setListSmc(smcList);
  }, [smcList]);

  const handleClick = useCallback(
    (index) => {
      setItemChecked({ tab: selectTab, index: index });

      const filterName = renderContractName(index, listSmc[index]);
      let filterNumId = listSmc[index];
      if (typeof listSmc[index] !== 'number') {
        filterNumId = 0;
      }
      onChangeSmcValue &&
        onChangeSmcValue({ filterName, tab: selectTab, filterNumId });
    },
    [selectTab, listSmc, onChangeSmcValue],
  );

  const handleChangeTab = useCallback(
    (tab) => {
      setSelectTab(tab);
      getListSmcByTabId(tab);
    },
    [smcList, contractList],
  );

  const getListSmcByTabId = useCallback(
    (tab) => {
      if (tab === 0) {
        setListSmc(smcList);
      } else {
        setListSmc(contractList);
      }
    },
    [smcList, contractList],
  );

  const renderContractName = useCallback(
    (index, val) => {
      if (selectTab === 0) {
        if (index === 0) return 'All Smart Contract';
        return `xsmart${val}`;
      } else {
        if (index === 0) return 'All Contract';
        return `xcontract${val}`;
      }
    },
    [selectTab],
  );

  return (
    <>
      <ModalBase visible={visible} onClose={() => onClose && onClose()}>
        <Thumb onClose={() => onClose && onClose()}></Thumb>
        <WrapTabContent>
          <TabContent
            onChangTab={(tab) => handleChangeTab(tab)}
            tabList={tabSmcList}
            selectTab={selectTab}
            delayTime={timeTr3 * 4}
          />
        </WrapTabContent>

        <ListSelect>
          {listSmc &&
            listSmc.map((item, index) => (
              <ItemSelect key={index} onClick={() => handleClick(index)}>
                <p> {renderContractName(index, item)} </p>
                {index === itemChecked.index && itemChecked.tab === selectTab && (
                  <img src={arrowCheck}></img>
                )}
              </ItemSelect>
            ))}
        </ListSelect>
      </ModalBase>
    </>
  );
};

export default ModalChooseTransaction;

export const tabSmcList = [
  {
    id: 0,
    name: 'Smart Contract',
  },
  {
    id: 1,
    name: 'Contract',
  },
];

const ListSelect = styled.div`
  height: 165px;
  padding-bottom: 24px;
  overflow-y: auto;
`;

const WrapTabContent = styled.div`
  width: 100%;
  margin-top: 16px;
`;

const ItemSelect = styled.div`
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #04004d;
  padding: 10px;
  /* border-top: 1px solid #edeef0; */
  border-bottom: 1px solid #edeef0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* :first-child {
    border: none;
  }
  :last-child {
    border: none;
  } */
`;
