import Button from '@components/atoms/Button';
import GroupTextMapping from '@components/atoms/GroupText/GroupTextMapping';
import NotePending from '@components/ContractDetail/NotePending';
import ModalBase from '@components/Modal/ModalBase';
import Thumb from '@components/Thumb';
import useScrollToTop from '@hooks/useScrollToTop';
import DetailLayout from '@layouts/Detail';
import ContactDetail from '@page/BuyToken/components/Checkout/ContactDetail';
import DistributionComponent from '@page/BuyToken/components/DistributionTable';
import { useAppSelector } from '@redux/configureStore';
import { getPaddingWhenHasNotch } from '@redux/selector';
import { timeTr3 } from '@utils/variable';
import React, { useEffect, useState } from 'react';
import { Animated } from 'react-animated-css';
import { useLocation, useNavigate } from 'react-router-dom';
import styled from 'styled-components';

const ContractDetailResult = ({ data, onPayment }) => {
  // hooks
  const location = useLocation();
  const navigate = useNavigate();
  useScrollToTop();

  // redux
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);

  // state
  const [mappingData, setMappingData] = useState([]);
  const [showPopupRefund, setShowPopupRefund] = useState(false);

  // react hooks
  useEffect(() => {
    if (data.data) {
      const datatmp = JSON.parse(JSON.stringify(data.data));
      console.log(`data tmp -->`, datatmp);

      const result = datatmp.map((item: any) => {
        const tmpObj = {};

        if (item.title) {
          tmpObj.title = item.title;
        }
        if (item.titlePosition) {
          tmpObj.titlePosition = item.titlePosition;
        }
        tmpObj.data = item;

        if (tmpObj.data.title) {
          delete tmpObj.data.title;
        }
        if (tmpObj.data.titlePosition) {
          delete tmpObj.data.titlePosition;
        }
        return tmpObj;
      });

      setMappingData(result);
    }
  }, [data]);

  // functions
  const buttonType = React.useMemo(() => {
    let title = 'home';
    switch (data.type) {
      case 'repaymentOnTime':
        title = data.status === 'pending' ? 'payment' : 'home';
        break;
      case 'repaymentLate':
        title = data.status === 'pending' ? 'payment' : 'home';
        break;
      case 'contract':
        if (data.status === 'refund') {
          title = 'request refund';
          break;
        }
        if (data.status === 'refundReject') {
          title = 'request again';
          break;
        }
        title = 'my wallet';
        break;
      default:
        title = 'home';
        break;
    }
    return title;
  }, [data]);

  const handleClick = React.useCallback(() => {
    switch (data.type) {
      case 'repaymentOnTime':
        data.status === 'pending' ? onPayment && onPayment(data) : navigate('/');
        break;
      case 'repaymentLate':
        data.status === 'pending' ? onPayment && onPayment(data) : navigate('/');
        break;
      case 'contract':
        if (data.status === 'refund') {
          setShowPopupRefund(true);
          break;
        }
        if (data.status === 'refundReject') {
          setShowPopupRefund(true);
          break;
        }
        navigate('/manage-tokenomic');
        break;
      default:
        navigate('/');
        break;
    }
  }, [data]);

  const renderButton = React.useMemo(() => {
    if (
      (data.status === 'pending' && data.type === 'repaymentOnTime') ||
      (data.status === 'pending' && data.type === 'repaymentLate') ||
      (data.type === 'nextPayment' && data.status === 'pending')
    ) {
      return null;
    }
    return (
      <Button
        title={buttonType}
        variant="container"
        type={
          (data.type === 'repaymentOnTime' || data.type === 'repaymentLate') &&
          data.status === 'pending'
            ? 'payment'
            : ''
        }
        onClick={() => handleClick()}
      />
    );
  }, [data]);

  return (
    <>
      <div style={{ marginTop: `-${paddingWhenHasNotch.paddingTop}px` }}>
        <DetailLayout buttonAction={renderButton} contentOnTop={data.contentOnTop}>
          {(data.status === 'pending' && data.type === 'repaymentOnTime') ||
          (data.status === 'pending' && data.type === 'repaymentLate') ? (
            <Animated animationIn="fadeInLeftCustom" animationInDelay={timeTr3 * 6}>
              <WrapTransactionDetail>
                <NotePending />
              </WrapTransactionDetail>
            </Animated>
          ) : null}

          <GroupTextMapping data={mappingData} />
          {data?.distributionTableData?.round &&
            data?.distributionTableData?.round <= 3 && (
              <DistributionComponent
                round={data?.distributionTableData.round}
                totalMtd={data?.distributionTableData.totalMtd}
              />
            )}
        </DetailLayout>
        <ModalBase
          visible={showPopupRefund}
          onClose={() => setShowPopupRefund(false)}>
          <Thumb onClose={() => setShowPopupRefund(false)} />
          <Text>
            Your refund request has been successfully submitted. Please allow time
            for processing.
          </Text>
          <Button
            title={'close'}
            onClick={() => {
              setShowPopupRefund(false);
              navigate('/manage-tokenomic');
            }}></Button>
        </ModalBase>
      </div>
    </>
  );
};

export default ContractDetailResult;

const Text = styled.div`
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 24px;
  color: #04004d;
  margin-bottom: 22px;
`;
const WrapTransactionDetail = styled.div`
  margin-top: var(--space-5);
`;
