import { Decimal } from 'decimal.js';

Decimal.set({ precision: 60});

const Count = (type, val, val2, toF) => {
  let v = 0;
  if(type === 'minus'){
    v = val.minus(new Decimal(val2));
    if(v < 0) {
      v = 0;
    }
  }else {
    v = val.plus(val2);
  }
  return v.toFixed(toF);
};


export const CountDecimal = (type, valueCoin, toggleType, typeNumberCount) => {
  let value = 0;
    const valueCoinN =  new Decimal(valueCoin || 0);

    if(toggleType === false){
      // when count num $,000
      value = Count(type, valueCoinN, typeNumberCount);
    }else {
        // when count num 0.000$
      const numToString =  valueCoin.toString();
      const length = numToString.length;
      const findIndexD = numToString.indexOf('.');
      if(findIndexD === -1){
        // when num have  $,000,  not 0.000$
        value = Count(type, valueCoinN, 0.1 * typeNumberCount, 1);
      }
      else {
         // when num have 0.000$ , $,000.000$
        if(parseInt(valueCoin.toString().slice(valueCoin.toString().indexOf(".") + 1)) === typeNumberCount && type === 'minus'){
          return valueCoinN.toFixed(0);
        }
        const lengthDecimal = length - findIndexD -1-3;
        if(lengthDecimal >= 0) {
          const arr = new Array(lengthDecimal).fill(0);
          let stringDecimal = arr.join("");
          if(typeNumberCount === 1) {
            stringDecimal= stringDecimal.concat("001");
          }else if(typeNumberCount === 10) {
            stringDecimal= stringDecimal.concat("010");
          }else {
            stringDecimal= stringDecimal.concat("100");
          }
          value = Count(type, valueCoinN, `0.${stringDecimal}`, lengthDecimal+ 3);
        }else {
          // when num have 0.$, 0.0$
          let num= 0;
          let toF = 0;
          switch(lengthDecimal){
            case -1:
              num = 0.01 * typeNumberCount;
              toF = 2;
              break;
            case -2:
              num = 0.1 * typeNumberCount;
              toF = 1;
              break;
            default:
              num = 0;
          }
          value = Count(type, valueCoinN, num, toF);
      }
    }
  }
    return value;
};
