import React from 'react';
import {
  TitleLeft,
  TitleCenter,
  Content,
  TableWrapper,
  ContentWrapper,
} from './style';
import TableRow from './TableRow';
import Table from '@components/Table';
import { numberWithCommas } from '@utils/currencyFormat';

const GroupText = ({
  title = '',
  titlePosition = 'normal',
  data = {} | [],
  type = 'normal',
}) => {
  const renderTitle = React.useMemo(() => {
    switch (titlePosition) {
      case 'normal':
        return <TitleLeft> {title} </TitleLeft>;
      case 'center':
        return <TitleCenter> {title} </TitleCenter>;
      default:
        break;
    }
  }, [titlePosition]);

  const renderTableNormal = React.useCallback(() => {
    let arrElement = [];
    for (const key in data) {
      if (Object.hasOwnProperty.call(data, key)) {
        const elm = data[key];
        let html = <TableRow data={elm} type={key} key={key} />;
        arrElement.push(html);
      }
    }
    return arrElement;
  }, [data]);

  const columns = React.useMemo(() => {
    return [
      {
        id: 1,
        name: 'Year',
        renderCell: ({ year }) => year,
      },
      {
        id: 2,
        name: 'Payment (USDT)',
        renderCell: ({ paymentUSDT }) => (
          <>{`from ${numberWithCommas(paymentUSDT, ',')}$/month `}</>
        ),
      },
      {
        id: 3,
        name: 'Receive (MTD)',
        renderCell: ({ receiveMTD }) => <>{numberWithCommas(receiveMTD, ',')}</>,
      },
    ];
  }, []);

  const renderTableDistribution = React.useCallback(() => {
    return <Table data={data} columns={columns} bg="black" title={'Distribution'} />;
  }, [data]);

  const RenderTable = React.useCallback(() => {
    switch (type) {
      case 'distribution':
        return renderTableDistribution();
      default:
        return <Content type={type}> {renderTableNormal()} </Content>;
    }
  }, [type, data]);

  return (
    <ContentWrapper>
      {title && renderTitle}
      {data && <TableWrapper> {RenderTable()} </TableWrapper>}
    </ContentWrapper>
  );
};

export default GroupText;
