import { getWhiteListPack } from '@api/BuyToken';
import ModalBase from '@components/Modal/ModalBase';
import CheckBackupFile from '@page/BuyToken/components/Modal/CheckBackupFile';
import DeniedBuytoken from '@page/BuyToken/components/Modal/DeniedBuytoken';
import ConfirmBackup from '@page/TokenomicApp/components/Round/components/Modal/ConfirmBackup';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';

const CheckConditionBuytoken = ({ round, onCheckConditionsSuccess, onClose }) => {
  const [showModalDenied, setShowModalDenied] = useState('');
  const walletAddress = useSelector((state) => state.global.walletAddress);

  const getBackedupFile = async () => {
    await sleep(200);
    return true;
    // systemCore
    //   .send({
    //     command: 'get-setting-d-app',
    //     value: {
    //       id: 'com.metatoken.ibe',
    //     },
    //   })
    //   .then((res) => {
    //     if (res && res.success) {
    //       return true;
    //     }
    //   })
    //   .catch((err) => {
    //     return false;
    //   });
  };

  const sleep = (ms) => {
    return new Promise((resolve) => setTimeout(resolve, ms));
  };

  const checkingAllProcess = async () => {
    if (walletAddress) {
      // setShowModalDenied('checkBackupFile');
      const isBackedUpFile = await getBackedupFile();

      if (!isBackedUpFile) {
        setShowModalDenied('noBackUpBefore');
        return;
      }
      if (round.status === 'sold_out') {
        setShowModalDenied('sold_out');
        return;
      }
      if (round.status === 'pending') {
        setShowModalDenied('pending');
        return;
      }

      getWhiteListPack(round.ID, walletAddress)
        .then((res) => {
          console.log(' ~ file: index.js:57 ~ getWhiteListPack ~ res', res);
          if (!res.data) {
            // if user not in white list
            setShowModalDenied('notInWhiteList');
            return;
          } else {
            // all conditions to buytoken passed
            onCheckConditionsSuccess && onCheckConditionsSuccess();
          }
        })
        .catch((err) => {
          setShowModalDenied('notInWhiteList');
        });
    }
  };

  useEffect(() => {
    checkingAllProcess();
  }, []);

  const handeleCofirmBackedUp = () => {};

  const handleCloseAnyModal = () => {
    setShowModalDenied('');
    onClose && onClose();
  };

  return (
    <>
      {showModalDenied !== '' &&
        showModalDenied !== 'checkBackupFile' &&
        showModalDenied !== 'noBackUpBefore' && (
          <DeniedBuytoken
            visible
            type={showModalDenied}
            onClose={handleCloseAnyModal}
          />
        )}
      {/* {showModalDenied === 'checkBackupFile' && (
        <ModalBase visible>
          <CheckBackupFile onClose={handleCloseAnyModal} />
        </ModalBase>
      )} */}
      {showModalDenied === 'noBackUpBefore' && (
        <ConfirmBackup
          visible={showModalDenied === 'noBackUpBefore'}
          onClose={handleCloseAnyModal}
          onBackup={handeleCofirmBackedUp}
        />
      )}
    </>
  );
};

export default CheckConditionBuytoken;
