import { TokenActionTypes } from './action';

const initState = {
  roundOpen: {
    CreatedAt: '',
    UpdatedAt: '',
    DeletedAt: null,
    roundName: '',
    defaultPrice: 0,
    start: '',
    end: '',
    status: 'open',
    description: 'founders',
    roundType: 1,
    totalVolume: 0,
    remainVolume: 0,
  },
};

export default function tokenReducer(state = initState, action) {
  switch (action.type) {
    case TokenActionTypes.SET_ROUND_OPEN:
      return {
        ...state,
        roundOpen: action.payload.data,
      };

    default:
      return { ...state };
  }
}
