import React, { useCallback, useEffect, useState } from 'react';
import { Animated } from 'react-animated-css';
import { useNavigate } from 'react-router-dom';
import Header from '@components/Header';
import TabContent from '@components/TabContent';
import { timeTr3 } from '@utils/variable';
import styled from 'styled-components';
import { transactions } from '../../ManageWallet';
import DisconnectWallet from '../../ManageWallet/components/DisconnectWallet';
import ManageWalletAddress from '../../ManageWallet/components/ManageWalletAddress';
import ModalTransactionDetail from './components/ModalTransactionDetail';
import Transaction from './components/Transactions';
import { getSmartcontractSuccess, getUserPacks } from '@api/BuyToken';
import { useDispatch, useSelector } from 'react-redux';
import ModalDisconnect from '@page/ManageWallet/components/Modal/ModalDisconnect';
import { useAppSelector } from '@redux/configureStore';
import {
  getManageTokenomicDefaultTab,
  getPaddingWhenHasNotch,
} from '@redux/selector';
import ContentPage from '@components/ContentPage';
import Button from '@components/atoms/Button';
import { format } from 'date-fns';
import TransactionItem from '@page/Transaction/components/TransactionItem';
import {
  setContractDetailHeaderTitle,
  setDefaultTransactionTab,
  setManageTokenomicDefaultTab,
} from '@redux/Global/reducer';
import { capitalizeAllFirstLetter, formatMTDToken } from '@utils/format';
import { toast } from 'react-toastify';

function Manage() {
  // hooks
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // redux
  const walletAddress = useSelector((state) => state.global.walletAddress);
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);
  const manageTokenomicDefaultTab = useAppSelector(getManageTokenomicDefaultTab);

  // state
  const [selectTab, setSelectTab] = useState(manageTokenomicDefaultTab);
  const [showModalDetail, setShowModalDetail] = useState(false);
  const [listTransaction, setListTransaction] = useState(false);
  const [isDisconnect, setisDisconnect] = useState(false);

  // react hooks
  useEffect(() => {
    fetchAllTransaction();
    // getUserPacks(walletAddress).then((res) => {
    //   const data = res.data.userPacks;
    //   const transactionMapping = data.map((el) => {
    //     const obj = {
    //       id: el.ID,
    //       type: 'contract',
    //       time: format(new Date(el.CreatedAt), 'dd/MM/yyyy'),
    //       repayment: `${
    //         el.PackVariant?.Pack?.Round?.description || 'Private sale'
    //       } - ${el.ID}`,
    //       status: el.status === 'cancel' ? 'expired' : el.status,
    //       balance: formatMTDToken(el.PackVariant?.totalVolume),
    //     };

    //     return obj;
    //   });

    //   console.log('transactionMapping -->', transactionMapping);
    //   setListTransaction(transactionMapping);
    // });
  }, []);

  // functions

  const fetchAllTransaction = async () => {
    try {
      const res = await Promise.all([
        getUserPacks(walletAddress),
        getSmartcontractSuccess(walletAddress),
      ]);

      const resContract = res[0].data.userPack;
      let dataContract = [];
      if (resContract !== null) {
        // sort by time
        console.log('resContract--->', resContract);

        resContract.sort(function (a, b) {
          return a.ID - b.ID;
        });
        const r = resContract.reverse();
        console.log('resContract 2222 --->', r);

        dataContract = r.map((el) => {
          const iContract = {
            id: el.ID,
            type: 'contract',
            time: format(new Date(el.CreatedAt), 'dd/MM/yyyy'),
            repayment: `${
              capitalizeAllFirstLetter(el.PackVariant?.Pack?.Round?.description) ||
              'Private Sale'
            } - ${el.ID}`,
            status: el.status === 'cancel' ? 'expired' : el.status,
            balance: el.PackVariant?.Pack.discountPrice * el.numberPackVariant,
          };
          return iContract;
        });
      }

      const resSmcContract = res[1].data.userPack;
      let dataSmcontract = [];
      if (resSmcContract !== null) {
        resSmcContract.sort(function (a, b) {
          return a.ID - b.ID;
        });
        const r = resSmcContract.reverse();
        dataSmcontract = r.map((el) => {
          const iSmc = {
            id: el.ID,
            type: 'smc1',
            time: format(new Date(el.CreatedAt), 'dd/MM/yyyy'),
            repayment: `xsmart${el.ID}`,
            status1: el.status === 'cancel' ? 'expired' : el.status,
            balance: formatMTDToken(el.PackVariant?.totalVolume),
          };
          return iSmc;
        });
      }

      setListTransaction([...dataContract, ...dataSmcontract]);
    } catch (error) {
      toast.error(error);
    }
  };

  const onClose = useCallback(() => {
    setisDisconnect(false);
  }, [isDisconnect]);

  const disConnectWallet = () => {
    setisDisconnect(true);
  };

  return (
    <ContentPage
      childrenButton={
        <WrapButton>
          <Button
            title="view transaction"
            onClick={() => {
              dispatch(setDefaultTransactionTab(1));
              navigate('/transaction');
            }}
          />
        </WrapButton>
      }>
      <Header
        children={'Manage'}
        onBack={() => navigate('/')}
        rightChildren={
          <DisconnectWallet label="Disconnect" onClick={disConnectWallet} />
        }
      />
      <Wrapper style={{ paddingTop: paddingWhenHasNotch.paddingTop + 10 }}>
        <div>
          <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 2}>
            <ManageWalletAddress showRight={false} type="earth" />
          </Animated>
          <WrapperContent>
            <TabContent
              onChangTab={(tab) => {
                dispatch(setManageTokenomicDefaultTab(tab));
                setSelectTab(tab);
              }}
              tabList={tabList}
              selectTab={selectTab}
              delayTime={timeTr3 * 4}
            />
          </WrapperContent>
          {selectTab === 1 && (
            <Animated animationIn="fadeInLeftCustom" animationInDelay={timeTr3 * 5}>
              {listTransaction &&
                listTransaction.map((item, index) => {
                  if (item.type === 'smc1' && item.status1 === 'success') {
                    // if (item.type === 'smc1') {
                    return (
                      <TransactionItem
                        onClick={() => {
                          navigate(`/detail-smart-contract/${item.id}`);
                        }}
                        time={item.time}
                        type={item.type}
                        repayment={item.repayment}
                        status={item.status}
                        balance={item.balance}
                        releaseMonth="smart contract"
                      />
                    );
                  }
                })}
            </Animated>
          )}
          {selectTab === 0 && (
            <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 5}>
              {listTransaction &&
                listTransaction.map((item, index) => {
                  if (item.type === 'contract') {
                    return (
                      <TransactionItem
                        onClick={() => {
                          dispatch(setContractDetailHeaderTitle('Manage'));
                          navigate(`/transaction-detail`, {
                            state: { id: item.id, apiTab: 'contract' },
                          });
                        }}
                        time={item.time}
                        type={item.type}
                        repayment={item.repayment}
                        status={item.status}
                        balance={item.balance}
                      />
                    );
                  }
                })}
            </Animated>
          )}
          <ModalTransactionDetail
            visible={showModalDetail}
            onClose={() => setShowModalDetail(false)}
          />
        </div>
      </Wrapper>

      <ModalDisconnect visible={isDisconnect} onClose={onClose} />
    </ContentPage>
  );
}

export default Manage;

const tabList = [
  {
    id: 0,
    name: 'Contract',
  },
  {
    id: 1,
    name: 'Smart Contract',
  },
];
const Wrapper = styled.div`
  padding: 0 15px;
  overflow: hidden;
  padding-bottom: 100px;
`;
const WrapperContent = styled.div`
  margin-top: 15px;
`;

export const WrapButton = styled.div`
  flex: 1;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
`;
