import React, { useEffect, useState } from 'react';
import styled, { css } from 'styled-components';
import { transactionStatus } from '@assets/icon';
import Button from './atoms/Button';

function DetailTransaction({
  data,
  note,
  showTitle = true,
  onClaim = undefined,
  style = undefined,
}) {
  const [isClaim, setIsClaim] = useState(false);
  const mappingStatusLabel = React.useCallback((status) => {
    switch (status) {
      case 'pending':
        return 'Pending';
      case 'success':
        return 'Success';
      case 'sold-out':
        return 'Sold Out';
      case 'time-out':
        return 'Time Out';
      case 'refund':
        return 'Refund';
      default:
        return '';
    }
  }, []);

  useEffect(() => {
    let isStatus = data.find((item) => {
      return item.name === 'Status';
    });
    if (isStatus && isStatus.status === 'pending') {
      setIsClaim(true);
    }
  }, [data]);
  return (
    <Root style={style}>
      {note && <Note>{note}</Note>}
      {showTitle && <Title>Transaction detail</Title>}
      <WrapTransactionDetail>
        <Content>
          {data.map((item, i) => {
            return (
              <Row key={i}>
                <Left>
                  {item?.customRenderName ? item.customRenderName() : item?.name}
                </Left>
                <Right>
                  {item?.customRenderValue ? (
                    item.customRenderValue()
                  ) : item?.name?.toLowerCase() === 'status' ? (
                    <Status status={item.status}>
                      <Image src={transactionStatus[item.status]} />
                      {mappingStatusLabel(item.status)}
                    </Status>
                  ) : (
                    item?.value
                  )}
                </Right>
              </Row>
            );
          })}
        </Content>
        {onClaim && (
          <Button
            disable={isClaim}
            title="Claim"
            variant="container"
            style={{ margin: '34px 0 24px' }}
            onClick={() => onClaim && onClaim()}></Button>
        )}
      </WrapTransactionDetail>
    </Root>
  );
}

export default DetailTransaction;

const Root = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`;

const Title = styled.div``;

const Note = styled.div`
  color: var(--color-accent-blue-1);
  font-weight: 500;
  font-size: 17px;
  line-height: 24px;

  & + ${Title} {
    margin-top: var(--space-3);
  }
`;

const WrapTransactionDetail = styled.div``;

const Content = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  border: 1px solid #edeef0;
  border-radius: 16px;
  padding: var(--space-2) var(--space-3);
  margin-top: var(--space-2);
`;

const Row = styled.div`
  ${({ noPad }) =>
    css`
      padding: ${noPad ? '0' : 'var(--space-2) 0'};
    `}
  /* margin-top: var(--space-1); */
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #edeef0;
  :last-child {
    border-bottom: none;
  }
`;

const Left = styled.p`
  height: 100%;
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 15px;
  line-height: 21px;
  white-space: nowrap;
  color: var(--color-des);
  margin-right: var(--space-0);
`;

const Right = styled.div`
  height: 100%;
  max-width: 192px;
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 15px;
  line-height: 21px;
  display: flex;
  justify-content: flex-end;
  color: var(--color-title);
`;
const Image = styled.img`
  margin-right: 6px;
`;
const Status = styled.div`
  width: 100px;
  height: 100%;
  min-height: 40px;
  display: flex;
  font-weight: 500;
  ${({ status }) => {
    switch (status) {
      case 'pending':
        return css`
          background-color: var(--color-semantic-orange-3);
          color: var(--color-semantic-orange-1);
        `;
      case 'success':
        return css`
          background-color: var(--color-semantic-green-3);
          color: var(--color-semantic-green-1);
        `;
      case 'sold-out':
      case 'time-out':
        return css`
          background-color: var(--color-semantic-pink-3);
          color: var(--color-semantic-pink-1);
        `;
      case 'refund':
        return css`
          background-color: var(--color-semantic-blue-3);
          color: var(--color-semantic-blue-1);
        `;
      default:
        return '';
    }
  }}
  justify-content: center;
  align-items: center;
`;
