import React from 'react';
import styled, { css } from 'styled-components';

const Table = ({ columns, data, bg, title = null }) => {
  return (
    <Root>
      <StyledTable bg={bg}>
        <TableHead>
          {title && (
            <TableRow
              style={{
                textAlign: 'center',
                justifyContent: 'center',
                padding: '10px 0',
              }}>
              {title}
            </TableRow>
          )}
          <TableRow isHead bg={bg}>
            {columns.map((col) => (
              <Cell minWidth={col.minWidth} key={col.id}>
                {col.name}
              </Cell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {data.map((row, index) => (
            <TableRow key={index.toString()}>
              {columns.map((col) => (
                <Cell minWidth={col.minWidth} key={`${col.id}-${index}`}>
                  {col.renderCell(row)}
                </Cell>
              ))}
            </TableRow>
          ))}
        </TableBody>
      </StyledTable>
    </Root>
  );
};

export default Table;

const Root = styled.div``;

const Cell = styled.td`
  display: flex;
  justify-content: ${(props) => props.justifyContent || 'center'};
  align-items: center;
  flex: 1;

  font-weight: 400;
  font-size: 11px;
  line-height: 13px;

  min-width: ${(props) => props.minWidth || '100px'};

  padding: var(--space-3) var(--space-2);

  text-transform: ${(props) => props.textCapitalize || 'normal'};
  white-space: nowrap;
  width: ${(props) => props.width || 'auto'};
  & + & {
    border-left: 1px solid #edeef0;
  }
  :first-child {
    min-width: 40px;
    max-width: 40px;
    overflow: hidden;
  }
`;

const TableRow = styled.tr`
  display: flex;
  min-width: 100%;

  ${({ bg, isHead }) => {
    switch (isHead) {
      case true:
        return css`
          position: relative;
          font-weight: 500;
          font-size: 12px;
          &::before {
            position: absolute;
            content: '';
            width: 100%;
            height: ${bg === 'black' ? '1px' : '2px'};
            top: 100%;
            left: 0;
            transform: translateY(-100%);
            background: ${bg === 'black'
              ? '#edeef0'
              : 'var(--color-gradient-blue-1)'};
          }

          ${Cell} {
            color: ${bg === 'black' ? '#04004D' : 'var(--color-accent-blue-1)'};
          }
        `;
      default:
        break;
    }
  }}
`;

const TableHead = styled.thead``;

const TableBody = styled.tbody``;

const StyledTable = styled.table`
  min-width: 335px;
  width: 100%;
  flex-direction: column;
  border: 1px solid #edeef0;
  border-radius: 10px;
  overflow: auto;
  color: ${(props) => (props.bg === 'black' ? '#667386' : '')};
  ${TableHead}, ${TableBody} {
    ${TableRow}:not(:first-of-type) {
      ${Cell} {
        border-top: 1px solid #edeef0;
      }
    }
  }
`;

const ResponsiveTable = styled.div``;
