import React from 'react';
import Button from '@components/atoms/Button';
import IconStatus from '@components/atoms/IconStatus';
import ModalBase from '@components/atoms/ModalBase';
import FlexBox from '@components/FlexBox';
import Text from '@components/Text';
import Thumb from '@components/Thumb';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import { SystemCore } from '@core';
import { useAppDispatch } from '@redux/configureStore';
import { setIsConnect, setWalletAddress } from '@redux/Global/reducer';

function ModalDisconnect({ visible, onClose, type }) {
  const [isBackup, setIsBackup] = React.useState(false);
  const navigate = useNavigate();
  const dispatch = useAppDispatch();

  const onDisconnect = () => {
    console.log('on disconnect start');
    SystemCore.send({
      command: 'disconnect-wallet',
    })
      .then((res) => {
        localStorage.setItem('mtdAddress', '');
        dispatch(setIsConnect(true));
        dispatch(setWalletAddress(''));
        console.log('disconnect success', res);
      })
      .catch((err) => {
        console.log('disconnect  error', err);
      });

    // onClose && onClose();
  };
  const onBackup = () => {
    setIsBackup(false);
    onClose && onClose();
    navigate('/');
  };
  const _onClose = () => {
    setIsBackup(false);
    onClose && onClose();
  };

  return (
    <ModalBase visible={visible} onClose={() => onClose?.()}>
      <Root>
        <Thumb />
        {isBackup ? (
          <Backup onBackup={onBackup} onClose={_onClose} />
        ) : (
          <Disconnect onDisconnect={onDisconnect} onClose={_onClose} />
        )}
      </Root>
    </ModalBase>
  );
}

const Disconnect = ({ onDisconnect, onClose }) => {
  return (
    <>
      <WrapImg>
        <IconStatus type={'error'} imageIcon={icon} />
      </WrapImg>
      <FlexBox flexDirection={'column'} alignItem={'center'} gap={'6px'}>
        <Header>{'Disconnect'}</Header>
        <Text
          fontSize={'15px'}
          lineHeight={'21px'}
          fontWeight={400}
          textAlign={'center'}>
          {'Are you sure you wanna disconnect the wallet?'}
        </Text>
      </FlexBox>
      <WrapButton>
        <FlexBox gap={'var(--space-3)'}>
          <Button
            variant="outlined"
            title={'NO'}
            borderColor={'var(--color-neutral-5)'}
            onClick={onClose}
          />
          <Button
            variant="container"
            title={'disconnect'}
            type={'error'}
            onClick={onDisconnect}
          />
        </FlexBox>
      </WrapButton>
    </>
  );
};

const Backup = ({ onBackup, onClose }) => {
  return (
    <>
      <WrapImg>
        <IconStatus type={'file'} />
      </WrapImg>
      <FlexBox flexDirection={'column'} alignItem={'center'} gap={'6px'}>
        <Header>{'Backup File'}</Header>
        <Text
          fontSize={'15px'}
          lineHeight={'21px'}
          fontWeight={400}
          textAlign={'center'}>
          {'Please backup the file, otherwise you will lose your current wallet.'}
        </Text>
      </FlexBox>
      <WrapButton>
        <FlexBox gap={'var(--space-3)'}>
          <Button
            variant="outlined"
            title={'NO, THANKS'}
            borderColor={'var(--color-neutral-5)'}
            onClick={onClose}
          />
          <Button
            variant="container"
            title={'backup'}
            type={'default'}
            onClick={onBackup}
          />
        </FlexBox>
      </WrapButton>
    </>
  );
};

export default ModalDisconnect;

const Root = styled.div``;

const Header = styled.p`
  width: 100%;
  font-weight: 700;
  font-size: 22px;
  line-height: 30px;
  text-align: center;
  color: var(--color-title);
  margin-top: var(--space-2);
`;

const WrapImg = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 30px;
`;

const WrapButton = styled.div`
  margin-top: var(--space-5);
  padding-bottom: 20px;
  span:first-child {
    background: unset;
    font-weight: 400;
    font-size: 17px;
    line-height: 100%;

    color: var(--color-neutral-4);
    -webkit-text-fill-color: unset;
  }
`;

const WrapText = styled.div``;

const icon = `data:image/svg+xml;base64,`;
