import { EventEmitter } from './EmmitEvent';

const receiveData = {
  'connect-wallet': -1,
  'excute-smart-contract': -1,
  'get-setting-d-app': -1,
  'disconnect-wallet': -1,
  'exit-app': -1,
  read: -1,
  write: -1,
};

export class CoreController extends EventEmitter {
  public static callbackFunction: any;

  constructor() {
    super();
    if (
      (window as any) &&
      (window as any).webkit &&
      (window as any).webkit.messageHandlers
    ) {
    }
    this._subscribe();
  }

  async send(payload = {}) {
    if ((window as any).webkit && (window as any).webkit.messageHandlers) {
      const res = await this.sendMessageToFlutter(payload);
      if (res == null) {
        return;
      }
      const command = res.command;
      const response = res.data;
      receiveData[command] = -1;

      if (typeof response == 'undefined') {
        throw `Command not response - ${JSON.stringify(res)}`;
      }
      if (response.success !== true) {
        throw response.message;
      }

      this.emit(command, res.data);
      return res.data;
    }
    return null;
  }

  async sendMessageToFlutter(message: { command?: any }) {
    if (typeof receiveData[message.command] != 'undefined') {
      receiveData[message.command] = 0;
    }
    //post msg
    if (
      (window as any).webkit &&
      (window as any).webkit.messageHandlers &&
      (window as any).webkit.messageHandlers.callbackHandler
    ) {
      (window as any).webkit.messageHandlers.callbackHandler.postMessage(
        JSON.stringify(message),
      );
    } else {
      throw 'not found window.webkit';
    }

    if (typeof receiveData[message.command] === 'undefined') {
      return;
    }

    // sleep
    await this.waitUntil(message.command);

    return receiveData[message.command];
  }

  async waitUntil(command: string | number) {
    return await new Promise<void>((resolve) => {
      const interval = setInterval(() => {
        if (typeof receiveData[command] != 'number') {
          resolve();
          clearInterval(interval);
        }
      }, 1000);
    });
  }

  _subscribe() {
    window.addEventListener('message', (ev) => {
      const { data } = ev;
      if (data.platform == 'ios') {
        if (data.isSocket !== true) {
          return (receiveData[data.command] = data.data);
        }
        this.emit(data.command, data.data);
        return;
      }
      this.emit(data.command, data.data);
      return data;
    });
  }
}

const systemCore = new CoreController();

export { systemCore };
