import React, { memo, useEffect, useState } from 'react';
import * as NumberFormat from 'react-number-format';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faPen,
  faPaste,
  faCircleExclamation,
} from '@fortawesome/free-solid-svg-icons';
import { Input, Root, WrapIcon } from './styled';
import styled from 'styled-components';
import { SystemCore } from '@core';
import { handelFormatDataCopyListSeedPhrase } from '@utils/format';
import { Animated } from 'react-animated-css';
import { timeTr3 } from '@utils/variable';
import SaveToClipboard from '@components/Modal/SaveToClipboard';
import IconPasted from '@assets/icon/ico-pasted.svg';
import { toast } from 'react-toastify';

function InputText({
  // onPast,
  placeholder,
  value = '',
  onChange,
  onBlur,
  refInput,
  disabled = false,
  type = 'text',
  copyAble = false,
  pasteAble = false,
  isIcon = true,
  id,
  height,
  boxShadow = true,
  error,
  rightIcon,
  borderRadius,
  style = {},
}) {
  const [valueInput, setValueInput] = useState('');
  const [copied, setCopied] = useState(false);
  const [pasted, setPasted] = useState(false);
  const handleCopy = () => {
    console.log('starting copy -->', valueInput);

    SystemCore.send({
      command: 'copy-clipboard',
      value: valueInput,
    })
      .then((res) => {
        if (res) {
          setCopied(true);

          setTimeout(() => {
            setCopied(false);
          }, 2000);
        }
      })
      .catch((err) => {
        console.log('err copy -->', err);
        toast.error('Copy fail !');
      });
  };

  useEffect(() => {
    setValueInput(value);
  }, [value]);

  const handleOnChange = (e) => {
    setValueInput(e);
    onChange?.(e);
  };

  const handlePaste = () => {
    SystemCore.send({
      command: 'get-from-clipboard',
    })
      .then((res) => {
        if (res) {
          if (res.data) {
            setPasted(true);
            onChange?.(res.data);
            setTimeout(() => {
              setPasted(false);
            }, 2000);
          }
        }
      })
      .catch((err) => {
        toast.error('Paste fail !');
      });
  };
  return (
    <>
      <Root error={error} className="input-text">
        {type === 'text' && (
          <Input
            ref={refInput}
            placeholder={placeholder}
            value={value}
            onChange={(e) => {
              handleOnChange(e?.target?.value);
            }}
            onBlur={(e) => {
              onBlur?.(e?.target?.value);
            }}
            disabled={disabled}
            copyAble={copyAble || pasteAble}
            isIcon={isIcon}
            height={height}
            id={id}
            boxShadow={boxShadow}
            error={error}
            borderRadius={borderRadius}
            style={style}
          />
        )}
        {type === 'email' && (
          <Input
            ref={refInput}
            placeholder={placeholder}
            value={value}
            onChange={(e) => {
              handleOnChange(e?.target?.value);
            }}
            onBlur={(e) => {
              onBlur?.(e?.target?.value);
            }}
            disabled={disabled}
            copyAble={copyAble || pasteAble}
            isIcon={isIcon}
            height={height}
            id={id}
            boxShadow={boxShadow}
            error={error}
            borderRadius={borderRadius}
            type="email"
            style={style}
          />
        )}
        {type === 'money' && (
          <NumberFormat
            value={value}
            thousandSeparator=","
            decimalSeparator="."
            prefix=""
            placeholder={placeholder}
            inputmode="numeric"
            onValueChange={(data, name) => {
              handleOnChange(data.value);
            }}
            onBlur={(e) => {
              onBlur?.(e?.target?.value);
            }}
            style={{
              width: '100%',
              height: '46px',
              border: '1px solid #edeef0',
              borderRadius: '6px',
              padding: '0 8px 0 38px',
              fontSize: 'var(--font-size-title)',
              color: 'var(--color-title)',
              boxShadow: boxShadow ? '0px 2px 20px #00000012' : 'none',
              ...style,
            }}
          />
          // <CurrencyInput
          //   placeholder={placeholder}
          //   value={valueInput}
          //   // defaultValue={0}
          //   decimalsLimit={18}
          //   decimalSeparator="."
          //   groupSeparator=","
          //   allowNegativeValue={false}
          //   // intlConfig={{ locale: 'vi-VN', currency: 'VND' }}
          //   onValueChange={(v, name) => {
          //     handleOnChange(v);
          //   }}
          //   onBlur={(e) => {
          //     onBlur?.(e?.target?.value);
          //   }}
          //   style={{
          //     width: '100%',
          //     height: '46px',
          //     border: '1px solid #edeef0',
          //     borderRadius: '6px',
          //     padding: '0 8px 0 38px',
          //     fontSize: 'var(--font-size-title)',
          //     color: 'var(--color-title)',
          //     boxShadow: '0px 2px 20px rgb(0 0 0 / 7%)',
          //   }}
          // />
        )}
        {isIcon && (
          <WrapIcon>
            <FontAwesomeIcon
              icon={faPen}
              style={{ color: error ? '#ff0366' : '' }}
            />
          </WrapIcon>
        )}
        {copyAble && (
          <WrapIcon pos="end" onClick={handleCopy}>
            <FontAwesomeIcon icon={faPaste} color={''} />
            {copied && (
              <Animated
                animationIn="fadeInRightCustom"
                animationInDelay={timeTr3 * 15}>
                <div className="copyted">Copied</div>
              </Animated>
            )}
          </WrapIcon>
        )}
        {pasteAble && (
          <WrapIcon pos="end" onClick={handlePaste}>
            <img src={IconPasted} className="icon" style={{ fill: '#04B800' }} />
            {pasted && (
              <Animated
                animationIn="fadeInRightCustom"
                animationInDelay={timeTr3 * 15}>
                <div className="copyted">Pasted</div>
              </Animated>
            )}
          </WrapIcon>
        )}
        {rightIcon && <WrapIcon pos="end">{rightIcon}</WrapIcon>}
      </Root>
      {!!error && (
        <Error>
          {typeof error === 'string' ? (
            <FontAwesomeIcon icon={faCircleExclamation} />
          ) : (
            ''
          )}
          {error}
        </Error>
      )}
      {/* {!!copied && (
        <SaveToClipboard onClose={() => setCopied(false)} value={valueInput} />
      )} */}
    </>
  );
}

export default memo(InputText);
export const Error = styled.div`
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 13px;
  line-height: 18px;
  color: #ff0366;
  margin-top: 4px;
  position: relative;
  padding-left: 20px;
  text-align: justify;

  svg {
    position: absolute;
    top: 4px;
    left: 0;
    margin-right: 8px;
  }
`;
