import { faEllipsis } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useRef, useState } from 'react';
import { Animated } from 'react-animated-css';
import TabList from '@components/TabList';
import styled from 'styled-components';
import BlockNoti from './components/BlockNoti';
import Transaction from './components/DetailTransaction';
import Modal from './components/Modal';
import { motion } from 'framer-motion';
import { timeTr2, timeTr3 } from '@utils/variable';
import Container from '@components/Container';
import Header from '@components/Header';
import { useNavigate } from 'react-router-dom';
import { useAppSelector } from '@redux/configureStore';
import { getDefaultNoticeTab, getPaddingWhenHasNotch } from '@redux/selector';
import { dataNoticeClone, dataNoticeContract, dataNoticeSmc } from './dataNotiClone';
import IconNoticeFilter from '@assets/icon/notice/iconnoti.svg';

import TabContent from '@components/TabContent';
import {
  setContractDetailHeaderTitle,
  setDefaultNoticeTab,
} from '@redux/Global/reducer';
import { useDispatch } from 'react-redux';
import HeaderContants from '@constants/headerConstant';

const transition = {
  layout: {
    duration: 0.7,
    ease: 'linear',
  },
};

function Notification() {
  // hooks
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // redux
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);
  const defaultNoticeTab = useAppSelector(getDefaultNoticeTab);

  // states
  const [listNotice, setListNotice] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [selectTab, setSelectTab] = useState(defaultNoticeTab);

  // react hooks
  useEffect(() => {
    console.log('mounted', selectTab);
    console.log('selectTab');

    setListSmcByTabId(selectTab);
    return () => {
      console.log('unmounted');
    };
  }, []);

  // functions
  const handleDeleteItem = (id) => {
    setListNotice((prev) =>
      prev.filter((item) => {
        return item.id !== id;
      }),
    );
  };

  const handleChangeTab = (tab) => {
    setSelectTab(tab);
    dispatch(setDefaultNoticeTab(tab));
    setListSmcByTabId(tab);
  };

  const setListSmcByTabId = (tab) => {
    switch (tab) {
      case 0:
        setListNotice(dataNoticeClone);
        break;
      case 1:
        setListNotice(dataNoticeSmc);
        break;
      case 2:
        setListNotice(dataNoticeContract);
        break;

      default:
        break;
    }
  };

  const handleReadAll = () => {
    console.log('handleReadAll --->');
    const currentListNotice = [...listNotice];
    const tmpList = currentListNotice.map((el) => {
      if (el.haveNotReaded) {
        el.haveNotReaded = false;
      }

      return el;
    });

    setListNotice(tmpList);
    setShowModal(false);
  };
  const handleClearAll = () => {
    console.log('handleClearAll --->');

    setListNotice([]);
    setShowModal(false);
  };

  const onClickDetail = (item) => {
    // dispatch(setContractDetailHeaderTitle('Notification'));
    // navigate(`/transaction-detail/${item.idMapping}`);
  };

  return (
    <div style={paddingWhenHasNotch}>
      <Container>
        <Header
          children="Notification"
          onBack={() => navigate(-1)}
          rightChildren={
            <img src={IconNoticeFilter} onClick={() => setShowModal(true)} />
          }
        />
        <WrapTabContent>
          <TabContent
            onChangTab={(tab) => handleChangeTab(tab)}
            tabList={tabList}
            selectTab={selectTab}
            delayTime={timeTr3 * 4}
          />
        </WrapTabContent>

        <ListNotification>
          {listNotice?.map((item, i) => (
            <Animated
              key={`${item.id}-${i}`}
              animationIn="fadeInLeftCustom"
              animationInDelay={timeTr3 * 5}>
              <BlockNoti
                item={item}
                onClickDetail={() => onClickDetail(item)}
                handleDeleteItem={handleDeleteItem}
                isLastItem={i === listNotice.length - 1}
              />
            </Animated>
          ))}
        </ListNotification>

        <Modal
          visible={showModal}
          onClose={() => setShowModal(false)}
          onClearAll={handleClearAll}
          onReadAll={handleReadAll}
        />
      </Container>
    </div>
  );
}

export default Notification;

const tabList = [
  {
    id: 0,
    name: 'Order',
  },
  {
    id: 1,
    name: 'Smart Contract',
  },
  {
    id: 2,
    name: 'Contract',
  },
];

const ListNotification = styled.div`
  margin-top: 8px;
  width: 100%;
`;

const WrapTabContent = styled.div`
  margin-top: 16px;
`;
