import { fetchRoundOpen } from '@api/RoundApi';
import { setRoundOpen } from '@redux/Token/action';
import { timeTr3 } from '@utils/variable';
import React, { useEffect, useState } from 'react';
import { Animated } from 'react-animated-css';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import BottomSheet from '../BottomSheet';
import Content from './Content';
import TimeLine from './Timeline';

const Overview = ({ active = false, onClick, tab }) => {
  const [round, setRound] = useState({});
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    fetchRoundOpen()
      .then((res) => {
        const data = res.data[0];
        setRound(data);
        setRoundOpen(dispatch, { data });
      })
      .catch((err) => {
        console.log('err --> ', err);
      });
  }, []);

  return (
    <Root>
      <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 3}>
        <TimeLine />
      </Animated>
      <div className="pb-4">
        <Content isList contents={contents} delayTime={timeTr3 * 4} />
      </div>
    </Root>
  );
};

export default Overview;

const Root = styled.div`
  /* padding-bottom: 210px; */
`;

