import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMinus, faPlus } from '@fortawesome/free-solid-svg-icons';
import { Animated } from 'react-animated-css';
import { timeTr3 } from '@utils/variable';

const ControlCount = ({
  typeNumberCount = 0,
  setTypeNumberCount,
  HandleCount,
  valueCoin,
  setToggleType,
  toggleType,
  delayTime,
  isOpen,
}) => {
  const [isShowMinus, setIsShowMinus] = useState(false);
  useEffect(() => {
    if (!valueCoin || valueCoin === 0) return;
    if (valueCoin >= 1 && valueCoin.toString().includes('.')) return;
    setToggleType(valueCoin < 1);
  }, [valueCoin]);

  useEffect(() => {
    if (toggleType) {
      if (!valueCoin) return;
      const v = parseInt(
        valueCoin.toString().slice(valueCoin.toString().indexOf('.') + 1),
      );
      if (valueCoin >= 1 && v >= typeNumberCount) {
        setIsShowMinus(false);
        return;
      }
      setIsShowMinus(v <= typeNumberCount);
    } else {
      setIsShowMinus(valueCoin <= typeNumberCount);
    }
  }, [valueCoin, toggleType, typeNumberCount]);

  const handleChangeNumberCount = (value) => {
    setTypeNumberCount(value);
  };

  const HandleCountMinus = () => {
    if (isShowMinus) return;
    HandleCount('minus');
  };
  return (
    <Wrapper>
      <Animated
        animationIn="fadeInRightCustom"
        animationInDelay={delayTime + timeTr3}>
        <Block
          onClick={() => isOpen && handleChangeNumberCount(1)}
          className={typeNumberCount === 1 && isOpen && 'active'}>
          1
        </Block>
      </Animated>
      <Animated
        animationIn="fadeInRightCustom"
        animationInDelay={delayTime + timeTr3 * 2}>
        <Block
          onClick={() => isOpen && handleChangeNumberCount(10)}
          className={typeNumberCount === 10 && isOpen && 'active'}>
          10
        </Block>
      </Animated>
      <Animated
        animationIn="fadeInRightCustom"
        animationInDelay={delayTime + timeTr3 * 3}>
        <Block
          onClick={() => isOpen && handleChangeNumberCount(100)}
          className={typeNumberCount === 100 && isOpen && 'active'}>
          100
        </Block>
      </Animated>
      <Animated
        animationIn="fadeInRightCustom"
        animationInDelay={delayTime + timeTr3 * 4}>
        <Control>
          <span
            className={isOpen && !isShowMinus && 'active'}
            onClick={HandleCountMinus}>
            <FontAwesomeIcon icon={faMinus} />
          </span>
          <span className={isOpen && 'active'} onClick={() => HandleCount('plus')}>
            <FontAwesomeIcon icon={faPlus} />
          </span>
        </Control>
      </Animated>
    </Wrapper>
  );
};

export default ControlCount;

const Wrapper = styled.div`
  display: flex;
  margin-top: 10px;
  justify-content: space-between;
  align-items: center;
`;
const Block = styled.div`
  padding: 0px 18px;
  border-radius: 6px;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 21px;
  color: white;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #edeef0;
  &.active {
    background: #004dff;
  }
`;
const Control = styled.div`
  width: 101px;
  height: 48px;
  box-shadow: 0px 7px 20px 0px #154fd41a;
  border-radius: 8px;
  line-height: 15px;
  font-size: 15px;
  display: flex;
  font-weight: bold;
  span {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #d3d7db;
    &.active {
      color: #004dff;
    }
  }
`;
