import React from 'react';
import { useSelector } from 'react-redux';
import { heightNotch } from '@utils/variable';
import styled from 'styled-components';

function Container({ children, fluid, style }) {
  const { isShowHeightNotch } = useSelector((state) => state.settingReducer);
  return (
    <Wrapper style={style} fluid={fluid} isShowHeightNotch={isShowHeightNotch}>
      {children}
    </Wrapper>
  );
}

export default Container;
export const Wrapper = styled.div`
  display: flex;
  width: 100%;
  /* min-height: 100vh; */
  flex-direction: column;
  padding: ${(props) => (props.fluid ? '' : '16px')};
  box-sizing: border-box;
  background: #fdfdfd;
  position: relative;
  overflow: hidden;
  padding-top: ${({ isShowHeightNotch }) => (isShowHeightNotch ? heightNotch : 0)}px;
  .wrap-tab {
    max-width: 300px;
    width: 300px;
    margin: 10px auto 5px;
  }
`;
