import React, { useEffect, useMemo, useState } from 'react';
import styled from 'styled-components';
import ScrollViewer from '../../components/ScrollViewer';
import Allocation from './components/Allocation';
import Benefit from './components/Benefit';
import CapitalPlan from './components/CapitalPlan';
import Distribution from './components/Distribution';
import Header from './components/Header';
import Overview from './components/Overview';
import Round from './components/Round';
import MainTab from './components/Tab/MainTab';
import { useLocation, useNavigate } from 'react-router-dom';
import { setIsConnect, setWalletAddress } from '@redux/Global/reducer';
import { useDispatch, useSelector } from 'react-redux';
import BottomSheet from './components/BottomSheet';
import { fetchRoundOpen, fetchRoundOpenById } from '@api/RoundApi';
import { setRoundOpen } from '@redux/Token/action';
import { timeTr3 } from '@utils/variable';
import { getPaddingWhenHasNotch } from '@redux/selector';
import { useAppSelector } from '@redux/configureStore';

import CheckConditionBuytoken from '@components/CheckConditionBuytoken';
import ContentPage from '@components/ContentPage';

let flag = true;

function TokenomicApp(props) {
  const dispatch = useDispatch();
  const location = useLocation();
  const navigate = useNavigate();
  const [round, setRound] = useState({});
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);
  const [timeEnd, setTimeEnd] = useState(new Date());
  const [timeCountDown, setTimeCountDown] = useState(new Date());
  const [type, setType] = useState('count');
  const [showModalCheckCondititon, setShowModalCheckCondititon] = useState(false);
  const walletAddress = useSelector((state) => state.global.walletAddress);
  const [tab, setTab] = React.useState('ov');

  const tabDefault = React.useMemo(() => {
    return location?.state?.currentTab || 'ov';
  }, [location?.state?.currentTab]);

  useEffect(() => {
    if (tabDefault === 'be') {
      setTab('be');
    }
    if (walletAddress && tabDefault !== 'be') {
      setTab('ro');
    }
    if (!walletAddress && tabDefault !== 'be') {
      setTab('ov');
    }
  }, [walletAddress]);

  const onConnect = React.useCallback(() => {
    if (walletAddress) {
      navigate('/manage-tokenomic');
    } else {
      dispatch(setIsConnect(true));
    }
  }, []);

  const onBuyNow = () => {
    if (walletAddress) {
      setShowModalCheckCondititon(true);
    } else {
      dispatch(setIsConnect(true));
    }
  };

  useEffect(() => {
    fetchRoundOpenById(4)
      .then((res) => {
        const data = res.data;

        let time = data.end;
        setTimeEnd(new Date(time).getTime());

        setRound(data);
        setRoundOpen(dispatch, { data });
      })
      .catch((err) => {
        console.log('err --> ', err);
      });
  }, []);

  const getRoundStatus = useMemo(() => {
    let roundStatus = 'PENDING';
    switch (round?.status) {
      case 'pre_order':
        roundStatus = 'PREORDER';
        break;
      case 'open':
        roundStatus = 'OPEN';
        break;
      case 'close':
        roundStatus = 'CLOSE';
        break;
      case 'sold_out':
        roundStatus = 'SOLD OUT';
        break;
      default:
        break;
    }
    return roundStatus;
  }, [round]);

  useEffect(() => {
    // if (flag) {
    //   let time = new Date().getTime() + 5000;
    //   setTimeEnd(time);
    //   flag = false;
    // } else {
    //   setType('buy now');
    // }
  }, []);

  const equalTime = () => {
    let result = isEqual(new Date(), new Date(timeEnd));
    return result;
  };

  const handleTimeOut = () => {
    // setType('buy now');
    let time = new Date().getTime() + 24 * 60 * 60 * 1000 * 30;
    setTimeEnd(time);
  };

  return (
    <ContentPage
      bottomSheet={
        <BottomSheet
          title="Private sale"
          type={type}
          buttonTitle={getRoundStatus}
          height={195}
          round={round}
          delayTime={timeTr3 * 10}
          onClick={onBuyNow}
          timeEnd={timeEnd}
          onTimeOut={handleTimeOut}
        />
      }>
      <div style={paddingWhenHasNotch}>
        <Sticky style={paddingWhenHasNotch}>
          <Header onConnect={onConnect} />
        </Sticky>
        <div style={{ padding: '0 20px' }}>
          <MainTab tabs={tabs} currentTab={tab} onChangeTab={setTab} />
        </div>

        <WrapContainer style={tab === 'al' ? { margin: '0 -20px' } : {}}>
          <ScrollViewer>
            {/* <Container> */}
            {tab === 'ov' && (
              <>
                <div style={{ padding: '0 20px' }}>
                  <Overview />
                </div>
              </>
            )}
            {tab === 'al' && (
              <>
                <div style={{ padding: '0 20px' }}>
                  <Allocation />
                </div>
              </>
            )}
            {tab === 'ro' && (
              <>
                <Round />
              </>
            )}
            {tab === 'be' && (
              <>
                <div style={{ padding: '0 20px 20px' }}>
                  <Benefit />
                </div>
              </>
            )}
            {tab === 'cp' && (
              <>
                <div style={{ padding: '0 20px' }}>
                  <CapitalPlan />
                </div>
              </>
            )}
            {tab === 'di' && (
              <>
                <Distribution />
              </>
            )}
            {/* </Container> */}
          </ScrollViewer>

          {showModalCheckCondititon && (
            <CheckConditionBuytoken
              round={round}
              onClose={() => setShowModalCheckCondititon(false)}
              onCheckConditionsSuccess={() => navigate(`/buy-token/${round.ID}`)}
            />
          )}
        </WrapContainer>
      </div>
    </ContentPage>
  );
}

export default TokenomicApp;

const WrapContainer = styled.div`
  flex: 1;
  display: flex;
  overflow: hidden;
  /* min-height: calc(100vh - 136px); */
  /* padding-bottom: 210px; */
  padding-bottom: 24px;
`;

const Sticky = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  background-color: #fff;
  padding: 0 16px;
  padding-top: 44px;
`;

const tabs = [
  { label: 'Overview', value: 'ov' },
  { label: 'Allocation', value: 'al' },
  { label: 'Round', value: 'ro' },
  { label: 'Benefit', value: 'be' },
  { label: 'Capital Plan', value: 'cp' },
  { label: 'Distribution', value: 'di' },
];
