import React from 'react';
import styled, { keyframes } from 'styled-components';
import QRCode from 'qrcode.react';

function QrCode({ value = '', size = 150, level = 'H', height = 206 }) {
  return (
    <WrapQRCode className="QrCode-styled" height={height}>
      <Blur height={height} />
      <QRCodeContainer height={height}>
        <QRCode id="qrcode" value={value} size={size} level={level} />
      </QRCodeContainer>
      <Background height={height} />
    </WrapQRCode>
  );
}

export default QrCode;

const WrapQRCode = styled.div`
  height: ${(props) => props.height + 'px' || '300px'};
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
`;

const Blur = styled.div`
  width: ${(props) => props.height + 'px' || '260px'};
  height: ${(props) => props.height - 40 + 'px' || '130px'};
  top: ${(props) => props.height / 7 + 'px' || '150px'};
  background: linear-gradient(
    79.72deg,
    #66b5f8 9.67%,
    #3e78fd 50.77%,
    #554cff 88.89%
  );
  filter: blur(40px);
  position: absolute;
`;
const QRCodeContainer = styled.div`
  width: ${(props) => props.height - 6 + 'px' || '260px'};
  height: ${(props) => props.height - 6 + 'px' || '260px'};
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  position: absolute;
  border-radius: 16px;
  z-index: 10;
`;

const rotate = keyframes`
  0%{
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);

  }
`;

const Background = styled.div`
  width: ${(props) => props.height + 'px' || '270px'};
  height: ${(props) => props.height + 'px' || ' 270px'};
  position: absolute;
  overflow: hidden;
  background-image: linear-gradient(#5ddcff, #3c67e3 43%, #4e00c2);
  border-radius: 20px;
  z-index: 8;

  ::before {
    position: absolute;
    content: '';
    width: ${(props) => props.height * 1.5 + 'px' || '400px'};
    height: ${(props) => props.height * 1.5 + 'px' || '400px'};
    top: ${(props) => '-' + props.height / 5 + 'px' || '-60px'};
    left: ${(props) => '-' + props.height / 5 + 'px' || '-60px'};
    margin: auto;
    background-image: linear-gradient(#5ddcff, #3c67e3 43%, #4e00c2);
    animation: ${rotate} 4s linear infinite;
    z-index: 9;
  }
`;
