import { numberFormatShowMTD, numberWithCommas } from '@utils/currencyFormat';
import {
  formatDepositBalance,
  formatMTDToken,
  hexToDecimal,
  formatReleaseToken,
} from '@utils/format';
import { format } from 'date-fns';
import {
  contractDetail,
  contractDetailWithStatus,
  contractSmcDetail,
  describeAndTime,
  notice,
  paymentWithBalane,
  prepaymentBlock,
  releaseAndRevoked,
  repaymentBlock,
  transactionWithFree,
  transtionDeposit1,
  transtionDepositReview,
  transtionWithStatus1,
  transtionWithStatus2,
  transtionWithStatus3,
  userInfo,
} from './transactionBlockData';

const renderDepositItem = (data) => {
  const { type, status, userPackId, time, from, dueTime } = data;
  const toSmc = data.to;
  const depositVol = data.transferVolume.toString();
  const depositStr = depositVol.slice(0, -6);
  const deposit = `${numberWithCommas(depositStr, ',')}$`;
  const debt = `-${numberWithCommas(depositStr, ',')}$`;
  const balance = `+${depositStr}$`;
  const transactionData = {
    dueTime,
    prePayment: formatDepositBalance(depositVol),
    address: 'TQbpvRQHgkhDNiRtwqjH8DCXWwwPwMDQSa',
  };
  let dataItem = [];
  if (status === 'pending') {
    dataItem = [transtionDepositReview(toSmc, deposit, debt, balance)];
  }
  if (status === 'expired' || status === 'success') {
    dataItem = [
      transtionWithStatus1(
        status,
        'Deposit',
        userPackId,
        format(new Date(time), 'dd MMM yyyy, hh:mm'),
      ),
      transtionDeposit1(from, toSmc, deposit, debt, balance),
      notice(data.UserPack.note),
    ];
  }

  const result = {
    type: type,
    status: status,
    data: dataItem,
    transactionData: transactionData,
    isReview: status === 'pending',
  };

  return result;
};

const renderRepaymentItem = (data, isReview = false) => {
  const { status, userPackId, CreatedAt, type } = data;
  const userPack = data?.userPack ?? data?.UserPack;
  const from = userPack.MTDAddress;
  const toSmc = userPack.tronAddress;
  const balanceStr = data.transferVolume.toString();
  const subStrBalance = balanceStr === '0' ? '0' : balanceStr.slice(0, -6);
  const amount = `${numberWithCommas(subStrBalance, ',')}$`;

  const balance = `-${amount}`;
  const dueTime = data?.dueTime ?? 0;
  const total = `0$`;
  const release = formatMTDToken(data.release.releaseAmount);
  const revoke = formatMTDToken(data.release.revokeAmount);

  const transactionData = {
    dueTime,
    prePayment: formatDepositBalance(balanceStr),
    address: 'TQbpvRQHgkhDNiRtwqjH8DCXWwwPwMDQSa',
  };

  const typeOfPayment =
    type === 'post'
      ? 'repaymentOverDue'
      : data?.late
      ? 'repaymentLate'
      : 'repaymentOnTime';

  let describe = 'REPAYMENT ON TIME 01/240M';
  if (typeOfPayment === 'repaymentLate') {
    describe = 'REPAYMENT ON LATE 01/240M';
  }

  let dataItem = [];
  if (isReview) {
    dataItem = [
      describeAndTime(describe, CreatedAt),
      paymentWithBalane(from, toSmc, amount),
      releaseAndRevoked(`+${release} MTD`, `-${revoke} MTD`),
    ];
  } else {
    dataItem = [
      transtionWithStatus1(
        status,
        typeOfPayment,
        userPackId,
        format(new Date(CreatedAt), 'dd MMM yyyy, hh:mm'),
      ),
      paymentWithBalane(from, toSmc, amount),
      releaseAndRevoked(`+${release} MTD`, `-${revoke} MTD`),
    ];
  }

  const result = {
    type: typeOfPayment,
    status: status,
    data: dataItem,
    transactionData: transactionData,
    isReview: isReview,
  };
  console.log('result ---->', result);

  return result;
};

const renderPrepaymentItem = (data) => {
  const { type, status, userPackId, timeStamp, transferVolume } = data;
  const smartContract = `xsmart${userPackId}`;
  const balane = hexToDecimal(transferVolume);

  const release = `+${numberWithCommas(balane.toFixed(3), ',')} MTD`;
  const dataItem = [
    transtionWithStatus3(
      userPackId,
      status,
      smartContract,
      release,
      format(new Date(timeStamp), 'dd MMM yyyy, hh:mm'),
    ),
  ];
  const result = {
    type: 'repayment',
    status: status,
    data: dataItem,
  };

  return result;
};

const renderItemTabOrder = (data) => {
  switch (data.type) {
    case 'deposit':
      return renderDepositItem(data);
    case 'balance':
    case 'repayment':
      return renderRepaymentItem(data);
    case 'contract':
      return renderItemTabContract(data);
    default:
      break;
  }
};

const renderItemTabSmartcontract = (data) => {
  switch (data.type) {
    case 'contract':
      return renderItemTabContract(data);
    case 'repayment':
      return renderRepaymentItem(data);
    case 'pre':
    case 'special':
      return renderPrepaymentItem(data);
    case 'post':
      return renderRepaymentItem(data);
    default:
      break;
  }
};

const renderItemTabContract = (data) => {
  console.log('data ---->', data);
  const { type, status, userPackId, time, dueTime, atRound, to, CreatedAt } = data;

  // general data
  const discountPrice = data.UserPack.PackVariant.Pack.discountPrice;
  const price = data.UserPack.PackVariant.Pack.price;
  const numberPackVariant = data.UserPack.numberPackVariant;
  const totalUsdt = discountPrice * numberPackVariant;
  const totalMtd = (discountPrice * numberPackVariant) / price;
  const MTDAddress = data.UserPack.MTDAddress;
  const balanceStr = data.transferVolume.toString();
  const subStrBalance = balanceStr === '0' ? '0' : balanceStr.slice(0, -6);
  const amount = `${numberWithCommas(subStrBalance, ',')}$`;
  const smcId = `xsmart${userPackId}`;

  // contractDetail and contractSmcDetail block
  const round = atRound.toUpperCase();
  const createTime = format(new Date(CreatedAt), 'dd MMM yyyy, hh:mm');
  const packageArr = [`${discountPrice}$ (${price}$/MTD) x${numberPackVariant}`];
  const receiveMTD = `${numberWithCommas(
    ((discountPrice * numberPackVariant) / price).toFixed(0),
    ',',
  )}`;
  const paymentUSDT = `${numberWithCommas(discountPrice * numberPackVariant, ',')}`;
  // userInfo block
  const email = data.UserPack.email;
  const tronAddress = data.UserPack.tronAddress;

  // prepaymentBlock
  const percentPrePayment = data.UserPack.PackVariant.percentPrePayment;
  const prepayment = `${percentPrePayment}% = ${numberWithCommas(
    (totalUsdt * percentPrePayment) / 100,
    ',',
  )} USDT`;
  const totalPreRelease = `${numberFormatShowMTD(
    (totalMtd * percentPrePayment) / 100,
    0,
  )} MTD`;
  const preDistribution = `${numberFormatShowMTD(
    (totalMtd * percentPrePayment) / 100 / 48,
    0,
  )}+ MTD/month`;

  // repayment block
  const repaymentPercent = 100 - percentPrePayment;
  const repayment = `${repaymentPercent}% = ${numberWithCommas(
    (totalUsdt * repaymentPercent) / 100,
    ',',
  )} USDT`;
  const totalReRelease = `${numberFormatShowMTD(
    (totalMtd * repaymentPercent) / 100,
    0,
  )} MTD`;
  const reReleaseTime = `${data.UserPack.PackVariant.paymentDuration} months`;

  // distribution block
  let distribution = [];
  if (data.UserPack.PackVariant.Pack.Round.ID > 3) {
    data.UserPack.distribution.monthlyPaymentArr.map((el, index) => {
      distribution.push({
        year: `${index + 1}st`,
        paymentUSDT: el,
        receiveMTD: `${formatMTDToken(
          data.UserPack.distribution.mtdPaymentArr[index],
          3,
        )}`,
      });
    });
  }

  //  transactionWithFree block
  const transferFee = '-1$';
  const totalRefund = '1,999$';

  // transactionData when pending
  const transactionData = {
    dueTime,
    prePayment: formatDepositBalance(balanceStr),
    address: 'TQbpvRQHgkhDNiRtwqjH8DCXWwwPwMDQSa',
  };

  // distribution data when round id < 3
  const distributionTableData = {
    round: data.UserPack.PackVariant.Pack.Round.ID,
    totalMtd:
      formatReleaseToken(data.UserPack.PackVariant.totalVolume) *
      data.UserPack.numberPackVariant,
  };

  let dataItem = [];

  switch (data.status) {
    case 'pending':
      dataItem = [
        contractDetail(round, createTime, packageArr, to, receiveMTD, paymentUSDT),
        userInfo(email, tronAddress),
        prepaymentBlock(prepayment, totalPreRelease, preDistribution),
        repaymentBlock(repayment, totalReRelease, reReleaseTime),
      ];
      if (data.UserPack.PackVariant.Pack.Round.ID > 3) {
        dataItem.push(distribution);
      }
      break;
    case 'expired':
    case 'success':
      dataItem = [
        transtionWithStatus2(
          userPackId,
          status,
          format(new Date(time), 'dd MMM yyyy, hh:mm'),
        ),
        contractSmcDetail(
          smcId,
          round,
          packageArr,
          MTDAddress,
          receiveMTD,
          paymentUSDT,
        ),
        userInfo(email, tronAddress),
        prepaymentBlock(prepayment, totalPreRelease, preDistribution),
        repaymentBlock(repayment, totalReRelease, reReleaseTime),
      ];
      if (data.UserPack.PackVariant.Pack.Round.ID > 3) {
        dataItem.push(distribution);
      }
      break;
    case 'refund':
    case 'refundProcessing':
      dataItem = [
        contractDetailWithStatus(
          round,
          status,
          createTime,
          packageArr,
          to,
          receiveMTD,
          paymentUSDT,
        ),
        userInfo(email, tronAddress),
        prepaymentBlock(prepayment, totalPreRelease, preDistribution),
        repaymentBlock(repayment, totalReRelease, reReleaseTime),
      ];
      if (data.UserPack.PackVariant.Pack.Round.ID > 3) {
        dataItem.push(distribution);
      }
    case 'refunded':
      dataItem = [
        transactionWithFree(
          userPackId,
          amount,
          transferFee,
          totalRefund,
          tronAddress,
          format(new Date(time), 'dd MMM yyyy, hh:mm'),
        ),
        contractDetailWithStatus(
          round,
          status,
          createTime,
          packageArr,
          to,
          receiveMTD,
          paymentUSDT,
        ),
        userInfo(email, tronAddress),
        prepaymentBlock(prepayment, totalPreRelease, preDistribution),
        repaymentBlock(repayment, totalReRelease, reReleaseTime),
      ];
      if (data.UserPack.PackVariant.Pack.Round.ID > 3) {
        dataItem.push(distribution);
      }
    default:
      break;
  }

  const result = {
    type: type,
    status: status,
    data: dataItem,
    transactionData: transactionData,
    distributionTableData: distributionTableData,
    isReview: status === 'pending',
  };

  return result;
};

export {
  renderItemTabOrder,
  renderItemTabSmartcontract,
  renderItemTabContract,
  renderRepaymentItem,
};
