import WrapScroll from '@components/molecules/WrapScroll';
import DetailListPackage from '../DetailListPackage';
import { fetchListGetPayment, getPackvariantByPackId } from '@api/BuyToken';
import {
  fixedNumberDropZeroAtTheEnd,
  numberFormatShowMTD,
  numberWithCommas,
} from '@utils/currencyFormat';
import { useNavigate, useParams } from 'react-router-dom';
import ContactDetail from '../Checkout/ContactDetail';
import GroupText from '@components/atoms/GroupText';
import DistributionTable from '@page/TokenomicApp/components/Distribution/components/DistributionTable';
import { capitalizeFirstLetter, formatMoney, formatMTDToken } from '@utils/format';
import { getYearlyAmount, rate } from '@utils/distributionByTime';
import { data } from 'autoprefixer';
import DistributionComponent from '../DistributionTable';
import { getTransactionPending } from '@api/Transaction';
import { useSelector } from 'react-redux';
import HasPendingTransaction from '../Modal/HasPendingTransaction';
import { format } from 'date-fns';
import useScrollToTop from '@hooks/useScrollToTop';

function Step2({ onNext, dataSteps, onViewTransactionPending }) {
  // hooks
  const params = useParams();
  const navigate = useNavigate();
  useScrollToTop();

  // redux
  const walletAddress = useSelector((state) => state.global.walletAddress);

  // states
  const [check, setCheck] = useState(false);
  const [openHasPendingTransaction, setOpenHasPendingTransaction] = useState(false);
  const [getPayments, setGetPayments] = useState();
  const [round, setRound] = useState(4);
  const [transactionPending, setTransactionPending] = useState();
  const [packvariantMapingId, setPackvariantMapingId] = useState();

  // react hooks
  useEffect(() => {
    if (params.id) {
      setRound(params.id);
    }
  }, [params]);

  useEffect(() => {
    const dataPackInfo = dataSteps.dataStep1.packPaymentInfo;
    getPackvariantByPackId(dataPackInfo.ID).then((res) => {
      const rs = res.data;

      const { paymentInfo } = dataSteps.dataStep1;
      if (!paymentInfo.year) {
        paymentInfo.year = 0;
      }

      const packvariantMaping = rs.find((el) => {
        return (
          el.percentPrePayment === paymentInfo.prepayment &&
          el.paymentDuration === paymentInfo.year * 12
        );
      });
      setPackvariantMapingId(packvariantMaping.ID);
      if (packvariantMaping) {
        fetchListGetPayment(
          dataPackInfo.ID,
          packvariantMaping.ID,
          dataPackInfo.count,
        ).then((res) => {
          if (res.data) {
            let arr = [];
            res.data.monthlyPaymentArr.map((el, index) => {
              arr.push({
                year: `${index + 1}st`,
                paymentUSDT: el,
                receiveMTD: `${formatMTDToken(res.data.mtdPaymentArr[index], 3)}`,
              });
            });

            setGetPayments(arr);
          }
        });
      }
    });
  }, []);

  const totalUsdt = useMemo(() => {
    const { packPaymentInfo } = dataSteps.dataStep1;
    return packPaymentInfo.discountPrice * packPaymentInfo.count;
  }, [dataSteps]);

  const totalMtd = useMemo(() => {
    const { packPaymentInfo } = dataSteps.dataStep1;
    return (
      (packPaymentInfo?.discountPrice * packPaymentInfo?.count) /
      packPaymentInfo?.price
    );
  }, [dataSteps]);

  const dataPrepayment = useMemo(() => {
    const { paymentInfo } = dataSteps.dataStep1;

    const prepaymentPercent = paymentInfo.prepayment;
    const totalMtdRelease = numberFormatShowMTD(
      (totalMtd * prepaymentPercent) / 100,
      0,
    );

    const distribution = numberFormatShowMTD(
      (totalMtd * prepaymentPercent) / 100 / 48,
      0,
    );
    const data = {
      prepayment: `${prepaymentPercent}% = ${numberWithCommas(
        (totalUsdt * prepaymentPercent) / 100,
        ',',
      )} USDT`,
      totalRelease: `${totalMtdRelease} MTD`,
      distribution: `${distribution}+ MTD/month`,
      releaseTime: `${48} months`,
    };
    return data;
  }, [dataSteps]);

  const dataRepayment = useMemo(() => {
    const { paymentInfo } = dataSteps.dataStep1;

    const repaymentPercent = 100 - paymentInfo.prepayment;
    const totalMtdRelease = numberFormatShowMTD(
      (totalMtd * repaymentPercent) / 100,
      0,
    );

    const data = {
      repayment: `${repaymentPercent}% = ${numberWithCommas(
        (totalUsdt * repaymentPercent) / 100,
        ',',
      )} USDT`,
      totalRelease: `${totalMtdRelease} MTD`,
      releaseTime: paymentInfo.year ? `${paymentInfo.year * 12} months` : `0 months`,
    };
    return data;
  }, [dataSteps]);

  const hanldeConfirm = async () => {
    try {
      const check = await getTransactionPending(walletAddress);

      if (check.data !== null) {
        setTransactionPending(check.data);
        setOpenHasPendingTransaction(true);
      } else {
        onNext?.({
          getPayments,
          dataRepayment,
          dataPrepayment,
          totalUsdt,
          totalMtd,
          packvariantMapingId,
          step: 2,
        });
      }
    } catch (error) {
      console.log('error --->', error);
    }
  };

  const handleViewTransaction = useCallback(() => {
    const txnTronData = transactionPending.txnTron;
    const userPackData = transactionPending.userPack;

    // buy pack response
    const tmpDepositMoney = txnTronData.transferVolume.toString();
    const buytokenResResult = {
      prePayment:
        numberWithCommas(tmpDepositMoney.slice(0, -6), ',') +
        '.' +
        tmpDepositMoney.slice(-6),
      address: txnTronData.to,
      userPackId: txnTronData.userPackId,
      dueTime: txnTronData.dueTime,
    };

    // user buy pack infomation
    const dataUser = {
      email: `${userPackData.email}`,
      refundWallet: `${userPackData.tronAddress}`,
      paymentMethod: 'USDT (TRC20)',
    };

    // data detail transaction
    const totalUsdt =
      userPackData.PackVariant.Pack.discountPrice * userPackData.numberPackVariant;
    const totalMtd =
      (userPackData.PackVariant.Pack.discountPrice *
        userPackData.numberPackVariant) /
      userPackData.PackVariant.Pack.price;
    const dataTransaction = {
      round: `${capitalizeFirstLetter(
        userPackData.PackVariant.Pack.Round.description,
      )}`,
      createTime: format(new Date(userPackData.CreatedAt), 'dd MMMM yyyy, HH:mm'),
      package: [
        `${numberWithCommas(userPackData.PackVariant.Pack.discountPrice, ',')} (${
          userPackData.PackVariant.Pack.price
        }$/MTD) x${userPackData.numberPackVariant}`,
      ],
      receiveMTDWallet: `${userPackData.MTDAddress}`,
      paymentUSDT: `${numberWithCommas(totalUsdt, ',')}$`,
      receiveMTD: `${numberFormatShowMTD(totalMtd, 0)} MTD`,
    };

    // prepayment data
    const prepaymentPercent = userPackData.PackVariant.percentPrePayment;
    const totalMtdReleasePre = numberFormatShowMTD(
      (totalMtd * prepaymentPercent) / 100,
      0,
    );
    const distributionPre = numberFormatShowMTD(
      (totalMtd * prepaymentPercent) / 100 / 48,
      0,
    );
    const dataPrepayment = {
      prepayment: `${prepaymentPercent}% = ${numberWithCommas(
        (totalUsdt * prepaymentPercent) / 100,
        ',',
      )} USDT`,
      totalRelease: `${totalMtdReleasePre} MTD`,
      distribution: `${distributionPre}+ MTD/month`,
      releaseTime: `${48} months`,
    };

    // repayment data
    const repaymentPercent = 100 - prepaymentPercent;
    const totalMtdReleaseRe = numberFormatShowMTD(
      (totalMtd * repaymentPercent) / 100,
      0,
    );
    const dataRepayment = {
      repayment: `${repaymentPercent}% = ${numberWithCommas(
        (totalUsdt * repaymentPercent) / 100,
        ',',
      )} USDT`,
      totalRelease: `${totalMtdReleaseRe} MTD`,
      releaseTime: `${userPackData.PackVariant.paymentDuration} months`,
    };

    // distribution data
    let getPayments = [];
    if (userPackData.distribution.monthlyPaymentArr !== null) {
      userPackData?.distribution?.monthlyPaymentArr.map((el, index) => {
        getPayments.push({
          year: `${index + 1}st`,
          paymentUSDT: el,
          receiveMTD: `${formatMTDToken(
            userPackData?.distribution?.mtdPaymentArr[index],
            3,
          )}`,
        });
      });
    }

    const round = userPackData.PackVariant.Pack.Round.ID;

    onViewTransactionPending &&
      onViewTransactionPending({
        getPayments,
        dataRepayment,
        dataPrepayment,
        dataTransaction,
        dataUser,
        buytokenResResult,
        totalMtd,
        round,
      });
  }, [transactionPending]);

  return (
    <>
      <ContentPage
        childrenButton={
          <div>
            <WrapApproveInfo>
              <WrapIcon>
                <CheckBox
                  type="circle"
                  value={check}
                  onChange={(v) => {
                    v ? setCheck(check + 1) : setCheck(check - 1);
                  }}
                />
              </WrapIcon>
              <Text>
                I have read and agree to the terms and <br /> conditions of the
                investor supporting program
                <br />
                <span className="investnow" onClick={() => navigate('/invest-now')}>
                  Invest now, Pay later.
                </span>
              </Text>
            </WrapApproveInfo>
            <WrapButton>
              <Button title="continue" onClick={hanldeConfirm} disable={!check} />
            </WrapButton>
          </div>
        }>
        <Content>
          <InfoBlock roundInfo={dataSteps?.dataStep1.roundInfo} />
          <WrapScroll className="ahihi">
            <DetailListPackage dataSteps={dataSteps.dataStep1} />

            <BlockTitleList
              list={[
                {
                  title: 'Total USDT',
                  value: `${numberWithCommas(totalUsdt, ',')}$`,
                },
              ]}
              delayTime={timeTr3 * 2}
              fullWidth
            />
            <BlockTitleList
              list={[
                {
                  title: 'Total MTD',
                  value: `${numberFormatShowMTD(totalMtd, 0)} MTD`,
                },
              ]}
              delayTime={timeTr3 * 2}
              fullWidth
            />

            {round > 3 ? (
              <>
                <GroupText
                  data={dataPrepayment}
                  title="prepayment"
                  titlePosition="center"
                />
                <GroupText
                  data={dataRepayment}
                  title="repayment"
                  titlePosition="center"
                />
                {getPayments && (
                  <GroupText
                    data={getPayments}
                    titlePosition="center"
                    type="distribution"
                  />
                )}
              </>
            ) : (
              <>
                <DistributionComponent round={round} totalMtd={totalMtd} />
              </>
            )}
          </WrapScroll>
        </Content>
      </ContentPage>
      <HasPendingTransaction
        buttonTitle="my transaction"
        title="Please complete your current transaction or wait for it to expire."
        visible={openHasPendingTransaction}
        onClose={() => setOpenHasPendingTransaction(false)}
        onViewTransaction={handleViewTransaction}
      />
    </>
  );
}

export default Step2;

