import { numberWithCommas } from '@utils/currencyFormat';
function Step2({ onNext, dataAllStep, setStep }) {
  const [checkbox, setCheckBox] = useState(false);

  const dataMapping = useMemo(() => {
    const d = dataAllStep.dataStep1;
    const noticeKey = d.notice === '' ? '----' : d.notice;

    const data = [
      {
        data: {
          deposit: `${numberWithCommas(d?.amount, ',')}$`,
          toSmartContract: 'x2478127987192312893',
          paymentMethods: 'USDT (TRC20)',
        },
        title: 'Transaction detail',
      },
      {
        data: {
          debtBalance: `-${numberWithCommas(d?.amount, ',')}$`,
          balance1: `+${numberWithCommas(d?.amount, ',')}$`,
        },
      },
      {
        data: {
          [noticeKey]: null,
        },
        title: 'Notice',
        type: 'notice',
      },
    ];
    return data;
  }, []);

  return (
    <ContentPage
      childrenButton={
        <WrapBottom>
          <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 6}>
            <WrapApproveInfo>
              <WrapIcon>
                <CheckBox
                  type="circle"
                  value={checkbox}
                  onChange={() => setCheckBox(!checkbox)}
                />
              </WrapIcon>
              <Text>
                I understand that it is necessary to deposit the exact amount of USDT
                (TRC20) to the wallet address, in order to complete the transaction.
              </Text>
            </WrapApproveInfo>
          </Animated>
          <Button
            title="Confirm"
            onClick={() => onNext?.({ fromStep: 2 })}
            disable={!checkbox}
          />
        </WrapBottom>
      }>
      <Wrapper>
        <div className="pt-2">
          <Header children={'Deposit'} onBack={() => setStep && setStep(1)} />
          <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 5}>
            <WrapTextHead>
              You have a transaction pending for your confirmation
            </WrapTextHead>
            <GroupTextMapping data={dataMapping} />
          </Animated>
        </div>
      </Wrapper>
    </ContentPage>
  );
}

export default Step2;

