import React from 'react';
import styled, { css } from 'styled-components';
import { format } from 'date-fns';
import { formatMoney, formatMTDToken } from '../../../../../../../utils/format';

const StatusDetail = ({ detail }) => {
  console.log('detail --->', detail.totalVolume / 10 ** 18);
  let b = BigInt(10 ** 18);
  let a = BigInt(detail.totalVolume);
  console.log('a -->', Number((a * 1000n) / b) / 1000);
  return (
    <Root>
      <WrapDetail>
        <Row>
          <Col isLabel>Start date</Col>
          <Col>{format(new Date(detail.start), 'MM/dd/yyyy')}</Col>
        </Row>
        <Row>
          <Col isLabel>End date</Col>
          <Col>{format(new Date(detail.end), 'MM/dd/yyyy')}</Col>
        </Row>
        <Row>
          <Col isLabel>Price</Col>
          <Col>{`${detail.defaultPrice}$`}</Col>
        </Row>
        <Row>
          <Col isLabel>Token MTD</Col>
          <Col isMoney>{formatMTDToken(detail.totalVolume, 3)}</Col>
        </Row>
        <Row>
          <Col isLabel>USDT</Col>
          <Col isMoney>{formatMoney(detail.usdt, ',')}</Col>
        </Row>
      </WrapDetail>
    </Root>
  );
};

export default StatusDetail;

const Root = styled.div`
  margin-top: var(--space-4);
`;

const WrapDetail = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;

  border: 1px solid rgba(237, 238, 240, 0.5);
  border-radius: 10px;
`;

const Row = styled.div`
  display: flex;
  justify-content: space-between;

  padding: var(--space-2) var(--space-3);

  & + & {
    border-top: 1px solid rgba(237, 238, 240, 0.5);
  }
`;

const Col = styled.div`
  max-width: 50%;

  font-weight: 400;
  color: var(--color-neutral-3);
  font-size: 15px;
  line-height: 15px;

  /* isLabel */
  ${(props) => {
    switch (props.isLabel) {
      case true:
        return css`
          font-weight: 500;
          color: var(--color-neutral-1);
        `;
      default:
        break;
    }
  }}

  /* isMoney */
  ${(props) => {
    switch (props.isMoney) {
      case true:
        return css`
          color: var(--color-accent-blue-1);
          font-weight: 500;
        `;
      default:
        break;
    }
  }}
`;
