import styled from 'styled-components';
import React from 'react';

const ScrollRoot = styled.div`
  position: relative;
  height: 100%;
  /* overflow-y: scroll; */
  width: 100%;
  -ms-overflow-style: none;
  scrollbar-width: none;
  ::-webkit-scrollbar {
    display: none;
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
`;

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  ::-webkit-scrollbar {
    display: none;
  }
`;

export default class ScrollViewer extends React.Component {
  constructor() {
    super();
    this._scrollRef = React.createRef();
    this._resTimeOut = React.createRef();
    this._resTimeOut0 = React.createRef();
    this._resTimeOut1 = React.createRef();
    this._resCountEvent = React.createRef(0);
  }

  componentDidMount() {
    this._invokeLoadMore();
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.canLoadMore && !this._loadingMore) {
      window.setTimeout(() => {
        this._checkHasScrollbar();
      });
    }
  }

  componentWillUnmount() {
    clearTimeout(this._resTimeOut.current);
  }
  // static getDerivedStateFromProps(nextProps,prevState) {
  //   if (nextProps.canLoadMore && !this._loadingMore) {
  //     window.setTimeout(() => {
  //       this._checkHasScrollbar();
  //     });
  //     return {}
  //   }
  //   else return null;
  // }

  _needLoadMore() {
    const scroll = this._scrollRef.current;
    if (scroll) {
      const elHeight = scroll.getBoundingClientRect().height;
      return Math.floor(scroll.scrollHeight - elHeight) <= 0;
    }
    return false;
  }

  _invokeLoadMore() {
    const { canLoadMore, onLoadMore } = this.props;
    if (canLoadMore && !this._loadingMore) {
      const loadMorePromise = typeof onLoadMore === 'function' && onLoadMore();
      if (loadMorePromise) {
        this._loadingMore = true;
        loadMorePromise.then(() => {
          this._loadingMore = false;
          this._checkHasScrollbar();
        });
      }
    }
  }

  _checkHasScrollbar() {
    if (this._needLoadMore()) {
      this._invokeLoadMore();
    }
  }

  _handleScroll(e) {
    e.stopPropagation();
    const scroll = this._scrollRef.current;
    if (scroll) {
      const elHeight = scroll.getBoundingClientRect().height;
      const scrollHeight = scroll.scrollHeight - elHeight;
      if (scrollHeight <= 0) {
        return;
      }
      const curPos = scroll.scrollTop;

      // Event scroll spread to the whole page.
      // There are three even emitted when scroll.
      if (curPos < 50) {
        clearTimeout(this._resTimeOut1.current);
        this._resCountEvent.current = 0;
        if (this._resTimeOut0.current) {
          clearTimeout(this._resTimeOut0.current);
        }
        this._resTimeOut0.current = setTimeout(() => {
          this._resTimeOut0.current = undefined;
          window?.dispatchEvent(new Event('scroll0'));
        }, 100);
      }
      if (curPos > 100) {
        clearTimeout(this._resTimeOut0.current);
        if (this._resTimeOut1.current) {
          clearTimeout(this._resTimeOut1.current);
        }
        this._resTimeOut1.current = setTimeout(() => {
          this._resTimeOut1.current = undefined;
          if (this._resCountEvent.current === 0) {
            this._resCountEvent.current = 1;
            window?.dispatchEvent(new Event('scroll1'));
          } else {
            this._resCountEvent.current = 0;
            window?.dispatchEvent(new Event('scroll2'));
          }
        }, 100);
      }

      if (curPos === scrollHeight) {
        window?.dispatchEvent(new Event('scroll2'));
      }
      // End of code

      if (curPos - (this._prePos || 0) > 0) {
        if (scrollHeight - curPos <= 0.1 * elHeight) {
          this._invokeLoadMore();
        }
      }
      this._prePos = curPos;
    }
  }

  render() {
    const { children, onLoadMore, canLoadMore, ...rest } = this.props;
    return (
      <ScrollRoot className="scroll_viewer-root" {...rest}>
        <Wrapper
          ref={this._scrollRef}
          className="scroll_viewer-container"
          onScroll={this._handleScroll}>
          {children}
        </Wrapper>
      </ScrollRoot>
    );
  }
}
