import axiosClient, { axiosInstance } from './ApiClient';

export const fetchRoundOpen = async () => {
  const url = '/rounds/open';
  const res = await axiosInstance.get(url);
  return res.data;
};

export const fetchRoundOpenById = async (id) => {
  const url = `/round/${id}`;
  const res = await axiosInstance.get(url);
  return res.data;
};

export const fetchRoundList = async () => {
  const url = '/rounds';
  const res = await axiosInstance.get(url);
  return res.data;
};
