import React, { useEffect, useState } from 'react';
import styled from 'styled-components';

import BuyTokenHeader from './components/BuyTokenHeader';
import { LayoutPage } from '../../components/LayoutPage';
import ModalBuyToken from './components/Modal';
import Content from './components/Modal/Content';
import Step1 from './components/ListStep/Step1';
import Step2 from './components/ListStep/Step2';
import Step3 from './components/ListStep/Step3';
import Step4 from './components/ListStep/Step4';
import Button from '@components/atoms/Button';
import { useSelector } from 'react-redux';
import { useLocation, useNavigate, useParams } from 'react-router-dom';
import { useAppSelector } from '@redux/configureStore';
import { getPaddingWhenHasNotch } from '@redux/selector';
import ContractDetail from '@components/ContractDetail';

import GroupTextMapping from '@components/atoms/GroupText/GroupTextMapping';
import { getYearlyAmount } from '@utils/distributionByTime';
import DistributionComponent from './components/DistributionTable';
import { getTransactionPending } from '@api/Transaction';

function BuyToken() {
  // hooks
  const navigate = useNavigate();
  const location = useLocation();
  const params = useParams();

  // redux
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);
  const { roundOpen } = useSelector((state) => state.tokenReducer);

  // states
  const [stack, setStack] = React.useState(1);
  const [dataSteps, setDataSteps] = useState();
  const [round, setRound] = useState(0);
  const [transactionPending, setTransactionPending] = useState();

  useEffect(() => {
    if (location.state) {
      handleChangeStep(location?.state?.dataSteps?.dataStep1);
    }
  }, [location]);

  useEffect(() => {
    if (params.id) {
      setRound(params.id);
    }
  }, [params]);

  const handleChangeStep = (data) => {
    if (data.step === 1) {
      setDataSteps({
        ...dataSteps,
        dataStep1: data,
        from: 'buy-token',
      });
      setStack(2);
    }
    if (data.step === 2) {
      setDataSteps({
        ...dataSteps,
        dataStep2: data,
      });
      setStack(3);
    }
    if (data.step === 3) {
      setDataSteps({
        ...dataSteps,
        dataStep3: data,
      });
      setStack(4);
    }
  };

  const dataOverview = React.useMemo(() => {
    if (stack === 5) {
      if (round < 3) {
        return [
          {
            data: dataSteps?.dataStep4.dataTransaction,
            title: 'Contract detail',
          },
          {
            data: dataSteps?.dataStep4.dataUser,
          },
        ];
      } else {
        return [
          {
            data: dataSteps?.dataStep4.dataTransaction,
            title: 'Contract detail',
          },
          {
            data: dataSteps?.dataStep4.dataUser,
          },
          {
            data: dataSteps?.dataStep2.dataPrepayment,
            title: 'PREPAYMENT',
            titlePosition: 'center',
          },
          {
            data: dataSteps?.dataStep2.dataRepayment,
            title: 'REPAYMENT',
            titlePosition: 'center',
          },
          {
            data: dataSteps?.dataStep2.getPayments,
            type: 'distribution',
            titlePosition: 'center',
          },
        ];
      }
    }
    if (stack === 6) {
      if (transactionPending.round < 3) {
        return [
          {
            data: transactionPending.dataTransaction,
            title: 'Contract detail',
          },
          {
            data: transactionPending.dataUser,
          },
        ];
      } else {
        return [
          {
            data: transactionPending.dataTransaction,
            title: 'Contract detail',
          },
          {
            data: transactionPending.dataUser,
          },
          {
            data: transactionPending.dataPrepayment,
            title: 'PREPAYMENT',
            titlePosition: 'center',
          },
          {
            data: transactionPending.dataRepayment,
            title: 'REPAYMENT',
            titlePosition: 'center',
          },
          {
            data: transactionPending.getPayments,
            type: 'distribution',
            titlePosition: 'center',
          },
        ];
      }
    }
  }, [stack, transactionPending]);

  const handleViewTransactionPending = (data) => {
    setTransactionPending(data);
    setStack(6);
  };

  const renderByStack = React.useMemo(() => {
    switch (stack) {
      case 1:
        return (
          <Step1 onNext={(data) => handleChangeStep(data)} dataSteps={dataSteps} />
        );
      case 2:
        return (
          <Step2
            onNext={(data) => handleChangeStep(data)}
            dataSteps={dataSteps}
            onViewTransactionPending={handleViewTransactionPending}
          />
        );
      case 3:
        return (
          <Step3 onNext={(data) => handleChangeStep(data)} dataSteps={dataSteps} />
        );
      case 4:
        return (
          <>
            <Step4
              dataSteps={dataSteps}
              onSubmit={(data) =>
                setTimeout(() => {
                  setDataSteps({
                    ...dataSteps,
                    dataStep4: data,
                  });
                  setStack(5);
                }, 2000)
              }
            />
          </>
        );
      case 5:
        return (
          <>
            <div style={{ marginBottom: -30 }}>
              <ContractDetail
                transactionData={dataSteps.dataStep4.buytokenRes}
                childrenTransactions={
                  <>
                    <GroupTextMapping data={dataOverview} />
                    {round <= 3 && (
                      <DistributionComponent
                        round={round}
                        totalMtd={dataSteps.dataStep2.totalMtd}
                      />
                    )}
                  </>
                }
                buttonBottom={
                  <Button
                    variant="container"
                    title={'MY WALLET'}
                    noGradient
                    onClick={() => navigate('/manage-tokenomic')}
                  />
                }
              />
            </div>
          </>
        );
      case 6:
        return (
          <>
            {transactionPending && (
              <div style={{ marginBottom: -30 }}>
                <ContractDetail
                  transactionData={transactionPending.buytokenResResult}
                  childrenTransactions={
                    <>
                      <GroupTextMapping data={dataOverview} />
                      {transactionPending.round <= 3 && (
                        <DistributionComponent
                          round={transactionPending.round}
                          totalMtd={transactionPending.totalMtd}
                        />
                      )}
                    </>
                  }
                  buttonBottom={
                    <Button
                      variant="container"
                      title={'MY WALLET'}
                      noGradient
                      onClick={() => navigate('/manage-tokenomic')}
                    />
                  }
                />
              </div>
            )}
          </>
        );
      default:
        return null;
    }
  }, [stack]);

  const renderHeader = React.useMemo(() => {
    switch (stack) {
      case 1:
        return <BuyTokenHeader onBack={() => navigate('/')} />;
      case 2:
        return <BuyTokenHeader title="Review Contract" onBack={() => setStack(1)} />;
      case 3:
        return <BuyTokenHeader onBack={() => setStack(2)} />;
      case 4:
        return <BuyTokenHeader onBack={() => setStack(3)} />;
      default:
        return (
          <BuyTokenHeader
            onBack={() => navigate('/', { state: { currentTab: 'ro' } })}
          />
        );
    }
  }, [stack]);
  return (
    <LayoutPage header={renderHeader}>
      <StackWrapper>{renderByStack}</StackWrapper>
      <ModalBuyToken
        visible={false}
        onClose={() => setStack(1)}
        content={<Content />}
      />
    </LayoutPage>
  );
}

export default BuyToken;

const WrapConfirm = styled.div``;

const StackWrapper = styled.div`
  padding-bottom: 140px;
`;
