import SwipeableBottomSheet from 'react-swipeable-bottom-sheet';
import FlipCountdown from '@rumess/react-flip-countdown';
import React from 'react';
import styled from 'styled-components';
import ProcessBar from './components/processBar';
import Button from '../../../../components/atoms/Button';
import './index.css';
import { Animated } from 'react-animated-css';
import moment from 'moment';
import { useNavigate } from 'react-router-dom';
import FlipClockCountdown from '@leenguyen/react-flip-clock-countdown';
import '@leenguyen/react-flip-clock-countdown/dist/index.css';
import BgBottomSheet from '@assets/images/BgbottomSheet.svg';
import { formatMTDToken } from '@utils/format';
function BottomSheet({
  title = 'Private sale',
  height = 210,
  type = 'buy',
  open = true,
  delayTime = 0,
  round,
  detailByRound,
  onClick,
  buttonTitle,
  onTimeOut,
  timeEnd = new Date(),
}) {
  const navigate = useNavigate();

  const mappingType = React.useMemo(() => {
    switch (type) {
      case 'sold':
      case 'close':
        return 'error';
      case 'count':
        return 'count';
      case 'preorder':
        return '';
      case 'pending':
        return 'import';
      case 'open':
        return 'success';
      default:
        return type;
    }
  }, [type]);

  const mappingTitle = React.useMemo(() => {
    switch (type) {
      case 'sold':
        return 'sold out';
      case 'open':
        return 'open';
      case 'close':
        return 'closed';
      case 'preorder':
        return 'preorder';
      case 'pending':
        return 'pending';
      default:
        return 'BUY NOW';
    }
  }, [type]);
  const HandleClick = () => {
    switch (type) {
      case 'sold':
        return 'sold out';
      case 'open':
      case 'buy now':
        navigate(`/buy-token/${round.ID}`);
        return;
      case 'close':
        return 'closed';
      case 'preorder':
        return 'preorder';
      case 'pending':
        return 'pending';

      default:
        return '';
    }
  };
  if (!open) return null;

  console.log('round -->', round);
  const handleComplete = () => {};
  return (
    <>
      {round.ID && (
        <Root className="Tokenomic-SwipeableBottomSheet">
          <SwipeableBottomSheet
            overflowHeight={height}
            open={false}
            fullScreen={true}
            shadowTip={false}
            overlay={false}
            topShadow={false}
            scrollTopAtClose={true}
            swipeableViewsProps={{ disabled: true }}>
            <Animated
              style={{ height: '100%' }}
              animationIn="fadeInUpCustom"
              animationInDelay={delayTime}>
              <WrapContent bg={BgBottomSheet}>
                <Content>
                  <WrapHeader>
                    <Header>{round?.description}</Header>
                  </WrapHeader>
                  <FlexLayout>
                    <WrapDate key={'date'}>
                      <DateCompoment>
                        Start: {moment(round?.start).format('L')}
                      </DateCompoment>
                      <DateCompoment>
                        End: {moment(round?.end).format('L')}
                      </DateCompoment>
                    </WrapDate>
                    <WrapProcess key={'process'}>
                      <ProcessBar
                        min={0}
                        max={round.totalVolume}
                        value={round.remainVolume}
                        currency={'MTD'}
                      />
                    </WrapProcess>
                    <WrapPrice key={'price'}>
                      <PriceTitle>PRICE:</PriceTitle>
                      <PriceValue>{round?.defaultPrice}$</PriceValue>
                    </WrapPrice>

                    {type !== 'count' && (
                      <WrapButton key={'button'}>
                        <Button
                          variant="outlined"
                          title={buttonTitle}
                          type={mappingType}
                          disableBackgroud
                          onClick={onClick}
                        />
                      </WrapButton>
                    )}
                    {type === 'count' && (
                      <WrapCountDown>
                        <FlipClockCountdown
                          className="flipclockcountdown"
                          id="flipclockcountdown"
                          to={timeEnd}
                          digitBlockStyle={{
                            width: 22,
                            height: 32,
                            fontSize: 20,
                            fontWeight: 'bold',
                            background: 'white',
                            color: '#554CFF',
                          }}
                          labelStyle={{
                            fontSize: 10,
                            fontWeight: 500,
                          }}
                          separatorStyle={{ color: 'white' }}
                          onComplete={() => onTimeOut && onTimeOut()}
                        />
                      </WrapCountDown>
                    )}
                  </FlexLayout>
                </Content>
              </WrapContent>
            </Animated>
          </SwipeableBottomSheet>
          <div className="overlay" style={{ height: `${height * 0.9}px` }}></div>
        </Root>
      )}
    </>
  );
}

export default BottomSheet;

const Root = styled.div`
  flex: 1;
  transform: ${(props) => (props.h ? `translateY(${props.height + 'px'})` : 'none')};
  transition: all 0.3s;
  position: relative;
  z-index: 999;
  > div {
    margin: 0 -2px;
  }
  .react-swipeable-view-container {
    .ReactSwipeableBottomSheet--closed {
      background-color: transparent !important;
    }
  }
`;

const WrapContent = styled.div`
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  background-image: ${(props) => `url(${props.bg})`};
  background-repeat: no-repeat;
  background-size: 100%;
  transition: all 0.5s linear;
  position: relative;
`;

const Content = styled.div`
  display: flex;
  width: 100%;
  height: 100%;
  padding: 0 20px;
  flex-direction: column;
  overflow-y: hidden;
`;

const WrapHeader = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Header = styled.div`
  font-weight: 700;
  font-size: 20px;
  line-height: 28px;
  text-transform: uppercase;

  margin-top: var(--space-4);

  color: white;
`;

const FlexLayout = styled.div`
  display: flex;
  flex-wrap: wrap;
  row-gap: 14px;
  margin-top: 13px;
`;

const WrapDate = styled.div`
  display: flex;
  flex-direction: column;
  color: white;
  flex: 1 0 40%;
`;

const DateCompoment = styled.div`
  font-weight: 400;
  font-size: 12px;
  line-height: 21px;
`;

const WrapPrice = styled.div`
  display: flex;
  flex-direction: column;
  color: white;
  flex: 1 0 30%;
`;

const PriceTitle = styled.div`
  font-weight: 500;
  font-size: 15px;
  line-height: 15px;
`;
const PriceValue = styled.div`
  font-weight: 700;
  font-size: 24px;
  line-height: 21px;
  margin-top: var(--space-1);
`;

const WrapProcess = styled.div`
  flex: 1 0 60%;
`;

const WrapButton = styled.div`
  flex: 1 0 50%;
`;
const WrapCountDown = styled.div`
  flex: 1 0 70%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  .flip-countdown
    .flip-countdown-piece
    .flip-countdown-card
    .flip-countdown-card-sec
    .card__top {
    border-bottom: none;
  }
  #flipclockcountdown {
    > div:nth-child(even) {
      margin-right: 3px;
      margin-left: 3px;
      margin-bottom: 0;
      ::before,
      ::after {
        width: 4px;
        height: 4px;
        background: '#fff' !important;
      }
    }
    > div:nth-child(odd) {
      ._SKh-V {
        border-bottom: var(--fcc-divider-height) solid #554cff;
      }
      ._1GZQ7 {
        border-bottom: var(--fcc-divider-height) solid #554cff;
      }
    }
    ._3cpN7 {
      margin-bottom: 0;
      ._2aOn7 {
        top: 0;
        transform: translate(-50%, -150%);
        bottom: unset;
      }
    }
  }
`;
