import { transactionStatus } from '@assets/icon';
import DefineKey from '@page/Transaction/components/definedKey';
import React from 'react';
import styled from 'styled-components';
import { Image, Left, Right, Row, StatusRow } from './style';

const TableRow = ({ data, type }) => {
  const mappingStatusLabel = React.useCallback(() => {
    switch (data) {
      case 'pending':
        return 'PENDING';
      case 'success':
        return 'SUCCESS';
      case 'sold-out':
        return 'SOLD OUT';
      case 'denied':
        return 'EXPIRED';
      case 'expired':
        return 'EXPIRED';
      case 'time-out':
        return 'TIME OUT';
      case 'refund':
        return 'REFUND';
      case 'refundReject':
        return 'REFUND REJECT';
      case 'refundProcessing':
        return 'REFUND PROCESSING';
      case 'refunded':
        return 'REFUNDED';
      default:
        return '';
    }
  }, [data]);

  const RowDefault = (data) => {
    return (
      <Row>
        <Left>{DefineKey[type] || type}</Left>
        <Right typeKey={type}>{data}</Right>
      </Row>
    );
  };

  const RowStatus = (data) => {
    return (
      <Row1>
        <Left>{DefineKey[type] || type}</Left>
        <Right1>
          <StatusRow status={data}>
            <Image src={transactionStatus[data]} />
            {mappingStatusLabel(data)}
          </StatusRow>
        </Right1>
      </Row1>
    );
  };

  const RowPackage = (data) => {
    return (
      <Row>
        <Left>{DefineKey[type] || type}</Left>
        <Right>
          <div className="package">
            {data.map((item, index) => (
              <p key={index}> {item} </p>
            ))}
          </div>
        </Right>
      </Row>
    );
  };

  const renderRow = React.useMemo(() => {
    switch (type) {
      case 'package':
        return RowPackage(data);
      case 'status':
        return RowStatus(data);
      default:
        return RowDefault(data);
    }
  }, [type, data]);

  return <>{renderRow}</>;
};

export default TableRow;

const Row1 = styled.div`
  height: 40px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  border-bottom: 1px solid #edeef0;
  :last-child {
    border-bottom: none;
  }
`;

const Right1 = styled.div`
  height: 100%;
  /* max-width: 60%; */

  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 13px;
  line-height: 21px;
  display: flex;
  justify-content: flex-start;
  color: var(--color-title);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;

  position: absolute;
  top: 0;
  right: 0;

  .package {
    font-size: 14px;
    text-align: right;
  }
`;
