import ContentPage from '@components/ContentPage';
import Header from '@components/Header';
import { LayoutPage } from '@components/LayoutPage';
import { useAppSelector } from '@redux/configureStore';
import { getContractDetailHeaderTitle } from '@redux/selector';
import React from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';

const DetailLayout = ({ children = null, buttonAction = null, contentOnTop }) => {
  // hooks
  const navigate = useNavigate();

  // redux
  const contractDetailHeaderTitle = useAppSelector(getContractDetailHeaderTitle);

  return (
    <ContentPage childrenButton={buttonAction}>
      <LayoutPage
        header={
          <Header
            onBack={() => navigate(-1)}
            children={contractDetailHeaderTitle}></Header>
        }>
        {contentOnTop && <Title>{contentOnTop}</Title>}
        <Wrapper>{children}</Wrapper>
      </LayoutPage>
    </ContentPage>
  );
};

export default DetailLayout;

const Title = styled.p`
  font-weight: 500;
  font-size: 17px;
  line-height: 23px;
  display: flex;
  align-items: flex-end;
  color: #004dff;
  margin-bottom: 16px;
`;

const Wrapper = styled.div`
  padding-bottom: 95px;
  margin-top: -12px;
`;
