import React from 'react';
import styled from 'styled-components';
import { formatMoney } from '@utils/format';
import Page from '@assets/icon/page.svg';
import { format } from 'date-fns';

const Transaction = ({ transaction, onClick, type }) => {
  const mappingStatusLabel = (status) => {
    switch (status) {
      case 'pending':
        return '<span style="color:#FC9D00">Pending</span>';
      case 'success':
        return '<span style="color:#04B800">Success</span>';
      case 'sold-out':
        return '<span style="color:#FF0366">Sold Out</span>';
      case 'time-out':
        return '<span style="color:#0084F4">Time Out</span>';
      case 'refund':
        return '<span style="color:#554CFF">Refund</span>';
      default:
        return '';
    }
  };

  const a = {
    title: 'Round - Private Funding',
    dateTime: '09/09/2022, 12:12',
    value: 50_000,
    status: 'pending',
    transaction: '0xb082dbdb7645082dbdb7645',
    metanodeWallet: 'text',
    round: 'text',
    price: 'text',
    package: 'text',
    numOfPack: 'text',
    totalMTD: 'text',
    totalUSDT: 'text',
    email: 'text',
    refundWallet: 'text',
    paymentMed: 'text',
  };

  console.log('trean --> ', transaction);

  return (
    <Root onClick={() => onClick(onClick)}>
      <WrapLeft>
        <Icon>
          <img src={Page} alt="" />
        </Icon>
        <Left>
          <Title>{a.title}</Title>
          <Des>
            {type === 2
              ? a.transaction
              : format(new Date(transaction?.CreatedAt), 'MM/dd/yyyy')}
          </Des>
        </Left>
      </WrapLeft>
      <WrapRight>
        <Right>
          <Value type={type}>
            {formatMoney(
              transaction?.PackVariant?.totalVolume * transaction?.numberPackVariant,
            ) || 0}{' '}
            MTD
          </Value>
          {type === 2 ? (
            <Des>{format(new Date(transaction?.CreatedAt), 'MM/dd/yyyy')}</Des>
          ) : (
            <Status
              dangerouslySetInnerHTML={{
                __html: mappingStatusLabel(transaction?.status),
              }}
            />
          )}
        </Right>
      </WrapRight>
    </Root>
  );
};

export default Transaction;

const Root = styled.div`
  display: flex;
  align-items: center;

  & + & {
    margin-top: 14px;
  }
`;

const Title = styled.div`
  line-break: anywhere;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
`;
const Des = styled.div`
  font-weight: 400;
  color: #667386;
  line-break: anywhere;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
`;

const Left = styled.div`
  display: flex;
  flex-direction: column;
  font-weight: 500;
  flex: 1;
`;
const WrapLeft = styled.div`
  display: flex;
  align-items: center;
  flex: 1;
`;

const Value = styled.div`
  font-weight: 500;
  color: ${({ type }) => (type === 2 ? '#004DFF' : '#04004D')};
`;
const Status = styled.div``;

const Right = styled.div`
  display: flex;
  flex-direction: column;
  text-align: right;
  margin-left: 10px;
`;
const Icon = styled.div`
  width: 48px;
  height: 48px;
  background: #ffffff;
  box-shadow: 0px 7px 20px rgba(21, 79, 212, 0.1);
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
`;
const WrapRight = styled.div``;
