import React, { memo, useEffect, useMemo, useState } from 'react';
import { Animated } from 'react-animated-css';
import { timeTr3 } from '@utils/variable';
import styled from 'styled-components';
import ArrowDown from '@assets/icon/arrow-down.svg';
import ModalList from '@components/Modal/ModalList';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTriangleExclamation } from '@fortawesome/free-solid-svg-icons';
import ControlCount from '../common/ControlCount';
import Input from '../common/Input';
import TabCurrency from '../common/TabCurrency';
import { CountDecimal } from '../common/CountDecimal';

function CountCoin({
  handleChangeValueCoinSending,
  valueCoin,
  errorMatchWallet,
  error,
}) {
  const [toggleType, setToggleType] = useState(false);
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [typeNumberCount, setTypeNumberCount] = useState(1);
  const [nameCoin, setNameCoin] = useState({ id: 10, name: 'MTD' });

  const checkNumber = useMemo(() => {
    return valueCoin && parseFloat(valueCoin);
  }, [valueCoin]);

  const HandleCount = (type) => {
    if (!checkNumber) return;
    const value = CountDecimal(type, valueCoin, toggleType, typeNumberCount);
    handleChangeValueCoinSending({ valueCoin: value.toString() });
  };

  const HandleClickModal = (id) => {
    const item = data.find((i) => i.id === id);
    setNameCoin(item);
  };
  return (
    <Wrapper>
      <WrapHeader>
        <Animated animationIn="fadeInLeftCustom" animationInDelay={timeTr3 * 5}>
          <div onClick={() => setIsOpenModal(true)} className="left">
            <span>
              {nameCoin.name.length > 8
                ? nameCoin.name.slice(0, 6) + '...'
                : nameCoin.name}
            </span>
            <img src={ArrowDown} style={{ marginLeft: '4px' }} />
          </div>
        </Animated>
        <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 5}>
          <TabCurrency toggleType={toggleType} setToggleType={setToggleType} />
        </Animated>
      </WrapHeader>
      <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 6}>
        <Input
          onChange={(value) => handleChangeValueCoinSending({ valueCoin: value })}
          valueCoin={valueCoin || ''}
          error={errorMatchWallet || error}
        />
      </Animated>
      <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 8}>
        {error && (
          <Error>
            <FontAwesomeIcon icon={faTriangleExclamation} />
            <span>The wallet balance is not enough!</span>
          </Error>
        )}
        {errorMatchWallet && (
          <Error>
            <FontAwesomeIcon icon={faTriangleExclamation} />
            <span>The wallet address cannot be the same!</span>
          </Error>
        )}
      </Animated>
      <ControlCount
        typeNumberCount={typeNumberCount}
        setTypeNumberCount={setTypeNumberCount}
        HandleCount={HandleCount}
        valueCoin={valueCoin}
        setToggleType={setToggleType}
        toggleType={toggleType}
        delayTime={timeTr3 * 9}
        isOpen={checkNumber}
      />
      <ModalList
        visible={isOpenModal}
        onClose={() => setIsOpenModal(false)}
        onClick={(v) => HandleClickModal(v)}
        data={data}
        itemActive={nameCoin.id}
      />
    </Wrapper>
  );
}

const data = [
  {
    id: 1,
    name: 'Token FHFJFFJH',
  },
  {
    id: 2,
    name: 'Token K',
  },
  {
    id: 3,
    name: 'DFGC',
  },
  {
    id: 4,
    name: 'Token AHKFP',
  },
  {
    id: 5,
    name: 'ABC',
  },
];
export default memo(CountCoin);

const Wrapper = styled.div``;
export const WrapHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 21px;
  margin-bottom: 15px;
  .left {
    padding: 0 10px;
    height: 32px;
    background: #ffffff;
    border: 1px solid var(--color-neutral-5);
    border-radius: 10px;
    padding: 0px 9px 0 12px;
    align-items: center;

    display: flex;
    justify-content: space-between;
  }
`;
export const Error = styled.div`
  width: 100%;
  height: 20px;
  display: flex;
  align-items: center;
  color: var(--color-warning);
  margin-top: var(--space-1);

  svg {
    width: 18px;
    height: 18px;
    margin: 0 8px;
  }

  span {
    font-size: 13px;
    line-height: 18px;
  }
`;
