const transtionWithStatus1 = (status, type, address, time) => {
  let describe = '';
  switch (type) {
    case 'Deposit':
      describe = 'DEPOSIT';
      break;
    case 'repaymentOnTime':
      describe = 'REPAYMENT ON TIME 01/240M';
      break;
    case 'repaymentLate':
      describe = 'REPAYMENT LATE 01/240M';
      break;
    case 'repaymentOverDue':
      describe = 'REPAYMENT OVERDUE 01/240M';
      break;
    default:
      break;
  }
  return {
    title: 'Transaction details',
    transaction: `0x${makeid(10)}`,
    describe: describe,
    status: status,
    timestamp: time,
  };
};

