'use strict';
//Config based on whitepaper
let rate = new Map();
let percentRelease = new Map();

//Round founder
percentRelease.set(1, [20, 30, 50]);
rate.set(1, [5, 5, 5, 10, 15, 30, 310, 310, 310, 1000]);

//Round Founding
percentRelease.set(2, [20, 30, 50]);
rate.set(2, [50, 50, 50, 60, 70, 140, 290, 290, 1000]);

//Round Advisory
percentRelease.set(3, [30, 35, 35]);
rate.set(3, [65, 65, 65, 85, 105, 210, 405, 1000]);

// ! Khng gi hm ny
const getYearlyAmount = (amount, year, roundType) => {
  if (year < 0 || year > rate.get(roundType).length) {
    return {
      error: 'Year invalid',
    };
  }

  console.log('rate.get(roundType).length', rate.get(roundType).length);

  let totalVolume = BigInt(amount) * BigInt(10 ** 18);
  let release = BigInt(0);
  let reserved = BigInt(0);
  let availability = BigInt(0);
  let volume = BigInt(0);

  for (let atYear = 1; atYear <= year; atYear++) {
    if (atYear <= 3) {
      volume =
        (BigInt(percentRelease.get(roundType)[atYear - 1]) * totalVolume) /
          BigInt(100) +
        reserved;
    } else {
      volume = reserved;
    }
    availability = (BigInt(rate.get(roundType)[atYear - 1]) * volume) / BigInt(1000);

    reserved =
      volume - (volume * BigInt(rate.get(roundType)[atYear - 1])) / BigInt(1000);

    release = reserved + availability;
  }

  return {
    year: year,
    release: percentRelease.get(roundType)[year - 1],
    availability: (rate.get(roundType)[year - 1] * 10) / 100,
    reserved: 100 - (rate.get(roundType)[year - 1] * 10) / 100,
    availabilityMTD: Number(availability / BigInt(10 ** 18)),
    releaseMTD: Number(release / BigInt(10 ** 18)),
    reservedMTD: Number(
      release / BigInt(10 ** 18) - availability / BigInt(10 ** 18),
    ),
  };
};

// ! Gi hm ny:
// Amount l tng lng MTD ca Pack
// month l s  thng hin ti VD: nm th 9 l thng 108
// roundType l 1, 2, 3
const getMonthlyAmount = (amount, month, roundType) => {
  if (roundType == 0) {
    return {
      error: 'roundType invalid',
    };
  }

  let year = month % 12 == 0 ? month / 12 : Math.floor(month / 12) + 1;
  if (year < 0 || year > rate.get(roundType).length) {
    return {
      error: 'Year invalid',
    };
  }

  let yearlyAmount = getYearlyAmount(amount, year, roundType);

  return {
    monthlyAmount: yearlyAmount.availability / 12,
  };
};

export { getYearlyAmount, rate };
