import React from 'react';
import Button from '@components/atoms/Button';
import ModalBase from '@components/atoms/ModalBase';
import Thumb from '@components/Thumb';
import styled from 'styled-components';

function Cancel({visible, setVisible, setShowTransition }) {
  return (
    <ModalBase visible={visible} onClose={() => setVisible(false)}>
    <WrapModal>
      <Thumb onClose={() => setVisible(false)}/>
      <div className="text">If you have already transferred USDT for this invoice, Cancel Order cannot be refunded. Are you sure to cancel this order?</div>
      <WrapButtonModal>
        <Button
          variant="outlined"
          title={'Close'}
          onClick={() => setVisible(false)}
          noGradient
        />
        <Button
          variant="container"
          title={'confirm'}
          onClick={() => { setShowTransition(false);  setVisible(false);} }
          noGradient
        />
      </WrapButtonModal>
    </WrapModal>
  </ModalBase>
  );
}

export default Cancel;

const WrapModal = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 10px;

  >.text {
    font-weight: 500;
    font-size: 17px;
    line-height: 23px;
    color: #04004D;
  }
`;
const Img = styled.img`
  width: 90px;
  height: 84px;
`;
const WrapButtonModal = styled.div`
    display: flex;
    width: 100%;
    margin-top: 10px;
    gap: 20px;
`;