import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import ModalUpdate from './ModalUpdate';
import iconInfo from '@assets/icon/iconInfo.svg';
import ModalTransfer from './ModalTransfer';
import WaitingModal from '@components/Modal/WaitingModal';
import ModalBase from '@components/atoms/ModalBase';
import ModalInfo from './ModalInfo';
function Category({ userPackId }) {
  const navigate = useNavigate();
  const [isOpen, setIsOpen] = useState(false);
  const [isOpenTransfer, setIsOpenTransfer] = useState(false);
  const [isOpenInfo, setIsOpenInfo] = useState(false);

  const handleUpdateInDeposit = () => {
    setIsOpen(true);
    postUpdate();
  };

  const postUpdate = async () => {
    await sleep(3000);
    setIsOpen(false);
  };

  const showInfo = () => {
    setIsOpenInfo(true);
  };

  const sleep = (ms) => {
    return new Promise((resolve) => setTimeout(resolve, ms));
  };

  const data = [
    {
      id: 1,
      description: 'Deposit',
      icon: iconDeposit,
      onClick: () => navigate(`/deposit/${userPackId}`),
    },
    {
      id: 2,
      description: 'Transfer',
      icon: iconTransfer,
      onClick: () => setIsOpenTransfer(true),
    },
    {
      id: 3,
      description: 'Update',
      icon: iconUpdate,
      onClick: handleUpdateInDeposit,
    },
    {
      id: 4,
      description: 'Infomation',
      icon: iconInfo,
      onClick: showInfo,
    },
  ];

  return (
    <Wrapper>
      {data.map((item, i) => (
        <WrapIcon
          key={item.id}
          onClick={(e) => {
            e.preventDefault();
            item.onClick?.();
          }}>
          <Icon receive={item.description === 'Receive'}>
            <img src={item.icon} alt="icon" />
          </Icon>
          <p>{item.description}</p>
        </WrapIcon>
      ))}

      {isOpen && (
        <ModalBase visible>
          <WaitingModal showThumb={false} title="UPDATING" onClose={() => {}} />
        </ModalBase>
      )}
      {/* <ModalUpdate
        visible={isOpen}
        onClose={() => setIsOpen(false)}
        onUpdate={onUpdate}
      /> */}
      <ModalTransfer
        visible={isOpenTransfer}
        onClose={(e) => setIsOpenTransfer(false)}
      />
      <ModalInfo
        userPackId={userPackId}
        visible={isOpenInfo}
        onClose={(e) => setIsOpenInfo(false)}
      />
    </Wrapper>
  );
}

export default Category;

const Wrapper = styled.div`
  display: flex;
  justify-content: space-evenly;
  position: relative;
  z-index: 1;
`;
const Icon = styled.div`
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ffffff;
  border: 1px solid #edeef0;
  backdrop-filter: blur(22px);
  border-radius: 18px;
  pointer-events: none;
  .icon {
    transform: ${(props) => props.receive && 'rotate(-180deg)'};
  }
`;

const WrapIcon = styled.div`
  width: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  p {
    margin-top: 12px;
    font-weight: 400;
    font-size: 13px;
    line-height: 18px;
    color: #04004d;
  }
`;

const iconDeposit =
  'data:image/svg+xml;base64,';

const iconTransfer =
  'data:image/svg+xml;base64,';

const iconReview =
  'data:image/svg+xml;base64,';

const iconUpdate =
  'data:image/svg+xml;base64,';
