import { axiosInstance } from './ApiClient';

export const getAllUserTransaction = async (mtdAddess) => {
  const url = `/userAddress/${mtdAddess}/transaction`;
  const res = await axiosInstance.get(url);
  return res.data;
};

export const getTransactionPending = async (mtdAddess) => {
  const url = `/userpack/${mtdAddess}/pending`;
  const res = await axiosInstance.get(url);
  return res.data;
};

export const getTronTransactionDetail = async (id) => {
  const url = `/transaction/usdt/${id}`;
  const res = await axiosInstance.get(url);
  return res.data;
};
