import React, { useCallback, useEffect, useMemo, useState } from 'react';
import styled from 'styled-components';
import ContentPage from '@components/ContentPage';
import Button from '@components/atoms/Button';
import { Animated } from 'react-animated-css';
import { timeDuration, timeTr3 } from '@utils/variable';
import InfoBlock from '../InfoBlock';
import TabList from '@components/TabList';
import WrapScroll from '@components/molecules/WrapScroll';
import BlockPackage from '../BlockPackage';
import { fetchPackByRoundId, fetchPackList, getWhiteListPack } from '@api/BuyToken';
import { useLocation, useParams } from 'react-router-dom';
import {
  fixedNumberDropZeroAtTheEnd,
  numberFormatShowMTD,
  numberWithCommas,
} from '@utils/currencyFormat';
import unlimitUSDT from '@assets/icon/unlimitUSDT.svg';

import { fetchRoundOpenById } from '@api/RoundApi';
import { useSelector } from 'react-redux';
import ModalNotice from '@components/Modal/ModalNotice';
import { formatReleaseToken } from '@utils/format';
import AntiSpam from '../Modal/AntiSpam';

function Step1({ onNext }) {
  const [error, setError] = useState(false);
  const [listPackByRoundId, setListPackByRoundId] = useState();
  const [activePack, setActivePack] = useState(0);
  const [packPaymentInfo, setPackPaymentInfo] = useState();
  const [packPaymentPicked, setPackPaymentPicked] = useState({});
  const [roundInfo, setRoundInfo] = useState();
  const [showCancelReview, setShowCancelReview] = useState(0);
  const [timePayment, setTimePayment] = useState();
  const param = useParams();
  const walletAddress = useSelector((state) => state.global.walletAddress);
  const [defaultSelect, setDefaultSelect] = useState(0);
  useEffect(() => {
    fetchRoundOpenById(param.id).then((res) => {
      if (res.data.status === 'pre_order' || res.data.status === 'close') {
        getWhiteListPack(param.id, walletAddress).then((res) => {
          let tmpData = res.data.packAccess.map((el) => {
            el.Pack.fiat = el.fiat;
            return el.Pack;
          });
          setRoundInfo(res.data);

          setListPackByRoundId(tmpData.reverse());
        });
      } else {
        fetchPackByRoundId(param.id).then((res) => {
          setRoundInfo(res.data);
          setListPackByRoundId(res.data.reverse());
        });
      }
    });
  }, []);

  const handleClickItem = (i) => {
    console.log('handleClickItem -->', i);
    setPackPaymentPicked((current) => {
      return {
        ...current,
        [i.type]: i.item,
      };
    });

    if (i.type === 'year' || timePayment.length === 0) {
      setError(true);
    }
  };

  const handleClickItemRepayment = (i) => {
    setPackPaymentPicked((pre) => {
      return {
        ...pre,
        prepayment: 100 - i.item,
      };
    });
  };
  console.log('packPaymentInfo -->', packPaymentInfo);
  const onChagneActivePack = (data) => {
    const pack = listPackByRoundId.find((el) => el.ID === data.packId);
    // pack.count = data.count;
    if (pack.durations === null) {
      pack.durations = [];
    }
    let time = pack.durations.map((el) => {
      return el.duration / 12;
    });

    if (data.count <= 0) {
      setActivePack(0);
      setTimePayment();
      setPackPaymentInfo();
      setError(false);
    } else {
      setActivePack(data.packId);
      setTimePayment(time);
      setPackPaymentInfo({ ...pack, count: data.count });

      let arr = pack.percentPrePayment;
      let index = arr.findIndex((item) => item === 50);
      if (index < 0) {
        setDefaultSelect(0);
      } else {
        setDefaultSelect(index);
      }
    }
  };

  const litmitUSDT = useMemo(() => {
    if (roundInfo && roundInfo.whitelist) {
      return roundInfo.whitelist.maxVolume - roundInfo.whitelist.boughtVolume;
    } else {
      return 0;
    }
    // return 1;
  }, [roundInfo]);

  const onReview = useCallback(() => {
    // setShowCancelReview(3);
    // return;
    if (packPaymentInfo.discountPrice * packPaymentInfo.count > litmitUSDT) {
      setShowCancelReview(1);
      return;
    }
    if (
      formatReleaseToken(packPaymentInfo.remainVolume) <
      packPaymentInfo.discountPrice * packPaymentInfo.count
    ) {
      setShowCancelReview(2);
      return;
    }

    onNext?.({
      packPaymentInfo,
      paymentInfo: packPaymentPicked,
      roundInfo: roundInfo?.round,
      step: 1,
    });
  }, [packPaymentInfo, roundInfo, packPaymentPicked]);

  const modalTitle = useMemo(() => {
    switch (showCancelReview) {
      case 1:
        return 'You have reached the "Limit Buying Volume" limit; please contact the support team to request an increase.';
      case 2:
        return 'There are currently insufficient MTD tokens to perform the contract. Please try again later.';
      case 3:
        return 'Requires a minimum purchase of $10,000, after which you can purchase additional MTDs.';

      default:
        break;
    }
  }, [showCancelReview]);

  return (
    <>
      <ContentPage
        childrenButton={
          <WrapButton>
            <div className="display-between mb-[12px]">
              <span>Total USDT</span>
              <span className="font-700 text-blue-1">
                {packPaymentInfo
                  ? `${numberWithCommas(
                      packPaymentInfo.discountPrice * packPaymentInfo.count,
                      ',',
                    )}$`
                  : '0$'}
              </span>
            </div>
            <div className="display-between mb-2">
              <span>Total MTD</span>
              <span className="font-700 text-blue-1">
                {packPaymentInfo
                  ? `${numberFormatShowMTD(
                      (packPaymentInfo?.discountPrice * packPaymentInfo?.count) /
                        packPaymentInfo?.price,
                      0,
                    )} MTD`
                  : '0 MTD'}
              </span>
            </div>
            <Button
              title="review contract"
              onClick={onReview}
              disable={!error}
              style={{ marginBottom: 10 }}
            />
          </WrapButton>
        }>
        <div className="pb-8">
          <InfoBlock style={{ marginBottom: 15 }} roundInfo={roundInfo?.round} />
          <WrapScroll>
            <BlockLimit>
              <TextLimit>Limit buying volume</TextLimit>
              <TextLimit>
                {litmitUSDT === 0 ? (
                  <img src={unlimitUSDT}></img>
                ) : (
                  numberWithCommas(litmitUSDT, ',')
                )}{' '}
                USDT
              </TextLimit>
            </BlockLimit>
            <Title>
              <Animated
                animationIn="fadeInLeftCustom"
                animationInDelay={timeTr3 * 6}>
                Number of Package
              </Animated>
            </Title>
            <List>
              {listPackByRoundId &&
                listPackByRoundId.map((el, index) => (
                  <BlockPackage
                    pack={el}
                    activePack={activePack}
                    onChagneActivePack={onChagneActivePack}
                    key={index}
                  />
                ))}
            </List>
            <Title>
              <Animated
                animationIn="fadeInLeftCustom"
                animationInDelay={timeTr3 * 6}>
                Prepayment (%)
              </Animated>
            </Title>
            <Animated
              animationIn="fadeInRightCustom"
              animationInDelay={timeTr3 * 7}
              animationInDuration={timeDuration}>
              {packPaymentInfo && activePack !== 0 && (
                <TabList
                  type="prepayment"
                  list={packPaymentInfo.percentPrePayment || []}
                  scroll={true}
                  onClick={handleClickItem}
                  autoActive={packPaymentPicked.prepayment}
                  defaultSelect={packPaymentInfo.percentPrePayment[defaultSelect]}
                  isResevers
                />
              )}
            </Animated>
            <Title>
              <Animated
                animationIn="fadeInLeftCustom"
                animationInDelay={timeTr3 * 6}>
                Repayment (%)
              </Animated>
            </Title>
            <Animated
              animationIn="fadeInRightCustom"
              animationInDelay={timeTr3 * 7}
              animationInDuration={timeDuration}>
              {packPaymentInfo && activePack !== 0 && (
                <TabList
                  type="repayment"
                  list={packPaymentInfo.percentPrePayment || []}
                  scroll={true}
                  autoActive={100 - packPaymentPicked.prepayment}
                  defaultSelect={
                    100 - packPaymentInfo.percentPrePayment[defaultSelect]
                  }
                  isResevers
                  onClick={handleClickItemRepayment}
                />
              )}
            </Animated>
            {timePayment && (
              <>
                <Title>
                  <Animated
                    animationIn="fadeInRightCustom"
                    animationInDelay={timeTr3 * 8}>
                    Payment Time
                  </Animated>
                </Title>
                <div className="pb-2">
                  <Animated
                    animationIn="fadeInRightCustom"
                    animationInDelay={timeTr3 * 9}
                    animationInDuration={timeDuration}>
                    <TabList
                      type="year"
                      list={timePayment}
                      widthMultipleRow={true}
                      onClick={handleClickItem}
                      autoActive={packPaymentPicked.year}
                      defaultSelect={timePayment[0]}
                    />
                  </Animated>
                </div>
              </>
            )}
          </WrapScroll>
        </div>
      </ContentPage>
      <ModalNotice
        show={showCancelReview !== 0}
        onClose={() => setShowCancelReview(0)}
        text={modalTitle}
        btnText="i understand"
      />
      {/* <AntiSpam visible={showCancelReview === 0} /> */}
    </>
  );
}

export default Step1;

export const WrapButton = styled.div`
  flex: 1;
  margin-top: 20px;
`;

export const Title = styled.p`
  display: flex;
  width: 100%;
  font-weight: 500;
  font-size: 15px;
  line-height: 15px;
  color: var(--color-title);
  margin-bottom: var(--space-2);
  margin-top: var(--space-3);
  color: #04004d;

  span {
    position: absolute;
    top: -4px;
    left: 40px;
    color: var(--color-warning);
    font-weight: 600;
  }
`;
const List = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const BlockLimit = styled.div`
  width: 100%;
  height: 60px;
  background: #dfddff;
  border-radius: 10px;
  padding: 22px 14px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-bottom: 4px;
`;
const TextLimit = styled.div`
  font-size: 15px;
  line-height: 15px;
  color: #04004d;
  font-weight: 500;
`;

const Text = styled.div`
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 24px;
  color: #04004d;
  margin-bottom: 22px;
`;
