import { getAllUserTransaction } from '@api/Transaction';
import { setContractDetailHeaderTitle } from '@redux/Global/reducer';
import { dataDetailTransactionBlock, typeBlock } from '@utils/dataBlockFake';
import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import BlockTransactionStatus from './BlockTransactionStatus';
import ModalPaymentLate from './ModalPaymentLate';

const BlockTransactionMapping = ({ listTransaction = [] }) => {
  // hooks
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const param = useParams();

  // redux

  // states

  // funtions
  const handleOnclick = (data) => {
    if (!data) return;

    dispatch(setContractDetailHeaderTitle('Manage'));
    navigate(`/payment-card-transaction`, {
      state: { id: param.id, cardData: data },
    });
  };

  return (
    <>
      {listTransaction.map((item, index) => (
        <BlockTransactionStatus
          data={item}
          type={item.type}
          key={index}
          handleOnlick={handleOnclick}
        />
      ))}
    </>
  );
};

export default BlockTransactionMapping;
