import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import Metanode from '@assets/icon/metanode.svg';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMinus, faPlus } from '@fortawesome/free-solid-svg-icons';
import { numberCurrencyFormat, numberWithCommas } from '@utils/currencyFormat';
import iconFiat from '@assets/icon/iconFiat.svg';
import iconMeta500 from '@assets/icon/iconMeta500.svg';
import iconMeta100 from '@assets/icon/iconMeta100.svg';
import iconMeta50 from '@assets/icon/iconMeta50.svg';
import iconMeta20 from '@assets/icon/iconMeta20.svg';
import iconMeta10 from '@assets/icon/iconMeta10.svg';
import iconMeta1 from '@assets/icon/iconMeta1.svg';
import { formatReleaseToken } from '@utils/format';
import Input from '@components/Input';

function BlockPackage({ pack, activePack, onChagneActivePack }) {
  const [count, setCount] = useState(0);

  useEffect(() => {
    if (pack.ID !== activePack) {
      setCount(0);
    }
  }, [activePack]);

  const handleCount = (num) => {
    const c = count + num;
    if (c < 0) {
      // setActivePack(0);
      setCount(0);
      onChagneActivePack({ packId: 0, count: c });
      return;
    }
    // setActivePack(pack.ID);
    onChagneActivePack({ packId: pack.ID, count: c });
    setCount(c);
  };

  const handleEnterInput = (e) => {
    let value = e.target.value;
    if (value[0] === '0') {
      value = value.slice(1);
    }
    if ((value.lenght === 1 && value === '0') || isNaN(value)) {
      onChagneActivePack({ packId: 0, count: c });
      setCount(0);
    }

    const countNum = Number(value);
    onChagneActivePack({ packId: pack.ID, count: countNum });
    setCount(countNum);
  };

  const renderIcon = (price) => {
    switch (price) {
      case 500000:
        return iconMeta500;
      case 100000:
        return iconMeta100;
      case 50000:
        return iconMeta50;
      case 20000:
        return iconMeta20;
      case 1000:
        return iconMeta1;
      default:
        return iconMeta10;
    }
  };

  return (
    <Wrapper
      className="display-between"
      active={activePack === 0 || pack.ID === activePack}>
      <div className="block">
        {/* <div className="avatar">
          <img src={Metanode} alt="" />
        </div> */}
        <img
          src={pack?.fiat ? iconFiat : renderIcon(pack.discountPrice)}
          className="avatar"
        />
        <div className="content">
          <div className="body2">
            <span className="font-500">{`${numberWithCommas(
              pack.discountPrice,
              ',',
            )}$`}</span>
            <span className="font-500"> - </span>
            <span className="">1MTD </span>
            <span className="">= {pack.price}$</span>
          </div>
          <div className="text-color-2 textpool" style={{ fontSize: 11 }}>
            Pool:{' '}
            {pack && numberWithCommas(formatReleaseToken(pack.remainVolume), ',')}{' '}
            MTD
          </div>
        </div>
      </div>
      <div className="wrap-count font-700">
        <FontAwesomeIcon
          icon={faMinus}
          width={30}
          height={30}
          onClick={() => handleCount(-1)}
        />
        <WrapInput>
          <InputCount value={count} onChange={handleEnterInput} />
        </WrapInput>
        {/* <SpanCount>
          <span
            className="text-blue-1 w-full"
            contenteditable="true"
            onInput={EnterInput}>
            {count}
          </span>
        </SpanCount> */}

        <FontAwesomeIcon
          icon={faPlus}
          width={30}
          height={30}
          onClick={() => handleCount(1)}
        />
      </div>
    </Wrapper>
  );
}

export default BlockPackage;
const Wrapper = styled.div`
  display: flex;
  opacity: ${(props) => (props.active ? '1' : '0.2')};

  .block {
    width: auto;
    height: 48px;
    border: 1px solid #edeef0;
    border-radius: 10px;
    display: flex;
    align-items: center;
    padding: 10px;
    gap: 10px;
    width: 68%;
    max-width: 68%;
    .avatar {
      width: 32px;
      height: 32px;
      /* background: #fbbcd5; */
      border-radius: 10px;
      display: grid;
      place-items: center;
    }
    .content {
      width: 100%;
      overflow: hidden;
      .textpool {
        width: 100%;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
  }
  .wrap-count {
    width: 101px;
    height: 48px;
    background: #ffffff;
    box-shadow: 0px 4px 10px rgba(0, 77, 255, 0.05);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    font-size: 15px;
    span {
      font-size: 16px;
    }
  }
`;

const WrapInput = styled.div`
  max-width: 36px;
  height: 28px;
  overflow: hidden;
  white-space: nowrap;
`;
const InputCount = styled.input`
  width: 100%;
  height: 100%;
  border: none;
  text-align: center;
  color: #004dff;
  font-weight: bold;
`;
