import React, { memo } from 'react';
import { Root, WrapButton } from './styled';

function ContentPage({ children, childrenButton = null, bottomSheet = null }) {
  return (
    <Root>
      {children}
      {childrenButton && <WrapButton>{childrenButton}</WrapButton>}
      {bottomSheet}
    </Root>
  );
}

export default memo(ContentPage);
