import { TLocation } from '@api/location';
import { createSlice } from '@reduxjs/toolkit';
export interface GobalState {
  themeMode: string;
  activeBar: string;
  locations: TLocation[];
  categories: any[];
  users: any[];
  walletAddress: string;
  isConnect: boolean;
  hasNotch: boolean;
  paddingTopWhenHasNotch: any;
  transactionDefaultTab: number;
  noticeDefaultTab: number;
  manageTokenomicDefaultTab: number;
  contractDetailHeaderTitle: string;
  cardsState: any;
}

const initialState: GobalState = {
  themeMode: 'dark',
  activeBar: 'home',
  locations: [],
  categories: [],
  users: [],
  walletAddress: localStorage.getItem('mtdAddress') || '',
  isConnect: false,
  hasNotch: window.localStorage.getItem('hasNotch') === 'true',
  paddingTopWhenHasNotch: {
    paddingTop: window.localStorage.getItem('hasNotch') === 'true' ? 44 : 0,
  },
  transactionDefaultTab: 1,
  noticeDefaultTab: 1,
  manageTokenomicDefaultTab: 1,
  contractDetailHeaderTitle: '',
  cardsState: {},
};

const globalSlice = createSlice({
  name: 'global',
  initialState,
  reducers: {
    setThemeMode(state, action) {
      return { ...state, themeMode: action.payload };
    },
    setLocation(state, action) {
      return { ...state, locations: action.payload };
    },
    setCategories(state, action) {
      return { ...state, categories: action.payload };
    },
    setUsers(state, action) {
      return { ...state, users: action.payload };
    },
    setWalletAddress(state, action) {
      console.log('xxxxx', action.payload);
      return { ...state, walletAddress: action.payload };
    },
    setIsConnect(state, action) {
      return { ...state, isConnect: action.payload };
    },
    setHasNotch(state, action) {
      return { ...state, hasNotch: action.payload };
    },
    setPaddingTopWhenHasNotch(state, action) {
      return { ...state, paddingTop: action.payload };
    },
    setDefaultTransactionTab(state, action) {
      return { ...state, transactionDefaultTab: action.payload };
    },
    setDefaultNoticeTab(state, action) {
      return { ...state, noticeDefaultTab: action.payload };
    },
    setContractDetailHeaderTitle(state, action) {
      return { ...state, contractDetailHeaderTitle: action.payload };
    },
    setManageTokenomicDefaultTab(state, action) {
      return { ...state, manageTokenomicDefaultTab: action.payload };
    },
    setCardsState(state, action) {
      return { ...state, cardsState: action.payload };
    },
  },
});

export const {
  setThemeMode,
  setLocation,
  setCategories,
  setWalletAddress,
  setIsConnect,
  setHasNotch,
  setPaddingTopWhenHasNotch,
  setDefaultTransactionTab,
  setDefaultNoticeTab,
  setContractDetailHeaderTitle,
  setManageTokenomicDefaultTab,
  setCardsState,
} = globalSlice.actions;
export default globalSlice.reducer;
