import { axiosInstance } from './ApiClient';

export const getBalanceByUserPackId = async (userPackId) => {
  console.log('userPackId', userPackId);
  const url = `/userpack/${userPackId}/balance`;
  console.log('url', url);
  const res = await axiosInstance.get(url);
  return res.data;
};

export const userCallDeposit = async (param) => {
  console.log('param', param);
  const url = `/deposit`;
  const res = await axiosInstance.post(url, param);
  return res.data;
};

export const getUserpackStates = async (userPackId) => {
  const url = `/userpack/${userPackId}/getState`;
  const res = await axiosInstance.get(url);
  return res.data;
};
export const getPendingDeposit = async (userPackId) => {
  const url = `/userPack/${userPackId}/transaction/usdt/pending`;
  const res = await axiosInstance.get(url);
  return res.data;
};
