import { faClose } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState } from 'react';
import Checkout from '@page/BuyToken/components/Checkout';
import styled from 'styled-components';
import BottomSheet from '../BottomSheet';
import SubTab from '../Tab/SubTab';
import Condition from './components/Condition';
import Status from './components/Status';
import Button from '@components/atoms/Button';
import Invoice from './components/Modal/Invoice';
import Cancel from './components/Modal/Cancel';
import { timeTr3 } from '@utils/variable';
import { Animated } from 'react-animated-css';
import { useDispatch, useSelector } from 'react-redux';
import { fetchRoundList } from '@api/RoundApi';
import { useNavigate } from 'react-router-dom';
import { setIsConnect } from '@redux/Global/reducer';

import CheckConditionBuytoken from '@components/CheckConditionBuytoken';

const Round = () => {
  const [showTransition, setShowTransition] = useState(false);
  const [visible, setVisible] = useState(false);
  const [visibleCancel, setVisibleCancel] = useState(false);
  const [roundList, setRoundList] = useState([]);
  const [detailByRound, setDetailByRound] = useState(null);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [defaultTab, setDefaultTab] = useState(3);
  const [tabActive, setTabActive] = useState(4);
  useEffect(() => {
    fetchRoundList()
      .then((res) => {
        setDetailByRound(res.data[defaultTab]);
        setRoundList(res.data);
        setDefaultTab(3);
        setTabActive(4);
      })
      .catch((err) => {
        console.log('err --> ', err);
      });
  }, []);

  const hanldeChangeTab = (ID) => {
    const tabIndex = roundList.findIndex((el) => el.ID === ID);
    if (tabIndex < 0) return;
    setDetailByRound(roundList[tabIndex]);
    setTabActive(ID);
  };

  const [showModalCheckCondititon, setShowModalCheckCondititon] = useState(false);
  const walletAddress = useSelector((state) => state.global.walletAddress);

  const onBuyNow = () => {
    if (walletAddress) {
      console.log(
        ' ~ file: index.js:58 ~ onBuyNow ~ walletAddress',
        walletAddress,
      );
      console.log('onBuyNow');
      setShowModalCheckCondititon(true);
    } else {
      dispatch(setIsConnect(true));
    }
  };

  const contentsPrivate = React.useMemo(() => {
    return [
      {
        title: 'Condition:',
        contents: [
          <p>
            Investors need to pay a certain amount in advance (down payment)
            depending on the Investors' Choice Package.
          </p>,

          <p>
            Payment must be made on time every month (calculation period: Start of
            Month to End of Month), and the payment information will be sent to
            investors in the detailed statement. Investors must not delay more than
            30 days + 15 days. If payment proceeds after 15 days of the 30-day
            overdue payment, it will be counted as a late payment.
          </p>,
          <p>
            More information on the investor-supported program{' '}
            <span
              style={{
                color: '#004DFF',
                fontWeight: 'bold',
                textDecoration: 'none',
              }}
              onClick={() => navigate('/invest-now')}>
              Invest now, pay later
            </span>
            .
          </p>,
        ],
      },
    ];
  }, []);
  const renderContentCondition = React.useMemo(() => {
    switch (tabActive) {
      case 1:
        return contentsFounder;
      case 2:
        return contentsFoundingTeam;
      case 3:
        return contentsAdvisors;
      default:
        return contentsPrivate;
    }
  }, [tabActive]);
  return (
    <Root>
      <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 2}>
        <SubTab
          tabs={roundList}
          onChangeTab={hanldeChangeTab}
          defaultTab={defaultTab}
        />
      </Animated>
      <div style={{ padding: '0 20px' }}>
        <Animated animationIn="fadeInLeftCustom" animationInDelay={timeTr3 * 2}>
          {detailByRound && <Status detail={detailByRound} />}
        </Animated>
        <Condition contents={renderContentCondition} />

        {/* <BottomSheet title="Private sale" type="open" height={210} round={roundOpen} /> */}
        {showTransition && (
          <WrapTrans>
            <Checkout
              header={
                <WrapHeader onClick={() => setShowTransition(false)}>
                  <FontAwesomeIcon style={{ fontSize: 22 }} icon={faClose} />
                  <div>Receipt</div>
                </WrapHeader>
              }
              footer={
                <WrapButton>
                  <Button
                    variant="container"
                    title={'I WILL COMFIRM LATER'}
                    noGradient
                  />
                  <Button
                    onClick={() => setVisibleCancel(true)}
                    variant="outlined"
                    type="warning"
                    title={'cancel ORDER'}
                    noGradient
                  />
                </WrapButton>
              }
            />
          </WrapTrans>
        )}
        <Invoice
          visible={visible}
          setShowTransition={setShowTransition}
          setVisible={setVisible}
        />
        <Cancel
          visible={visibleCancel}
          setShowTransition={setShowTransition}
          setVisible={setVisibleCancel}
        />

        <ButtonWrap>
          <Button
            title={'buy now'}
            width={'120px'}
            height={'40px'}
            onClick={onBuyNow}
          />
        </ButtonWrap>
        {showModalCheckCondititon && (
          <CheckConditionBuytoken
            round={detailByRound}
            onClose={() => setShowModalCheckCondititon(false)}
            onCheckConditionsSuccess={() =>
              navigate(`/buy-token/${detailByRound.ID}`)
            }
          />
        )}
      </div>
    </Root>
  );
};

export default Round;

const Root = styled.div`
  /* padding-bottom: 210px; */
`;

export const WrapButton = styled.div`
  flex: 1;
  padding: 20px 0;
  display: grid;
  grid-gap: 20px;
  > .Button-Outlined .text {
    -webkit-text-fill-color: #ff0366;
  }
`;
const WrapTrans = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  overflow-x: scroll;
  background-color: #fff;
  padding: 0 20px;
  ::-webkit-scrollbar {
    display: none;
  }
`;

const WrapHeader = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 10px 0;
  font-weight: 500;
  font-size: 17px;
  line-height: 23px;
  color: #04004d;
  > svg {
    position: absolute;
    left: 0;
  }
`;
const tabs = [
  { label: 'Founder', value: 'fo', id: 1 },
  { label: 'Founding Team', value: 'ft', id: 2 },
  { label: 'Advisors', value: 'ad', id: 3 },
  { label: 'Private Funding', value: 'pf', id: 4 },
  { label: 'Public Investors 1', value: 'pi1', id: 5 },
  { label: 'Public Investors 2', value: 'pi2', id: 6 },
];

const statuses = ['pre-order', 'open', 'close'];

const detail = {
  from: '01/01/2023',
  to: '01/01/2023',
  price: 0.0001,
  token: 10_000_000,
  usdt: 5_000_000_000,
  total: 5_000_000,
  currency: '$',
};

const contentsFounder = [
  {
    title: 'Condition:',
    contents: [
      <p>Locked and partial Release in 3 years.</p>,
      <p>
        Availability splits over 9 years, low supply for 6 years, high supply from
        7th year; the first month of year 10th, the rest of reserve will transfer
        into to Availability balance.
      </p>,
      <p>
        Reserve: the number of reserved tokens accumulated annually in 9 years,
        starting from the 7th year, reduces reserve by increasing availability
        percentage annually.
      </p>,
    ],
  },
];

const contentsAdvisors = [
  {
    title: 'Condition:',
    contents: [
      <p>Locked and partial Release in 3 years.</p>,
      <p>
        Availability : splits over 7 years, low supply for 4 years, high supply from
        5th year; the first month of year 8th, the rest of reserve will transfer into
        Availability balance.
      </p>,
      <p>
        Reserve: the number of reserved tokens accumulated annually in 7 years,
        starting from the 5th year, reduces reserve by increasing availability
        annually.
      </p>,
    ],
  },
];

const contentsFoundingTeam = [
  {
    title: 'Condition:',
    contents: [
      <p>Locked and partial Release in 3 years.</p>,
      <p>
        Availability splits over 8 years, low supply for 5 years, high supply from
        6th year; the first month of year 9th, the rest of reserve will transfer into
        Availability balance.
      </p>,
      <p>
        Reserve: the number of reserved tokens accumulated annually in 8 years,
        starting from the 6th year, reduces reserve by increasing availability
        annually.
      </p>,
    ],
  },
];

const ButtonWrap = styled.div`
  margin: 16px 0;
  display: flex;
  justify-content: center;
  button {
    width: 156px;
  }
`;
