import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import TabButton from './TabButton';
import Swip from '../Swip';

const SubTab = ({ tabs, onChangeTab, defaultTab = 0 }) => {
  const [activeTab, setActiveTab] = React.useState(0);

  const setDefaultActiveTab = () => {
    if (defaultTab) {
      setActiveTab(tabs[defaultTab]?.ID);
    } else {
      setActiveTab(tabs[0]?.ID);
    }
  };

  useEffect(() => {
    setDefaultActiveTab();
  }, [tabs, defaultTab]);

  const onClick = (v) => {
    setActiveTab(v);
    onChangeTab && onChangeTab(v);
  };

  return (
    <Root>
      <Swip>
        {tabs.map((tab) => (
          <TabButton
            key={tab.value}
            tab={tab}
            active={tab.ID === activeTab}
            onClick={onClick}
          />
        ))}
      </Swip>
    </Root>
  );
};

export default SubTab;

const Root = styled.div`
  margin-bottom: var(--space-);
  display: flex;
  flex-wrap: nowrap;
  overflow: auto;
  margin-top: 10px;
  margin-left: -20px;
  margin-right: -20px;
  padding: 0 20px;
  & .Swip {
    padding-left: 15px;
    padding-right: 15px;
    padding: 0 8px;
  }

  ::-webkit-scrollbar {
    display: none;
  }

  & {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
`;
