import { transactionStatus } from '@assets/icon';
import DefineKey from '@page/Transaction/components/definedKey';
import React from 'react';
import { Image, Left, Right, Row, StatusRow } from './style';

const TableRow = ({ data, type }) => {
  console.log('data TableRow -->', data);
  const mappingStatusLabel = React.useCallback(() => {
    switch (data) {
      case 'pending':
        return 'Pending';
      case 'success':
        return 'Success';
      case 'sold-out':
        return 'Sold Out';
      case 'denied':
        return 'denied';
      case 'time-out':
        return 'Time Out';
      case 'refund':
        return 'Refund';
      case 'refund reject':
        return 'refund reject';
      default:
        return '';
    }
  }, [data]);

  const RowDefault = (data) => {
    return (
      <Row>
        <Left>{DefineKey[type] || type}</Left>
        <Right>{data}</Right>
      </Row>
    );
  };

  const RowStatus = (data) => {
    return (
      <Row>
        <Left>{DefineKey[type] || type}</Left>
        <Right>
          <StatusRow status={data == 'refund reject' ? 'denied' : data}>
            <Image
              src={
                data == 'refund reject'
                  ? transactionStatus['denied']
                  : transactionStatus[data]
              }
            />
            {mappingStatusLabel(data)}
          </StatusRow>
        </Right>
      </Row>
    );
  };

  const RowPackage = (data) => {
    return (
      <Row>
        <Left>{DefineKey[type] || type}</Left>
        <Right>
          <div className="package">
            {data.map((item, index) => (
              <p key={index}> {item} </p>
            ))}
          </div>
        </Right>
      </Row>
    );
  };

  const renderRow = React.useMemo(() => {
    switch (type) {
      case 'Package':
        return RowPackage(data);
      case 'Status':
        return RowStatus(data);
      default:
        return RowDefault(data);
    }
  }, [type, data]);

  return <>{renderRow}</>;
};

export default TableRow;
