import React, { memo, useEffect, useMemo, useState } from 'react';
import styled from 'styled-components';
import ControlCount from '../../page/SendingCoin/components/CreateSending/component/common/ControlCount';
import {
  Error,
  WrapHeader,
} from '../../page/SendingCoin/components/CreateSending/component/CountCoin';
import { timeTr3 } from '@utils/variable';
import { Animated } from 'react-animated-css';
import Input from '../../page/SendingCoin/components/CreateSending/component/common/Input';
import { CountDecimal } from '../../page/SendingCoin/components/CreateSending/component/common/CountDecimal';
import TabCurrency from '../../page/SendingCoin/components/CreateSending/component/common/TabCurrency';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTriangleExclamation } from '@fortawesome/free-solid-svg-icons';

function CountInputCoin({
  handleChange,
  value,
  error,
  setError,
  titleLeft,
  // childrenLeftInput,
}) {
  console.log(error);
  const [toggleType, setToggleType] = useState(false);
  const [typeNumberCount, setTypeNumberCount] = useState(1);

  useEffect(() => {
    if (!value || value === 0) return;
    if (value >= 1 && value.toString().includes('.')) return;
    setToggleType(value < 1);
  }, [value]);

  const checkNumber = useMemo(() => {
    return value && parseFloat(value);
  }, [value]);

  const HandleCount = (type) => {
    if (!checkNumber) return;
    const valueC = CountDecimal(type, value, toggleType, typeNumberCount);
    validateValue(valueC);

    handleChange(valueC.toString());
  };
  const validateValue = (v) => {
    if (parseFloat(v) <= 0) {
      setError?.('The amount must be greater than 0');
    } else {
      setError?.('');
    }
  };
  return (
    <Wrapper>
      <WrapHeader>
        <Animated animationIn="fadeInLeftCustom" animationInDelay={timeTr3 * 15}>
          <div className="left-fee">{titleLeft}</div>
        </Animated>
        <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 15}>
          <TabCurrency toggleType={toggleType} setToggleType={setToggleType} />
        </Animated>
      </WrapHeader>
      <Animated
        animationIn="fadeInRightCustom"
        animationInDelay={timeTr3 * 16}></Animated>
      <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 8}>
        {error && (
          <Error>
            <FontAwesomeIcon icon={faTriangleExclamation} />
            <span>{error}</span>
          </Error>
        )}
      </Animated>
      <ControlCount
        typeNumberCount={typeNumberCount}
        setTypeNumberCount={setTypeNumberCount}
        HandleCount={HandleCount}
        valueCoin={value}
        setToggleType={setToggleType}
        toggleType={toggleType}
        delayTime={timeTr3 * 17}
        isOpen={checkNumber}
      />
    </Wrapper>
  );
}
export default memo(CountInputCoin);

const Wrapper = styled.div`
  .left-fee {
    color: #04004d;
    line-height: 25px;
    font-weight: 400;
  }
`;
