import React from 'react';
import useHeightNotch from '@hooks/useHeightNotch';
import styled from 'styled-components';

function WrapScroll({ children, height }) {
  const heightN = useHeightNotch(height);

  return <Wrapper heightN={heightN}>{children}</Wrapper>;
}

export default WrapScroll;
export const Wrapper = styled.div`
  flex: 1;
  display: flex;
  width: 100%;
  flex-direction: column;
  max-height: ${({ heightN }) => heightN}px;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    display: none;
  }
`;
