import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faStar } from '@fortawesome/free-solid-svg-icons';
import React from 'react';
import styled, { css } from 'styled-components';

const RankTag = ({ type, fullWidth, onClick }) => {
  return (
    <Root
      typeRank={(type || 'none').toLowerCase()}
      fullWidth={fullWidth}
      onClick={onClick}>
      <FontAwesomeIcon icon={faStar} fontSize={'12'} />
      <Label>{(type || 'none').toUpperCase()}</Label>
    </Root>
  );
};

export default RankTag;

const Root = styled.div`
  ${({ fullWidth }) => {
    return css`
      padding: 6px 0px;
      color: white;
      border-radius: 20px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: ${fullWidth && '100%'};
    `;
  }}

  ${({ typeRank }) => {
    switch (typeRank) {
      case 'base':
        return css`
          background: var(--color-gradient-blue-1);
        `;
      case 'silver':
        return css`
          background: var(--color-gradient-gray-1);
        `;
      case 'gold':
      case 'sun':
        return css`
          background: var(--color-gradient-orange-1);
        `;
      case 'earth':
        return css`
          background: var(--color-gradient-green-1);
        `;
      default:
        return css`
          background: var(--color-text-inactive);
          color: var(--color-des);
        `;
    }
  }}
`;

const Label = styled.div`
  font-weight: 500;
  font-size: 13px;
  line-height: 15px;
  margin-left: 6px;
`;
