import React from 'react';
import { Animated } from 'react-animated-css';
import { timeTr3 } from '@utils/variable';
import styled, { css } from 'styled-components';

const Content = ({
  isList = false,
  contents = [],
  delayTime = 0,
  animated = true,
  styleCustom = {},
  titleGradient = true,
}) => {
  let c = 0;
  return (
    <Root>
      {contents.map((content, index) => {
        return (
          <ContentBlock key={index}>
            <React.Fragment key={index.toString()}>
              <Animated
                animationIn={animated ? 'fadeInLeftCustom' : ''}
                animationInDelay={delayTime + timeTr3 * (c = c + 1)}>
                {content.title && (
                  <ContentBlockHeader isGradient={titleGradient}>
                    <p style={styleCustom}> {content.title} </p>
                  </ContentBlockHeader>
                )}
              </Animated>

              <ContentBlockContext isList={isList}>
                {content.contents.map((text, indexSecond) => (
                  <Animated
                    key={indexSecond.toString()}
                    animationIn={animated ? 'fadeInRightCustom' : ''}
                    animationInDelay={delayTime + timeTr3 * (c = c + 1)}>
                    <Context key={indexSecond.toString()}>{text}</Context>
                  </Animated>
                ))}
              </ContentBlockContext>
            </React.Fragment>
          </ContentBlock>
        );
      })}
    </Root>
  );
};

export default Content;

const Root = styled.div`
  margin-top: var(--space-3);
`;

const ContentBlock = styled.div`
  margin-top: var(--space-3);
`;

export const ContentBlockHeader = styled.div`
  ${(props) => {
    if (props.isGradient) {
      return css`
        background: var(--color-gradient);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
      `;
    }
  }}

  font-weight: 700;
  font-size: 15px;
  line-height: 21px;
`;

export const Context = styled.div`
  ${(props) => {
    switch (props.primaryColor) {
      case 'green':
        return css`
          color: var(--color-success);
        `;
      default:
        return css`
          color: var(--color-des);
        `;
    }
  }}
`;

export const ContentBlockContext = styled.div`
  ${(props) => {
    switch (props.isList) {
      case true:
        return css`
          ${Context} {
            position: relative;
            padding-left: var(--space-4);

            &::before {
              content: '.';
              width: 20px;
              height: 20px;
              display: flex;
              justify-content: center;
              align-items: center;

              position: absolute;
              top: -10px;
              left: 0px;
              font-size: 30px;
            }
          }
        `;
      default:
        break;
    }
  }}

  ${Context} {
    margin-top: 6px;
    &:not(:first-child) {
      margin-top: var(--space-5);
    }
  }
`;
