import { SystemCore } from '../../core/';
import createAction from '../action';

export const SettingActionTypes = Object.freeze({
  GET_SETTING_APP: 'GET_SETTING_APP',
  GET_SETTING_APP_SUCCESS: 'GET_SETTING_APP_SUCCESS',
  GET_SETTING_APP_FAIL: 'GET_SETTING_APP_FAIL',

  SETTING_STATUS_NOTIFY: 'SETTING_STATUS_NOTIFY',
  SETTING_STATUS_NOTIFY_SUCCESS: 'SETTING_STATUS_NOTIFY_SUCCESS',
  SETTING_STATUS_NOTIFY_FAIL: 'SETTING_STATUS_NOTIFY_FAIL',

  SET_ROW_BOTTOM_SHEET: 'SET_ROW_BOTTOM_SHEET',
  STATUS_SHOW_HIDE_NOUTCH_HEIGHT: 'STATUS_SHOW_HIDE_NOUTCH_HEIGHT',
});

export const setStatusNotify = (value) => (dispatch) => {
  dispatch({ type: SettingActionTypes.SETTING_STATUS_NOTIFY });
  SystemCore.send({
    command: 'set-status-notification',
    value: value,
  })
    .then((res) => {
      dispatch({
        type: SettingActionTypes.SETTING_STATUS_NOTIFY_SUCCESS,
        payload: res,
      });
    })
    .catch((err) => {
      dispatch({
        type: SettingActionTypes.SETTING_STATUS_NOTIFY_FAIL,
        payload: err,
      });
    });
};

export function getSettingApp(dispatch) {
  dispatch({ type: SettingActionTypes.GET_SETTING_APP });
  SystemCore.send({
    command: 'get-my-setting',
  })
    .then((res) => {
      dispatch(createAction(SettingActionTypes.GET_SETTING_APP_SUCCESS, res));
    })
    .catch((err) => {
      dispatch(createAction(SettingActionTypes.GET_SETTING_APP_FAIL, err));
    });
}

export const setRowBottomSheet = (number) => async (dispatch) => {
  return dispatch(
    createAction(SettingActionTypes.SET_ROW_BOTTOM_SHEET, number),
  );
};

export const setShowHideNotchHeight = (status) => async (dispatch) => {
  return dispatch(
    createAction(SettingActionTypes.STATUS_SHOW_HIDE_NOUTCH_HEIGHT, status),
  );
};
