import { userCallDeposit } from '@api/Deposit';
import { useAppSelector } from '@redux/configureStore';
import { getPaddingWhenHasNotch } from '@redux/selector';
import React, { useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import styled from 'styled-components';
import Step1 from './components/Step/Step1';
import Step2 from './components/Step/Step2';
import Step3 from './components/Step/Step3';

function Deposit() {
  // hooks
  const navigate = useNavigate();
  const param = useParams();

  // redux
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);

  // states
  const [step, setStep] = useState(1);
  const [dataAllStep, setDataAllStep] = useState({});
  const [depositPending, setDepositPending] = useState();

  // functions
  const handleChangeStepDeposit = (data) => {
    switch (data.fromStep) {
      case 1:
        setDataAllStep({
          ...dataAllStep,
          dataStep1: data,
          userPackId: param.id,
        });
        setStep(2);
        break;
      case 2:
        handleDeposit();
        break;
      case 3:
        navigate(`/manage-tokenomic`);
        break;
      default:
        break;
    }
  };

  const handleBackStep3 = () => {
    setStep(1);
    setDataAllStep({});
  };

  const handleDeposit = () => {
    const queryParam = {
      userPackId: parseInt(param.id),
      depositAmount: parseInt(dataAllStep.dataStep1.amount),
    };

    userCallDeposit(queryParam)
      .then((res) => {
        const { data } = res;
        let tmpDepositMoney = data.prePayment.toString();
        const depositRes = {
          prePayment: tmpDepositMoney.slice(0, -6) + '.' + tmpDepositMoney.slice(-6),
          address: data.smAddress,
          userPackId: data.userPackId,
          toSmc: `xsmart${data.userPackId}`,
        };
        setDataAllStep({
          ...dataAllStep,
          dataStep2: depositRes,
        });
        setStep(3);
      })
      .catch((err) => {
        console.log('Deposit err -->', err);
      });
  };

  const handleViewDepositPending = (data) => {
    setDepositPending(data);
    setDataAllStep({
      ...dataAllStep,
      dataStep1: data.tmpStep1,
      dataStep2: data.tmpStep2,
    });
    setTimeout(() => {
      setStep(3);
    }, 200);
  };

  const renderByStep = React.useMemo(() => {
    switch (step) {
      case 1:
        return (
          <Step1
            onNext={handleChangeStepDeposit}
            onViewDepositPending={handleViewDepositPending}
          />
        );
      case 2:
        return (
          <Step2
            dataAllStep={dataAllStep}
            onNext={handleChangeStepDeposit}
            setStep={setStep}
          />
        );
      case 3:
        return <Step3 dataAllStep={dataAllStep} handleBack={handleBackStep3} />;
      default:
        return null;
    }
  }, [step]);

  return <div style={paddingWhenHasNotch}>{renderByStep}</div>;
}

export default Deposit;

const Wrapper = styled.div``;
