import styled, { css } from 'styled-components';

export const Root = styled.div`
  width: 100%;
  display: flex;
  position: relative;
  color: ${(props) => (props.error ? '#FF0366' : 'var(--color-title)')};
  input {
    ::placeholder {
      color: #edeef0;
    }
  }
`;

export const Input = styled.input`
  width: 100%;
  height: ${({ height }) => height || '46px'};
  border: 1px solid ${(props) => (props.error ? '#FF0366' : '#edeef0')};
  padding: ${({ copyAble, isIcon }) =>
    copyAble && isIcon
      ? '0 44px 2px 44px'
      : isIcon
      ? '0 16px 2px 44px'
      : copyAble
      ? '0 44px 2px 16px'
      : '0 16px 2px 16px'};
  border-radius: ${(props) =>
    props.borderRadius ? `${props.borderRadius}px` : '6px'};
  font-size: var(--font-size-title);
  color: ${(props) => (props.error ? '#FF0366' : 'var(--color-title)')};
  box-shadow: ${({ boxShadow }) => (boxShadow ? '0px 2px 20px #00000012' : 'none')};
  display: block;
  :disabled {
    font-weight: bold;
  }
`;

export const WrapIcon = styled.div`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: #edeef0;

  svg {
    width: 20px;
    height: 16px;

    &[data-icon='paste'] {
      color: var(--color-accent-blue-1);
    }
  }

  .copyted {
    position: absolute;
    bottom: 100%;
    left: 50%;
    padding: 2px 4px;
    background: #fff;
    border-radius: 6px;
    -webkit-transform: translate(-50%, -20%);
    -ms-transform: translate(-50%, -20%);
    -webkit-transform: translate(-50%, -20%);
    -ms-transform: translate(-50%, -20%);
    transform: translate(-50%, -20%);
    font-size: 12px;
    color: rgb(25 91 247);
  }

  ${({ pos }) => {
    switch (pos) {
      case 'end':
        return css`
          right: 16px;
        `;
      default:
        return css`
          left: 16px;
        `;
    }
  }}
`;
