import { SystemCore } from '@core';
import ModalConnectWallet from '@page/ConnectWallet/modalConnectWallet';
import { useAppDispatch, useAppSelector } from '@redux/configureStore';
import { setIsConnect, setWalletAddress } from '@redux/Global/reducer';
import { setHasNotch, setPaddingTopWhenHasNotch } from '@redux/globalRef/reducer';
import { systemCore } from '@utils/core';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { Outlet } from 'react-router-dom';
import { getPaddingWhenHasNotch } from '@redux/selector';
import { toast } from 'react-toastify';

function MainWrapper() {
  const dispatch = useAppDispatch();
  const isWalletActive = useSelector((state: any) => state.global.walletAddress);
  const isConnecting = useSelector((state: any) => state.global.isConnect);
  const [isLoading, setIsLoading] = useState(false);
  const [isBackupFile, setIsBackupFile] = useState(false);
  const [walletActive, setWalletActive] = useState({
    address: '',
    balance: '00',
    isWalletActive: false,
  });
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);

  const onClose = () => {
    dispatch(setIsConnect(false));
  };

  const onClickConnectWallet = async () => {
    if (walletActive && walletActive.address) {
      onConnectWallet(walletActive);
      return;
    }
    setIsLoading(true);
    systemCore
      .send({
        command: 'connect-wallet',
        value: {
          id: 'com.metatoken.ibe',
        },
      })
      .then((res) => {
        onConnectWallet(res.data['wallet-active']);
      })
      .catch((err) => {
        // onConnectWallet({ address: '0xasdasdjkjansdosikajkansbnsk12312' });
        dispatch(setIsConnect(false));
        toast.error('Connect Wallet Error!', {
          position: toast.POSITION.TOP_RIGHT,
        });
        setIsLoading(false);
      });
  };

  const onConnectWallet = (walletActive) => {
    console.log('-----2222222----', walletActive);

    if (walletActive.address && walletActive.address.trim().length > 0) {
      localStorage.setItem('mtdAddress', walletActive.address);
      dispatch(setWalletAddress(walletActive.address));
      dispatch(setIsConnect(false));
    }
  };

  const getWalletActive = () => {
    console.log('-----111111----');

    systemCore
      .send({
        command: 'get-setting-d-app',
        value: {
          id: 'com.metatoken.ibe',
        },
      })
      .then((res) => {
        if (res && res.success) {
          console.log('getWalletActive -->', res);
          let walletActive = res.data['wallet-active'];
          if (walletActive) {
            setWalletActive(walletActive);
            // onConnectWallet(walletActive);
          }
        }
      })
      .catch((err) => {
        toast.error('Connect Wallet Error!', {
          position: toast.POSITION.TOP_RIGHT,
        });
        console.log('err', err);
      });
  };

  // useEffect(() => {
  //   console.log('isWalletActive -->', isWalletActive);

  //   if (!isWalletActive) {
  //     console.log('isWalletActive -->', isWalletActive);
  //     getWalletActive();
  //   }
  // }, []);

  const onBackupFile = () => {
    setIsBackupFile(true);
    setTimeout(() => {
      setIsBackupFile(false);
      dispatch(setIsConnect(false));
    }, 2000);
  };

  return (
    <React.Fragment>
      <div style={{ paddingTop: 44 }}>
        {/* Main content */}
        <Outlet />
        <ModalConnectWallet
          visible={isConnecting}
          onClose={onClose}
          onConnect={onClickConnectWallet}
          isLoading={isLoading}
        />
      </div>
    </React.Fragment>
  );
}

export default MainWrapper;
