import React from 'react';
import styled from 'styled-components';
import { numberWithCommas } from '@utils/currencyFormat';
import { typeBlock } from '@utils/dataBlockFake';
import iconRepaylate from '@assets/icon/infoRepaylate.png';
import iconRepayOverdue from '@assets/icon/infoRepayOverdue.png';

const WrapperPaymentBlock = ({ color, handleOnlick, data }) => {
  const renderIcon = (type) => {
    switch (type) {
      case typeBlock.repaymentlate:
        return iconRepaylate;
      case typeBlock.repaymentoverdue:
        return iconRepayOverdue;
      default:
        return null;
    }
  };

  return (
    <WrapPayment colorBlock={color} onClick={() => handleOnlick(data)}>
      <Right colorText={color.colorText}>
        <p className="title">
          {renderIcon(data.type) && <Icon src={renderIcon(data.type)} />}
          {data.title}
        </p>
        <p className="title">{data.timeout}</p>
      </Right>
      <Left>
        <div className="grid grid-cols-2">
          <div className="text-left">
            {data.payment && (
              <SubTitle>
                Payment: <span>{numberWithCommas(data.payment, ',')}</span> USDT
              </SubTitle>
            )}
          </div>
          <div className="text-right">
            {data.release && (
              <SubTitle>
                Release: <span>{numberWithCommas(data.release, ',')}</span> MTD
              </SubTitle>
            )}
            {data.revoked && (
              <SubTitle>
                Revoked: <span>{numberWithCommas(data.revoked, ',')}</span> MTD
              </SubTitle>
            )}
          </div>
        </div>
      </Left>
    </WrapPayment>
  );
};

export default WrapperPaymentBlock;

const WrapPayment = styled.div`
  background: ${({ colorBlock }) => colorBlock.backgoundColor};
  border: 0.5px solid ${({ colorBlock }) => colorBlock.colorText};
  border-radius: 4px;
  padding: 10px;
  margin-top: 15px;
`;

const Right = styled.div`
  text-align: left;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .title {
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    color: ${({ colorText }) => colorText || '#554CFF'};
    margin-bottom: 7px;
  }
`;

const Icon = styled.img`
  margin-right: 6px;
`;

const SubTitle = styled.p`
  font-weight: 400;
  font-size: 11px;
  line-height: 14px;
  color: #04004d;
  & + & {
    margin-top: 6px;
  }
  span {
    font-weight: bold;
  }
`;

const Left = styled.div``;
