import React, { useCallback, useEffect, useRef, useState } from 'react';
import TabList from '@components/TabList';
import styled from 'styled-components';
import { timeTr3 } from '@utils/variable';
import arrowDown from '@assets/icon/arrowDown.svg';
import Container from '@components/Container';
import Header from '@components/Header';
import { useLocation, useNavigate } from 'react-router-dom';
import { useAppSelector } from '@redux/configureStore';
import { getDefaultTransactionTab, getPaddingWhenHasNotch } from '@redux/selector';
import TabContent from '@components/TabContent';
import { getUserPacks } from '@api/BuyToken';
import { useDispatch, useSelector } from 'react-redux';
import { Animated } from 'react-animated-css';
import TransactionItem from './components/TransactionItem';
import ModalChooseTransaction, {
  listAllSmc,
  tabSmcList,
} from './components/ModalChooseTransaction';
import {
  dataTransactionTabAll,
  dataTransactionTabContract,
  dataTransactionTabSmc,
} from '@utils/datajsonFake';
import {
  setContractDetailHeaderTitle,
  setDefaultTransactionTab,
} from '@redux/Global/reducer';
import { getAllUserTransaction } from '@api/Transaction';
import { toast } from 'react-toastify';
import { data } from 'autoprefixer';
import { prependZero, hexToDecimal, capitalizeAllFirstLetter } from '@utils/format';
import { format } from 'date-fns';
import { numberWithCommas } from '@utils/currencyFormat';

const tabList = [
  {
    id: 0,
    name: 'Order',
  },
  {
    id: 1,
    name: 'Smart Contract',
  },
  {
    id: 2,
    name: 'Contract',
  },
];

function TransactionPage() {
  // hooks
  const navigate = useNavigate();
  const location = useLocation();
  const dispatch = useDispatch();

  // redux
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);
  const defaultTransactionTab = useAppSelector(getDefaultTransactionTab);
  const walletAddress = useSelector((state) => state.global.walletAddress);

  // states
  const [selectTab, setSelectTab] = useState(defaultTransactionTab);
  const [isOpen, setIsOpen] = useState(false);
  const [filterSmcValue, setFilterSmcValue] = useState();
  const [filterId, setFilterId] = useState(0);
  const [listTransaction, setListTransaction] = useState();
  const [listAllTransaction, setListAllTransaction] = useState({
    userPack: [],
    txnTron: [],
    txnMTD: [],
  });
  const [listDataFilter, setListDataFilter] = useState({
    contractList: [],
    smList: [],
  });

  // react hooks
  useEffect(() => {
    getAllUserTransaction(walletAddress)
      .then((res) => {
        const { data } = res;
        const result = {
          userPack: data.userPack || [],
          txnTron: data.txnTron || [],
          txnMTD: data.txnMTD || [],
          contractList: data?.contractList
            ? ['All Contract', ...data.contractList]
            : ['All Contract'],
          smList: data?.smList
            ? ['All Smart Contract', ...data.smList]
            : ['All Smart Contract'],
        };
        const { userPack, txnTron, txnMTD, contractList, smList } = result;

        let lRe = mappingTransactionItem(txnTron, 'txnTron');
        if (selectTab === 1) {
          lRe = mappingTransactionItem(txnMTD, 'txnMTD');
        }
        if (selectTab === 2) {
          lRe = mappingTransactionItem(userPack, 'userPack');
        }

        setListTransaction(lRe);
        setFilterSmcValue(smList[0]);
        setListDataFilter({ contractList, smList });
        setListAllTransaction({ userPack, txnTron, txnMTD });
      })
      .catch((err) => toast.error('Fail !'));
  }, []);

  // functions
  const onClick = useCallback(
    (item) => {
      console.log('id --->', item, selectTab);
      let apiTab = 'order';
      if (selectTab === 1) {
        apiTab = 'smc';
      }
      if (selectTab === 2) {
        apiTab = 'contract';
      }
      dispatch(setContractDetailHeaderTitle('Transaction'));
      navigate(`/transaction-detail`, {
        state: { id: item.id, apiTab },
      });
    },
    [selectTab],
  );

  const handleChangeTab = React.useCallback(
    (tab) => {
      setSelectTab(tab);
      dispatch(setDefaultTransactionTab(tab));
      handleFilter(tab, filterId);
    },
    [listAllTransaction, filterId],
  );

  const handleFilter = React.useCallback(
    (index, filterNumId) => {
      let arr = mappingTransactionItem(listAllTransaction.txnTron, 'txnTron');
      if (index === 1) {
        if (filterNumId === 0) {
          arr = mappingTransactionItem(listAllTransaction.txnMTD, 'txnMTD');
        } else {
          const filterListTxnMTD = listAllTransaction.txnMTD.filter(
            (i) => i.userPackId === filterNumId,
          );

          arr = mappingTransactionItem(filterListTxnMTD, 'txnMTD');
        }
      }
      if (index === 2) {
        if (filterNumId === 0) {
          arr = mappingTransactionItem(listAllTransaction.userPack, 'userPack');
        } else {
          const filterListUserPack = listAllTransaction.userPack.filter(
            (i) => i.ID === filterNumId,
          );
          arr = mappingTransactionItem(filterListUserPack, 'userPack');
        }
      }

      setListTransaction(arr);
    },
    [listAllTransaction, filterId],
  );

  const onChangeSmcValue = useCallback(
    (data) => {
      const { filterName, filterNumId } = data;
      setFilterId(filterNumId);
      handleFilter(selectTab, filterNumId);
      setFilterSmcValue(filterName);
      setIsOpen(false);
    },
    [selectTab, listAllTransaction],
  );

  const mappingTransactionItem = (lItem, lCase) => {
    switch (lCase) {
      case 'txnTron':
        const txnTron = lItem.map((i) => {
          const item = {
            id: i.ID,
            type: mappingTxnTronType(i),
            time: format(new Date(i.CreatedAt), 'dd/MM/yyyy'),
            des: mappingTxnTronDes(i),
            releaseMonth:
              i.type !== 'repayment'
                ? null
                : `${prependZero(i.paymentMonth)}/${
                    i.UserPack.PackVariant.paymentDuration
                  }`,
            status: i.status,
          };

          const transferVolumeStr = i.transferVolume.toString();
          let bl = numberWithCommas(Number(transferVolumeStr.slice(0, -6)), ',');
          if (i.type === 'contract') {
            const tranferVolOfContract =
              i.UserPack.PackVariant.Pack.discountPrice *
              i.UserPack.numberPackVariant;
            item.balance = `+${numberWithCommas(tranferVolOfContract, ',')} USDT`;
          }
          if (i.type === 'refund' || i.type === 'balance') {
            item.balance = `-${bl} USDT`;
          }

          if (i.type == 'repayment' || i.type === 'deposit') {
            item.balance = `+${bl} USDT`;
          }

          return item;
        });

        return txnTron;
      case 'userPack':
        const userPack = lItem.map((i) => {
          const item = {
            id: i.ID,
            type: 'contract',
            time: format(new Date(i.CreatedAt), 'dd/MM/yyyy'),
            des: `${capitalizeAllFirstLetter(
              i.PackVariant.Pack.Round.description,
            )} - ${i.ID}`,
            status: i.status,
          };

          let bl = i.PackVariant.Pack.discountPrice * i.numberPackVariant;
          item.balance = numberWithCommas(bl, ',');

          return item;
        });

        return userPack;

      default:
        const txnMTD = lItem.map((i) => {
          const item = {
            id: i.ID,
            type: 'smartContract',
            time: format(new Date(i.CreatedAt), 'dd/MM/yyyy'),
            des: mappingTxnMTDDes(i),
            releaseMonth:
              i.type === 'contract'
                ? null
                : `${prependZero(i.month)}/${
                    i.userPack.PackVariant.paymentDuration
                  }`,
            status:
              i.type === 'contract'
                ? `${numberWithCommas(hexToDecimal(i.transferVolume), ',')} MTD`
                : '',
            isCreate: i.type === 'contract',
          };

          if (i.type !== 'contract') {
            item.balance =
              i.transferVolume === '0'
                ? '+0 MTD'
                : `+${numberWithCommas(
                    hexToDecimal(i.transferVolume).toFixed(3),
                    ',',
                  )} MTD`;
          }

          return item;
        });
        return txnMTD;
    }
  };

  const mappingTxnTronDes = (item) => {
    switch (item.type) {
      case 'repayment':
        if (item.late) return 'Repayment Late';
        return 'Repayment on Time';
      case 'balance':
        return 'Balance';
      case 'deposit':
        return 'Deposit';
      case 'contract':
        return `${capitalizeAllFirstLetter(
          item.UserPack.PackVariant.Pack.Round.description,
        )} - ${item.UserPack.ID}`;
      case 'refund':
        return `Refund - ${item.UserPack.ID}`;

      default:
        return 'Repayment on Time';
    }
  };

  const mappingTxnMTDDes = (item) => {
    switch (item.type) {
      case 'contract':
        return `Create xsmart${item.userPack.ID}... success`;
      case 'post':
        if (item.monthStatus === 'early') {
          return 'Repayment on Time';
        }
        if (item.monthStatus === 'overdue') {
          return 'Repayment Overdue';
        }
        return 'Repayment Late';

      default:
        return 'Prepayment';
    }
  };

  const mappingTxnTronType = (item) => {
    if (item.type === 'repayment') {
      if (item.late) return 'repaymentLate';
      return 'repaymentOnTime';
    }
    return item.type;
  };

  return (
    <div style={paddingWhenHasNotch}>
      <Container>
        <Header
          children="Transaction"
          onBack={() =>
            location?.state?.idDetailSmartContact
              ? navigate(
                  `/detail-smart-contract/${location?.state?.idDetailSmartContact}`,
                )
              : navigate(-1)
          }
        />

        <Wrapper style={{ paddingTop: 20 }}>
          <SelectBox>
            <ButtonSelect onClick={() => setIsOpen(!isOpen)}>
              <span className="text">{filterSmcValue}</span>
              <IconSelect src={arrowDown}></IconSelect>
            </ButtonSelect>
          </SelectBox>
          <TabContent
            onChangTab={(tab) => handleChangeTab(tab)}
            tabList={tabList}
            selectTab={selectTab}
            delayTime={timeTr3 * 4}
          />
          <ListTransaction>
            <Animated animationIn="fadeInLeftCustom" animationInDelay={timeTr3 * 5}>
              {listTransaction &&
                listTransaction.map((item, index) => (
                  <TransactionItem
                    onClick={() => onClick(item)}
                    time={item.time}
                    type={item.type}
                    releaseMonth={item.releaseMonth}
                    repayment={item.des}
                    status={item.status}
                    balance={item.balance}
                    isCreate={item?.isCreate}
                  />
                ))}
            </Animated>
          </ListTransaction>
        </Wrapper>

        <ModalChooseTransaction
          visible={isOpen}
          onClose={() => setIsOpen(false)}
          onChangeSmcValue={onChangeSmcValue}
          contractList={listDataFilter.contractList}
          smcList={listDataFilter.smList}
        />
      </Container>
    </div>
  );
}

export default TransactionPage;

const Wrapper = styled.div``;

const ListTransaction = styled.div``;

const ButtonSelect = styled.div`
  background: #004dff;
  border-radius: 16px;
  padding: 4px 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  .text {
    font-weight: 500;
    font-size: 15px;
    line-height: 15px;

    display: flex;
    align-items: center;
    text-align: center;
    margin-right: 4px;
    color: #ffffff;
  }
`;

const IconSelect = styled.img``;

const SelectBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
`;
