import React from 'react';
import styled from 'styled-components';

function TabCurrency({toggleType, setToggleType }) {
  return (
    <Wrap>
        <div className={`tab ${toggleType && "next"} `}>{toggleType? "0.00$": "$,00"}</div>
        <span onClick={() => setToggleType(false)}>$,00</span>
        <span onClick={() => setToggleType(true)}>0.00$</span>
    </Wrap>
  );
}

export default TabCurrency;


export const Wrap = styled.div`
    width: 150px;
    height: 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: var(--color-neutral-5);
    border-radius: 10px;
    padding: 1px;
    position: relative;

    .tab {
      position: absolute;
      width: 50%;
      background: #FFFFFF;
      border-radius: 8px;
      color: #04004D;
      height: 29px;
      left: 1px;
      transition: all 0.3s linear;
      color: #04004D;
      display: flex;
      justify-content: center;
      align-items: center;
      &.next {
        left: calc(50% - 1px);
      }
   }
   >span {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #667386;
      height: 100%;
    }

`;
