/* eslint-disable no-use-before-define */
import React, { memo } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons';
import { Root, TitleRoot, BackButton, NextButton } from './styled';
import { Animated } from 'react-animated-css';
import { getPaddingWhenHasNotch } from '@redux/selector';
import { useAppSelector } from '@redux/configureStore';

const Header = ({
  children,
  isShowBack = true,
  onBack,
  isShowNext = false,
  onNext,
  btnEnd,
  rightChildren,
  delayAnimation = 0,
  color,
  backgound = '#fff',
}) => {
  const paddingWhenHasNotch = useAppSelector(getPaddingWhenHasNotch);

  return (
    <Root
      color={color}
      backgound={backgound}
      style={{
        paddingTop: paddingWhenHasNotch.paddingTop,
        paddingBottom: 10,
      }}>
      {!!isShowBack && (
        <Animated animationIn="fadeInLeftCustom" animationInDelay={delayAnimation}>
          <BackButton onClick={onBack}>
            <FontAwesomeIcon style={{ color: color }} icon={faChevronLeft} />
          </BackButton>
        </Animated>
      )}
      <TitleRoot color={color}>
        <Animated
          style={{ whiteSpace: 'nowrap' }}
          animationIn="fadeInRightCustom"
          animationInDelay={delayAnimation}>
          {children}
        </Animated>
      </TitleRoot>
      {btnEnd && btnEnd}
      {!!isShowNext && !rightChildren && (
        <NextButton onClick={onNext}>
          <FontAwesomeIcon icon={faChevronRight} />
        </NextButton>
      )}
      {!!rightChildren && rightChildren}
    </Root>
  );
};

export default memo(Header);
