import React, { useCallback, useEffect, useState } from 'react';
import { Animated } from 'react-animated-css';
import FlexBox, { FlexItem } from '@components/FlexBox';
import FormGroup from '@components/FormGroup';
import InputText from '@components/InputText';
import QrCode from '@components/QrCode';
import { timeTr3 } from '@utils/variable';
import styled, { css } from 'styled-components';
import { createTime } from '@utils/date';
import { useAppDispatch } from '@redux/configureStore';
import { setDeponsit } from '@redux/Deposit/reducer';
import ArrowRedo from '@assets/icon/arrowRedo.svg';
import WrapQr from '../../../../components/ContractDetail/WrapQr';
import { transactionStatus } from '@assets/icon';
const minute = 0;
const secondTime = 5 * 60;
function Content({ transitionDetail, note, contentInputData, onRedo = undefined }) {
  const mappingStatusLabel = React.useCallback((status) => {
    switch (status) {
      case 'pending':
        return 'PENDING';
      case 'success':
        return 'SUCCESS';
      case 'sold-out':
        return 'SOLDOUT';
      case 'time-out':
        return 'TIMEOUT';
      case 'refund':
        return 'REFUND';
      default:
        return '';
    }
  }, []);

  const dispath = useAppDispatch();
  const [status, setStatus] = useState('pending');
  const [timePending, setTimePending] = useState(createTime(secondTime, minute));
  const [timeOut, setTimeOut] = useState(false);
  let timeDown = null;
  const handleCountDown = (time, func) => {
    let t = time;
    timeDown = setInterval(() => {
      if (t === 0) {
        if (typeof func === 'function') {
          func();
        }
        clearInterval(timeDown);
      }
      let str = createTime(t, minute);
      setTimePending(str);
      t--;
    }, 1000);
  };

  useEffect(() => {
    handleCountDown(secondTime, onDeponsitDone);
    return () => {
      clearInterval(timeDown);
    };
  }, []);

  const onDeponsitDone = () => {
    dispath(setDeponsit(true));
    setStatus('time-out');
    setTimeOut(true);
  };

  const handleRedo = () => {
    dispath(setDeponsit(false));
    handleCountDown(secondTime, onDeponsitDone);
    setStatus('pending');
  };

  return (
    <>
      <FlexBox flexDirection={'column'} gap={'var(--space-2)'}>
        <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 1}>
          <FlexBox alignItem={'center'}>
            <FlexItem flex={'0 1 30%'}>
              <TextLeft>Transaction</TextLeft>
            </FlexItem>
            <FlexItem flex={'0 1 70%'}>
              <TextRight transaction>0xb082dbdb7645082dbdb7645</TextRight>
            </FlexItem>
          </FlexBox>
        </Animated>
        <Animated animationIn="fadeInLeftCustom" animationInDelay={timeTr3 * 2}>
          <FlexBox alignItem={'center'}>
            <FlexItem flex={'0 1 30%'}>
              <TextLeft>Status</TextLeft>
            </FlexItem>
            <FlexItem flex={'0 1 70%'}>
              <TextRight className="">
                <Status status={status}>
                  <Image src={transactionStatus[status]} />
                  {mappingStatusLabel(status)}
                </Status>
              </TextRight>
            </FlexItem>
          </FlexBox>
          <FlexboxContant>
            <FlexBox alignItem={'center'}>
              <FlexItem flex={'0 1 30%'}>
                <TextLeft>Time</TextLeft>
              </FlexItem>
              <FlexItem flex={'0 1 70%'}>
                <FlexItemChild>
                  <TextRight className="status">{timePending}</TextRight>
                  {status === 'time-out' && (
                    <ReFundIcon onClick={handleRedo}>
                      <img src={ArrowRedo}></img>
                    </ReFundIcon>
                  )}
                </FlexItemChild>
              </FlexItem>
            </FlexBox>
          </FlexboxContant>
        </Animated>
      </FlexBox>
      {!timeOut && <WrapQr />}
      {!timeOut && (
        <WrapFormGroup>
          <FlexBox gap={'var(--space-3)'} flexDirection={'column'}>
            <FormGroup
              label={'Or deposit the exact amount'}
              controll={InputText}
              controllProps={{
                copyAble: true,
                isIcon: false,
                value: contentInputData?.money,
                disabled: true,
              }}
            />

            <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 5}>
              <FormGroup
                label={'To USDT (TRC20) wallet address'}
                controll={InputText}
                controllProps={{
                  copyAble: true,
                  isIcon: false,
                  value: contentInputData?.address,
                  disabled: true,
                }}
              />
            </Animated>
          </FlexBox>
        </WrapFormGroup>
      )}
      {!timeOut && (
        <Animated animationIn="fadeInLeftCustom" animationInDelay={timeTr3 * 6}>
          <WrapTransactionDetail>{note}</WrapTransactionDetail>
        </Animated>
      )}

      <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 7}>
        <WrapTransactionDetail>{transitionDetail}</WrapTransactionDetail>
      </Animated>
    </>
  );
}

export default Content;

const TextLeft = styled.div`
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;
  color: var(--color-neutral-1);
`;

const TextRight = styled.div`
  ${({ transaction }) => {
    return css`
      font-weight: 500;
      font-size: 15px;
      line-height: 17px;
      color: ${transaction
        ? 'var(--color-accent-blue-1)'
        : 'var(--color-neutral-1)'};
      &.status {
        background: #ffeaf2;
        color: #ff0366;
        border-radius: 4px;
        padding: 10px 12px;
        line-height: 15px;
        display: flex;
        justify-content: center;
        align-items: center;
        &.error {
          background: rgba(251, 188, 213, 0.5);
          color: #ff0366;
        }
      }
    `;
  }}
`;

const WrapFormGroup = styled.div`
  margin-top: var(--space-3);
  input {
    color: var(--color-accent-blue-1);
  }
`;

const WrapTransactionDetail = styled.div`
  margin-top: var(--space-5);
`;

const FlexboxContant = styled.div`
  margin-top: 10px;
`;

const FlexItemChild = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  div {
    margin-right: 10px;
  }
`;
const Status = styled.div`
  width: 110px;
  height: 100%;
  min-height: 30px;
  display: flex;
  font-weight: 500;
  border-radius: 4px;
  ${({ status }) => {
    switch (status) {
      case 'pending':
        return css`
          background-color: var(--color-semantic-orange-3);
          color: var(--color-semantic-orange-1);
        `;
      case 'success':
        return css`
          background-color: var(--color-semantic-green-3);
          color: var(--color-semantic-green-1);
        `;
      case 'sold-out':
      case 'time-out':
        return css`
          background-color: var(--color-semantic-pink-3);
          color: var(--color-semantic-pink-1);
        `;
      case 'refund':
        return css`
          background-color: var(--color-semantic-blue-3);
          color: var(--color-semantic-blue-1);
        `;
      default:
        return '';
    }
  }}
  justify-content: center;
  align-items: center;
`;

const ReFundIcon = styled.div`
  width: 32px;
  height: 32px;
  background: #ff0366;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Image = styled.img`
  margin-right: 8px;
`;
