import React, { useState, useEffect, memo } from 'react';
import { Animated } from 'react-animated-css';
import { timeTr2 } from '@utils/variable';
import styled from 'styled-components';
import { motion, AnimateSharedLayout } from 'framer-motion';

function TabContent({
  tabList,
  onChangTab,
  selectTab,
  delayTime = 0,
  underline = false,
}) {
  const [tabSelect, setTabSelect] = useState();

  // useEffect(() => {
  //   setTabSelect(selectTab);
  // }, [selectTab]);

  return (
    <Root className="wrap-tab-content">
      <AnimateSharedLayout>
        {tabList.map((elm, i) => {
          return (
            <Animated
              key={elm?.id}
              className="item"
              animationIn={'fadeInRightCustom'}
              animationInDelay={delayTime + timeTr2 * i}>
              <Tab
                select={elm?.id === selectTab}
                underline={underline}
                onClick={() => {
                  // setTabSelect(elm?.id);
                  onChangTab?.(elm?.id);
                  window.navigator.vibrate(100);
                }}
                className="tab-name">
                {elm?.name}
                {elm?.id === selectTab ? (
                  <motion.div className="underline" layoutId="jan-2022" />
                ) : null}
              </Tab>
            </Animated>
          );
        })}
      </AnimateSharedLayout>
    </Root>
  );
}

export default memo(TabContent);

const Root = styled.div`
  display: flex;
  width: 100%;
  height: 32px;
  margin-bottom: var(--space-1);
  justify-content: center;

  .item {
    flex: 1;
    width: 100%;
  }
  .tab-name {
    width: 100% !important;
  }
`;

const Tab = styled.button`
  color: ${(props) => (props.select ? 'var(--color-title)' : '#D3D7DB')};
  font-weight: 500;
  font-size: 17px;
  border: none;
  background: none;
  padding: 0 10px;
  cursor: pointer;
  position: relative;
  white-space: nowrap;
  -webkit-tap-highlight-color: transparent;
  /* ::before {
    // visibility: ${(props) => (props.select ? 'visible' : 'hidden')};
    content: '';
    position: absolute;
    width: 30px;
    height: ${(props) => (props.select ? '3px' : '1px')};
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: ${(props) => (props.select ? '-6px' : '-5px')};
    border-radius: 15px;
    background: ${(props) =>
    props.select
      ? 'var(--color-background-btn)'
      : props.underline
      ? '#D3D7DB'
      : 'none'};
  } */
  .underline {
    position: absolute;
    bottom: ${(props) => (props.select ? '-6px' : '-5px')};
    left: 0;
    right: 0;
    width: 80%;
    padding: 0 8px;
    margin: 0 auto;
    border-radius: 15px;
    height: ${(props) => (props.select ? '3px' : '1px')};
    background: ${(props) =>
      props.select
        ? 'var(--color-background-btn)'
        : props.underline
        ? '#D3D7DB'
        : 'none'};
  }
`;
