import { ABI } from './abi';
import { ethers } from 'ethers';

const formatAddressToHex = (address) => {
  if (address === null) return;
  let prefix = '0';
  return prefix.repeat(64 - address.length) + address;
};

const getTypeAbi = (arr: Array<any>) => {
  if (arr.length <= 0) return `bool[]`;
  let str = arr
    .map((item) => {
      if (item.type === 'tuple') {
        return `tuple(${item.components.map((i) => i.type).join(',')})`;
      }
      return item.type;
    })
    .flat()
    .join(',');
  return `${str}`;
};

const formatABI = (strHex, functionName) => {
  console.log('22222 ---->', functionName);

  const AbiCoder = new ethers.utils.AbiCoder();
  let totalRequest = ABI.find((item) => item.name === functionName);
  console.log('33333 ---->', totalRequest);

  if (!totalRequest) return [];
  let allComponent;
  if (totalRequest && totalRequest.outputs) {
    if (totalRequest.outputs[0].components) {
      allComponent = totalRequest.outputs[0].components;
    } else if (totalRequest.outputs && isArray(totalRequest.outputs)) {
      allComponent = totalRequest.outputs;
    } else {
      allComponent = [];
    }
  }
  let typeABI = getTypeAbi(allComponent);
  console.log('44444 ---->', typeABI);

  const data = `0x${strHex}`;
  console.log({ data });

  let decode = AbiCoder.decode(['uint256', 'uint256', 'uint256', 'uint256'], data);

  return decode;
};

export const hextoDec = (s) => {
  var i,
    j,
    digits = [0],
    carry;
  for (i = 0; i < s.length; i += 1) {
    carry = parseInt(s.charAt(i), 16);
    for (j = 0; j < digits.length; j += 1) {
      digits[j] = digits[j] * 16 + carry;
      carry = (digits[j] / 10) | 0;
      digits[j] %= 10;
    }
    while (carry > 0) {
      digits.push(carry % 10);
      carry = (carry / 10) | 0;
    }
  }
  return digits.reverse().join('');
};

export const formatHexString = (stringValue: String) => {
  const resArray: String[] = [];
  for (let index = 0; index < stringValue.length; index += 64) {
    resArray.push(stringValue.slice(index, index + 64));
  }
  return resArray;
};

const isObject = (obj) => {
  return typeof obj === 'object' && obj !== null && !Array.isArray(obj);
};

const isArray = (arr) => {
  return Array.isArray(arr);
};

const switchData = (i, cb?: Function) => {
  if (isObject(i)) {
    return Number(hextoDec(formatAddressToHex(i._hex.slice(2))));
  }
  if (isArray(i)) {
    if (cb && typeof cb === 'function') return cb(i);
    return [];
  }
  return i;
};

const mappingData = (data) => {
  let dataMapping = data.map((item) => {
    if (isArray(item)) {
      return item.map((i) => switchData(i, mappingData));
    }
    return switchData(item);
  });
  return dataMapping;
};

export const formatNumberToHex = (number) => {
  console.log(
    ' ~ file: handleChainValue.ts:88 ~ formatNumberToHex ~ number',
    number,
  );
  const hex = BigInt(number).toString(16);
  try {
    let str = '0'.repeat(64 - hex.length) + hex;
    console.log(' ~ file: handleChainValue.ts:92 ~ formatNumberToHex ~ str', str);
    return '0'.repeat(64 - hex.length) + hex;
  } catch {
    return 'FFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFF';
  }
};

export const GeneraterInputGetPriceList = (
  value = 0,
  toAddress,
  inputValue,
  type,
  from = 0,
  to = 10,
  idOfAddress = 1,
) => {
  console.log(' ~ file: handleChainValue.ts:132 ~ idOfAddress', idOfAddress);
  let input;
  switch (type) {
    case 'number':
      input = {
        address: toAddress,
        input: inputValue + formatNumberToHex(value),
        amount: '0',
      };
      break;
    case 'address':
      input = {
        address: toAddress,
        input: inputValue + formatAddressToHex(value),
        amount: '0',
      };
      break;
    case 'paging':
      input = {
        address: toAddress,
        input: inputValue + formatNumberToHex(from) + formatNumberToHex(to),
        amount: '0',
      };
      break;
    case 'getAddress':
      input = {
        address: toAddress,
        input: inputValue + formatNumberToHex(idOfAddress),
        amount: '0',
      };
      break;

    default:
      input = {
        address: toAddress,
        input: inputValue + formatNumberToHex(value),
        amount: '0',
      };
      break;
  }
  return input;
};

export const formatReturnValue = (strHex, functionName) => {
  console.log('111111 ---->', strHex, functionName);
  let dataFormat = [...formatABI(strHex, functionName)];
  let dataMapping = mappingData(dataFormat);

  console.log({ dataMapping });

  return dataMapping;
};
