import React, { useEffect, useLayoutEffect } from 'react';
import ProtectedRoute from '@layouts/Auth/ProtectedRoute';
import AuthProvider from '@layouts/Auth/AuthProvider';
import MainWrapper from '@layouts/MainWrapper';
import {
  Navigate,
  RouterProvider,
  RouteObject,
  createHashRouter,
} from 'react-router-dom';
import useInjectLocales from '@hooks/useInjectLocales';
import Deposit from './Deposit';
import BuyToken from './BuyToken';
import TokenomicApp from './TokenomicApp';
import Manage from './TokenomicApp/Manage';
import DetailSmartContact from './TokenomicApp/Manage/DetailSmartContact';
import Notification from './Notification';
import ErrorBoundary from './ErrorBoudary';

import { SystemCore } from '@core';
import { useAppDispatch } from '@redux/configureStore';
import { setHasNotch, setPaddingTopWhenHasNotch } from '@redux/globalRef/reducer';
import MyOrderDetail from './TokenomicApp/Manage/DetailSmartContact/MyOderDetail';
import TransactionPage from './Transaction';
import TransactionDetail from './Transaction/components/TransactionDetail';
import InvestNow from './InvestNow';
import PaymentCardTransaction from './Transaction/PaymentCardTransaction';

export const declareRouter: RouteObject[] = [
  {
    element: <MainWrapper />,
    children: [
      {
        path: '/',
        element: <TokenomicApp />,
      },
      {
        path: '/detail-smart-contract/:id',
        element: <DetailSmartContact />,
      },
      {
        path: '/deposit/:id',
        element: <Deposit />,
      },
      {
        path: '/buy-token/:id',
        element: <BuyToken />,
        errorElement: <ErrorBoundary />,
      },
      {
        path: '/notication',
        element: <Notification />,
      },
      {
        path: '/manage-tokenomic',
        element: <Manage />,
      },
      {
        path: '/my-order-detail/:id',
        element: <MyOrderDetail />,
      },
      {
        path: '/transaction-detail',
        element: <TransactionDetail />,
      },
      {
        path: '/payment-card-transaction',
        element: <PaymentCardTransaction />,
      },
      {
        path: '/transaction',
        element: <TransactionPage />,
      },
      {
        path: '/invest-now',
        element: <InvestNow />,
      },
    ],
    errorElement: <ErrorBoundary />,
  },
  //   {
  //     path: '/register',
  //     element: <Register />,
  //   },
];

const router = createHashRouter(declareRouter);
function MainRoute() {
  const { injected } = useInjectLocales();
  const dispatch = useAppDispatch();
  useLayoutEffect(() => {
    SystemCore.send({
      command: 'has-device-notch',
    })
      .then((res) => {
        if (res.success) {
          const status = res.data.isHasNotch;
          if (status) {
            console.log('---------is has notch --------', res.data);

            SystemCore.setStatusNotch(status);
            dispatch(setHasNotch(status));
            dispatch(
              setPaddingTopWhenHasNotch({
                paddingTop: status ? 44 : 0,
              }),
            );
            window.localStorage.setItem('hasNotch', JSON.stringify(status));
          }
        }
      })
      .catch((err) => {
        dispatch(setHasNotch(false));
        dispatch(
          setPaddingTopWhenHasNotch({
            paddingTop: 0,
          }),
        );
        window.localStorage.removeItem('hasNotch');
      });
  }, []);
  return (
    <AuthProvider>{injected && <RouterProvider router={router} />}</AuthProvider>
  );
}

export default MainRoute;
