// !!! Remember to multiple with number of packVariants of field totalPrice !!!
export const calculatePostPayment = (
  month,
  interest,
  totalMonth,
  totalPrice,
  downPaymentPercent,
  price,
) => {
  console.log('month --------- month', {
    month,
    interest,
    totalMonth,
    totalPrice,
    downPaymentPercent,
    price,
  });
  let downPayment = totalPrice * (downPaymentPercent / 100);
  let remainingPayment = totalPrice - downPayment;
  let monthlyPayment = remainingPayment / totalMonth;

  let month100 = 100 / interest;

  let totalPayment = 0;
  let monthDiscount;
  for (let i = 1; i <= month; i++) {
    if (month * interest > 100) {
      if (i >= month100) {
        monthDiscount = 1;
      } else {
        monthDiscount = i * (interest / 100);
      }
    } else {
      monthDiscount = 1 - (totalMonth - i) * (interest / 100);
    }

    let monthlyDiscPayment = Math.ceil(monthlyPayment * monthDiscount);
    totalPayment += monthlyDiscPayment;
    remainingPayment -= monthlyDiscPayment;

    if (i == month) {
      return {
        month: month,
        usdtMonthlyPayment: monthlyDiscPayment,
        mtdRelease: monthlyDiscPayment / price,
        mtdReleaseLate: (monthlyDiscPayment / price) * 0.8,
        mtdReleasePenalty: (monthlyDiscPayment / price) * 0.2,
      };
    }

    // monthly payment of next month
    monthlyPayment = remainingPayment / (totalMonth - i);
  }
};

