import React from 'react';
import styled from 'styled-components';
import { formatMoney } from '../../../utils/format';

const ManageWalletStatus = ({ statuses = [] }) => {
  const returnColor = React.useCallback((v) => {
    switch (v) {
      case 'locked':
        return {
          bg: 'rgb(252, 221, 233)',
          color: 'rgb(255, 3, 102)',
        };
      case 'release':
        return {
          bg: 'rgb(255, 249, 204)',
          color: 'rgb(252, 157, 0)',
        };
      case 'reserve':
        return {
          bg: 'rgb(221, 219, 255)',
          color: 'rgb(85, 76, 255)',
        };
      case 'availability':
        return {
          bg: 'rgb(227, 247, 223)',
          color: 'rgb(4, 184, 0)',
        };
      default:
        return {
          bg: 'rgb(187, 207, 251)',
          color: 'rgb(102, 115, 134)',
        };
    }
  }, []);

  return (
    <Root>
      <WrapStatus>
        {statuses.map((status, index) => (
          <StatusBlock
            key={index.toString()}
            bgColor={returnColor(status.id).bg}
            textColor={returnColor(status.id).color}>
            <WrapStatusTitle>
              <StatusIcon src={status.icon} />
              <StatusTitle>{status.label}</StatusTitle>
            </WrapStatusTitle>
            <StatusValue>{formatMoney(status.value, ',')} MTD</StatusValue>
          </StatusBlock>
        ))}
      </WrapStatus>
    </Root>
  );
};

export default ManageWalletStatus;

const Root = styled.div``;

const WrapStatus = styled.div`
  display: flex;
  flex-wrap: wrap;

  gap: var(--space-3);
`;

const StatusBlock = styled.div`
  /* min-width: 160px; */

  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;

  padding: var(--space-3);
  background-color: ${({ bgColor }) => bgColor};
  color: ${({ textColor }) => textColor};

  font-weight: 500;
  line-height: 15px;
  font-size: 12px;

  box-shadow: 0px 7px 64px rgba(0, 0, 0, 0.07);
  backdrop-filter: blur(15px);
  border-radius: 10px;
`;

const StatusTitle = styled.div`
  font-size: 15px;
`;
const StatusIcon = styled.img`
  & + ${StatusTitle} {
    margin-left: 6px;
  }
`;
const WrapStatusTitle = styled.div`
  display: flex;
  align-items: center;
`;
const StatusValue = styled.div`
  margin-top: 10px;
  white-space: nowrap;
`;
