import React, { useMemo } from 'react';
import ModalBase from '@components/atoms/ModalBase';
import Thumb from '@components/Thumb';
import FlexBox from '@components/FlexBox';

import styled from 'styled-components';
import Button from '@components/atoms/Button';
import IconStatus from '@components/atoms/IconStatus';
import FormGroup from '@components/FormGroup';
import InputText from '@components/InputText';

const DeniedBuytoken = ({ onClose, visible, type }) => {
  console.log('type -->', type);
  const renderTitleByType = useMemo(() => {
    let title = '';
    switch (type) {
      case 'sold_out':
        title =
          'This round was sold out. Please contact us via the email address listed below, or continue to follow the token sale schedule for this round.';
        break;
      case 'pending':
        title =
          'This round is still pending. Please contact us via the email address listed below, or continue to follow the token sale schedule for this round.';
        break;
      case 'notInWhiteList':
        title =
          'Your wallet is not on our whitelist. Please contact us via the email address listed below, or continue to follow the token sale schedule for this round.';
        break;
      default:
        break;
    }
    return title;
  }, []);

  return (
    <ModalBase visible={visible} onClose={onClose}>
      <WrapContent>
        <Thumb onClick={onClose} />
        {/* <WrapIcon>
          <IconStatus type="warning" />
        </WrapIcon> */}
        <Title className="body1 font-500">{renderTitleByType}</Title>
        <FormGroup
          controll={InputText}
          controllProps={{
            copyAble: true,
            isIcon: false,
            value: 'investor@metanode.co',
            disabled: true,
          }}
        />
      </WrapContent>
    </ModalBase>
  );
};

export default DeniedBuytoken;

const WrapContent = styled.div`
  padding-bottom: 16px;
`;
const Title = styled.div`
  margin-top: 16px;
`;
