import React, { useEffect, useState } from 'react';
import styled, { css } from 'styled-components';
import { formatMoney } from '../../../../utils/format';
import DistributionTable from './components/DistributionTable';
import Content from '../Overview/Content';
import SubTab from '../Tab/SubTab';
import { Animated } from 'react-animated-css';
import { timeTr3 } from '@utils/variable';
import { fetchRoundList } from '@api/RoundApi';
import { useNavigate } from 'react-router-dom';

const Distribution = () => {
  const [roundList, setRoundList] = useState([]);
  const [detailByRound, setDetailByRound] = useState(null);
  const [defaultTab, setDefaultTab] = useState(0);
  const [tabActive, setTabActive] = useState(1);
  const navigate = useNavigate();

  useEffect(() => {
    fetchRoundList()
      .then((res) => {
        setDetailByRound(res.data[defaultTab]);
        setRoundList(res.data);
        setDefaultTab(3);
      })
      .catch((err) => {
        console.log('err --> ', err);
      });
  }, []);

  const returnFontWeight = React.useCallback((v) => {
    return v === 'year' ? 500 : v === 'Total' ? 700 : 400;
  }, []);

  const columns = React.useMemo(
    () => [
      {
        id: 'year',
        name: 'Year',
        minWidth: '50px',
        renderCell: ({ year }) => {
          return <CustomCell fontWeight={returnFontWeight(year)}>{year}</CustomCell>;
        },
      },
      {
        id: 'release',
        name: 'Release',
        renderCell: ({ release }) => release,
      },
      {
        id: 'availability',
        name: 'Availability',
        renderCell: ({ availability }) => availability,
      },
      {
        id: 'reserved',
        name: 'Reserved',
        renderCell: ({ reserved }) => reserved,
      },
      {
        id: 'releaseMTD',
        name: 'Release MTD',
        renderCell: ({ releaseMTD }) => formatMoney(releaseMTD, ','),
      },
      {
        id: 'availabilityMTD',
        name: 'Availability MTD',
        renderCell: ({ availabilityMTD }) => formatMoney(availabilityMTD, ','),
      },
      {
        id: 'reservedMTD',
        name: 'Reserved MTD',
        renderCell: ({ reservedMTD }) => formatMoney(reservedMTD, ','),
      },
    ],
    [],
  );

  const handleChangeTab = (tab) => {
    setTabActive(tab);
  };

  const [dataTable, contentTable] = React.useMemo(() => {
    switch (tabActive) {
      case 1:
        return [apiDistributionFounder, contents];
      case 2:
        return [apiDistributionFounderTeam, contentsFouderTeam];
      case 3:
        return [apiDistributionAdivisors, contentsAdvisors];
      default:
        return [];
    }
  }, [tabActive]);

  const spanInvest = () => {
    return (
      <span
        style={{
          color: '#004DFF',
          marginLeft: 2,
          display: 'block',
          textDecoration: 'none',
          fontWeight: 'bold',
        }}
        // href="https://metanode.co/tokenomic/invest-now-pay-later"
        onClick={() => navigate('/invest-now')}>
        Invest now, pay later.
      </span>
    );
  };

  const contentTextPrivateSale = [
    {
      title: 'Private Sale distribution rules:',
      contents: [<p>Follow the investor-supported program {spanInvest()}</p>],
    },
  ];

  const contentTextPublicSale1 = [
    {
      title: 'Public Sale 1distribution rules:',
      contents: [<p>Follow the investor-supported program {spanInvest()}</p>],
    },
  ];

  const contentTextPublicSale2 = [
    {
      title: 'Public Sale 2 distribution rules:',
      contents: [<p>Follow the investor-supported program {spanInvest()}</p>],
    },
  ];

  const renderContentNotable = () => {
    switch (tabActive) {
      case 4:
        return contentTextPrivateSale;
      case 5:
        return contentTextPublicSale1;
      default:
        return contentTextPublicSale2;
    }
  };

  return (
    <Root>
      <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 2}>
        <SubTab tabs={roundList} defaultTab={0} onChangeTab={handleChangeTab} />
      </Animated>
      {tabActive <= 3 ? (
        <div className="pb-3 px-[20px]">
          <Animated animationIn="fadeInRightCustom" animationInDelay={timeTr3 * 3}>
            <WrapContent>
              <DistributionTable data={dataTable} columns={columns} />
            </WrapContent>
          </Animated>
          <Content isList contents={contentTable} />
        </div>
      ) : (
        <div className="px-[20px]">
          <Content
            isList
            contents={renderContentNotable()}
            styleCustom={{ color: '#04004D' }}
            titleGradient={false}
          />
        </div>
      )}
    </Root>
  );
};

