import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import Table from '../../../../../components/Table';

const DistributionTable = ({ columns = [], data = [] }) => {
  const [activeDot, setActiveDot] = useState(1);
  const refScroll = useRef();

  const handleScrollTable = () => {
    if (refScroll && refScroll.current) {
      const offsetWidth = refScroll.current.offsetWidth;
      const scrollWidth = refScroll.current.scrollWidth;
      const scrollLeft = refScroll.current.scrollLeft;
      const maxScrollLeft = scrollWidth - offsetWidth;
      if (scrollLeft > maxScrollLeft / 2 + 20) {
        setActiveDot(2);
      }
      if (scrollLeft < maxScrollLeft / 2 - 20) {
        setActiveDot(1);
      }
    }
  };

  const moveLeft = () => {
    if (refScroll && refScroll.current) {
      refScroll.current.scrollLeft = 0;
      setActiveDot(1);
    }
  };

  const moveRight = () => {
    if (refScroll && refScroll.current) {
      const offsetWidth = refScroll.current.offsetWidth;
      const scrollWidth = refScroll.current.scrollWidth;
      refScroll.current.scrollLeft = scrollWidth - offsetWidth;
      setActiveDot(2);
    }
  };

  return (
    <>
      <Root ref={refScroll} onScroll={handleScrollTable}>
        <Table columns={columns} data={data} />
      </Root>
      <WrapDots activeDot={activeDot}>
        <DotScroll className="dot-1" onClick={moveLeft} />
        <DotScroll className="dot-2" onClick={moveRight} />
      </WrapDots>
    </>
  );
};

export default DistributionTable;

const WrapDots = styled.div`
  width: 100%;
  display: flex;
  margin-top: 16px;
  justify-content: center;

  .dot-1 {
    background: ${({ activeDot }) => (activeDot === 1 ? '#004DFF' : '#667386')};
  }
  .dot-2 {
    background: ${({ activeDot }) => (activeDot === 2 ? '#004DFF' : '#667386')};
  }
`;

const DotScroll = styled.div`
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 0 4px;
`;

const Root = styled.div`
  overflow-x: auto;
  scroll-behavior: smooth;
`;
