import React, { memo } from 'react';
import { Animated } from 'react-animated-css';
import { timeDuration, timeTr3 } from '@utils/variable';
import styled from 'styled-components';
import FlexBox, { FlexItem } from '../../../components/FlexBox';
import Text from '../../../components/Text';
import { capitalizeFirstLetter } from '@utils/format';
import { useSelector } from 'react-redux';

const InfoBlock = ({ style, roundInfo }) => {
  const walletAddress = useSelector((state) => state.global.walletAddress);

  const fontStyle = React.useMemo(() => {
    return {
      title: {
        fontWeight: 500,
        fontSize: '20px',
        lineHeight: '20px',
      },
      label: {
        fontWeight: 400,
        fontSize: '13px',
        lineHeight: '13px',
      },
      text_1: {
        fontWeight: 500,
        fontSize: '15px',
        lineHeight: '21px',
      },
      text_2: {
        fontWeight: 500,
        fontSize: '15px',
        lineHeight: '18px',
      },
    };
  }, []);

  return (
    <Animated
      animationIn="fadeInRight"
      animationInDelay={timeTr3 * 2}
      animationInDuration={timeDuration}
      style={style}>
      <Root>
        <FlexBox flexDirection={'column'} gap={'var(--space-2)'}>
          <FlexBox gap={'6px'} justifyContent={'space-between'} flexWrap={'wrap'}>
            <FlexBox flex={'1 0 100%'}>
              <Text {...fontStyle['title']}>
                Round -{' '}
                {`${roundInfo && capitalizeFirstLetter(roundInfo.description)}`}
              </Text>
            </FlexBox>
            <FlexItem>
              <Text {...fontStyle['label']}>Price</Text>:{' '}
              <Text {...fontStyle['text_1']}>
                1MTD = {`${roundInfo && roundInfo.defaultPrice}`}$
              </Text>
            </FlexItem>
            {/* <ItemActive>Package: {`${roundInfo.defaultPrice}`}$</ItemActive> */}
          </FlexBox>
          <FlexBox gap={'4px'} justifyContent={'space-between'} flexWrap={'wrap'}>
            <FlexBox flex={'1 0 100%'}>
              <Text {...fontStyle['label']}>
                Metanode Wallet - Receive MTD Token
              </Text>
            </FlexBox>
            <FlexItem>
              <TextWallet {...fontStyle['text_2']}>{walletAddress}</TextWallet>
            </FlexItem>
          </FlexBox>
        </FlexBox>
      </Root>
    </Animated>
  );
};
export default memo(InfoBlock);

const Root = styled.div`
  display: flex;
  flex-direction: column;
  padding: var(--space-4);
  background: var(--color-gradient-purple-1);
  color: white;
  border-radius: 16px;
`;

const TextWallet = styled.div`
  font-size: 15px;
  font-weight: 500;
  line-height: 18px;
  word-break: break-all;
`;
