import { numberWithCommas } from '@utils/currencyFormat';
import React, { useMemo } from 'react';
import styled from 'styled-components';
import Content from './Content';
import Note from '../../../../components/ContractDetail/Note';
import TransactionDetail from './TransactionDetail';

const Checkout = ({ note, dataSteps, onRedo = undefined }) => {
  const contentInputData = useMemo(() => {
    let tmpData = {};
    switch (dataSteps.from) {
      case 'buy-token':
        tmpData = {
          money: '143',
          address: '123',
        };
        break;
      case 'deposit':
        let tmpDepositData = dataSteps.dataStep2;
        let tmpDepositMoney = tmpDepositData.prePayment.toString();
        tmpData = {
          money: tmpDepositMoney.slice(0, -6) + '.' + tmpDepositMoney.slice(-6),
          address: tmpDepositData.smAddress,
          userPackId: dataSteps.userPackId,
        };
        break;
      case 'payment':
        let data = dataSteps.data.detail;
        tmpData = {
          money: data?.Amount,
          address: data?.To,
          transaction: data?.Transaction,
        };
        break;
      default:
        break;
    }

    return tmpData;
  }, [dataSteps]);

  return (
    <Root>
      <Content
        onRedo={onRedo}
        contentInputData={contentInputData}
        transitionDetail={<TransactionDetail dataSteps={dataSteps} />}
        note={note ? note : <Note />}
      />
    </Root>
  );
};
export default Checkout;

const Root = styled.div`
  padding: 10px 0 30px;
`;
